import {Injectable} from '@angular/core';
import {FormGroup, Validators} from '@angular/forms';

const BOOLEAN_TYPE = 'boolean';

@Injectable({
    providedIn: 'root'
})
export class FormsService {

    constructor() {
    }

    createArrayOfControls(group: FormGroup): string[] {
        if (group) {
            return Object.keys(group.controls).filter((key: string) => {
                const abstractControls = group.get(key);
                if (!(abstractControls instanceof FormGroup)) {
                    return key;
                }
            });
        }
    }

    generateValidationListByBean(validationBean, control) {
        const validatorsArray: any[] = [];
        for (const controlName in validationBean) {
            if (validationBean.hasOwnProperty(controlName) && control === controlName) {
                for (const key in validationBean[control]) {
                    if (validationBean[control].hasOwnProperty(key)) {
                        if (typeof validationBean[control][key] === BOOLEAN_TYPE) {
                            validatorsArray.push(Validators[key]);
                        } else {
                            validatorsArray.push(Validators[key](validationBean[control][key]));
                        }
                    }
                }
            }
        }
        return validatorsArray.length ? Validators.compose([...validatorsArray]) : null;
    }
}
