import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {SubSink} from 'subsink';

import {selectTournamentsData} from '@tournaments/selectors/selectors';
import {selectSharedDisqualificationTypes} from '@shared/selectors/selectors';
import {AddScoreList, LoadTournaments, StartTournament, UpdateTournament} from '@tournaments/actions/actions';
import {TournamentsService} from '@tournaments/services/tournaments.service';
import {ConnectionService} from '@services/connection.service';
import {LocalStorageService} from '@services/local-storage.service';
import {MarkService} from '@services/mark.service';
import {PageTypes} from '@models/constants/page-types';
import {ITournament} from '@models/interfaces/tournament';
import {ICompetitor, IDialogActions, IDialogPrompt} from '@models/interfaces/human';
import {IStage} from '@models/interfaces/stage';
import {IScore} from '@models/interfaces/score';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {ExerciseMocks} from '@models/mocks/exercise.mock';
import {DialogsService} from '@shared/services/dialogs.service';
import {tap} from 'rxjs/internal/operators/tap';
import {SharedService} from '@shared/services/shared.service';
import emptyScore = ExerciseMocks.emptyScore;
import {LoadDisqualificationTypes} from '@shared/actions/actions';

const DEFAULT_TIME = '00 : 00 : 00';

@Component({
    selector: 'app-exercise',
    templateUrl: './exercise.component.html',
    styleUrls: ['./exercise.component.scss']
})

export class ExerciseComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public tournament: ITournament;
    public competitors: ICompetitor[];
    public passedCompetitors: ICompetitor[] = [];
    public currentCompetitor: ICompetitor = null;
    public stages: IStage[];
    public filledExercises: any[] = [];
    public pageType = PageTypes;
    public currentScore = {...emptyScore};
    public enterNumber: boolean;
    public stageNumber: number;
    public connectionStatus: string;
    public mark: string;
    public disqualificationTypes$: Observable<string[]>;
    public targetScore = 0;
    public passedTargets: number[] = [];
    public decrementButtons: number[] = [-5, -3, -1];
    public incrementButtons: number[] = [1, 3, 5];
    public seconds = 0;
    public milliseconds = 0;
    public submitted = false;

    private tournamentId: string;
    private stageId: string;
    private markSubs: SubSink = new SubSink();
    private promptSubs: SubSink = new SubSink();
    private dialogSubs: SubSink = new SubSink();
    private translatedDisqualification: string[];
    private disqualifications: string[];
    private alertSubs: SubSink = new SubSink();

    constructor(private route: ActivatedRoute,
                private tournamentsService: TournamentsService,
                private connectionService: ConnectionService,
                private storageService: LocalStorageService,
                private store: Store<any>,
                private location: Location,
                private markService: MarkService,
                private dialogsService: DialogsService,
                private sharedService: SharedService) {
        super();
    }

    ngOnInit() {
        this.getRouteParams();
        this.getDisqualificationTypes();
        this.getConnectionStatus();
    }

    getConnectionStatus() {
        if (this.connectionService.status) {
            this.subscriptions.sink = this.connectionService.status
                .subscribe(status => this.connectionStatus = status);
        }
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams.subscribe((params) => {
            this.tournamentId = params['tournament'];
            this.stageId = params['stage'];
            this.getData(this.tournamentId);
        });
    }

    getData(tournamentId: string) {
        this.subscriptions.sink = this.store.pipe(select(selectTournamentsData))
            .subscribe((tournaments: ITournament[]) => {
                if (!tournaments) {
                    return this.store.dispatch(new LoadTournaments());
                }
                this.tournament = this.findTournamentById(tournaments, tournamentId);
                this.competitors = this.filterCompetitorsByValue(this.tournament.competitors);
                this.stages = this.tournament.stages;
                this.stageNumber = this.findStageIndex(this.stages, this.stageId) + 1;
            });
    }

    findCompetitorByNumber() {
        const foundCompetitor = this.findCompetitor(this.competitors, this.currentScore);
        const passedCompetitor = this.findCompetitor(this.passedCompetitors, this.currentScore);
        const isHasResult = foundCompetitor && this.findCompetitorByStage(foundCompetitor);
        if (isHasResult) {
            this.passedCompetitors.push(foundCompetitor);
            this.initAlert('     .   ');
            return;
        }
        if (this.currentScore.mark && foundCompetitor && !passedCompetitor) {
            this.currentCompetitor = foundCompetitor;
            this.passedCompetitors.push(this.currentCompetitor);
            this.filledExercises.push(this.currentScore);
        } else {
            this.initAlert('     ');
        }
    }

    findCompetitorByStage(competitor: ICompetitor): boolean {
        return competitor.result.some((res) => res.stageId === this.stages[this.stageNumber - 1].id);
    }

    nextStage() {
        this.getTimerValue();
        this.submitted = true;
        if (this.saveStatus) {
            return;
        }
        this.submitted = false;
        this.addScoreList();
        this.loadNextCompetitor();
        if (this.stageNumber < this.stages.length) {
            this.dialogsService.initLoader('  ');
            this.stageId = this.stages[this.stageNumber].id;
            this.stageNumber = this.findStageIndex(this.stages, this.stageId) + 1;
            this.store.dispatch(new StartTournament(this.tournamentId));
            setTimeout(() => this.dialogsService.hideLoader(), 2000);
        } else {
            this.loadNextCompetitor();
            this.dialogsService.initLoader(' ');
            this.archiveTournament(this.tournament);
            setTimeout(() => this.dialogsService.hideLoader(), 2000);
            this.location.back();
            return;
        }
        this.passedCompetitors = [];
        this.filledExercises = [];
    }

    archiveTournament(tournament: ITournament) {
        tournament.active = false;
        this.store.dispatch(new UpdateTournament({tournament, tournamentId: tournament.id}));
    }

    addScoreList() {
        if (this.connectionStatus && this.connectionStatus !== this.connectionService.NO_CONNECTION) {
            this.addScoreListToServer(this.tournamentId, this.stageId);
            return;
        }
        this.addScoreListToStorage(this.tournamentId, this.stageId);
    }

    addScoreListToServer(tournamentId: string, stageId: string) {
        this.store.dispatch(new AddScoreList({
            tournamentId,
            stageId,
            scores: this.filledExercises,
            fromStorage: false
        }));
    }

    addScoreListToStorage(tournamentId: string, stageId: string) {
        this.storageService.addScoreList(tournamentId, stageId, this.filledExercises);
    }

    loadNextCompetitor() {
        this.getTimerValue();
        this.submitted = true;
        if (this.saveStatus) {
            return;
        }
        this.submitted = false;
        this.addScoreList();
        this.store.dispatch(new StartTournament(this.tournamentId));
        this.currentCompetitor = null;
        this.currentScore = {...emptyScore};
        this.passedTargets = [];
        this.seconds = 0;
        this.milliseconds = 0;
        this.targetScore = 0;
    }

    loadNextTarget() {
        if (this.currentScore.score === null) {
            this.currentScore.score = 0;
        }
        this.currentScore.score += this.targetScore;
        this.passedTargets.push(this.targetScore);
        this.targetScore = 0;
    }

    getDisqualificationTypes() {
        this.store.dispatch(new LoadDisqualificationTypes());
        this.disqualificationTypes$ = this.store.pipe(
            select(selectSharedDisqualificationTypes),
            tap((types: string[]) => {
                this.disqualifications = types;
                if (!(this.disqualifications.includes('Other'))) {
                    this.disqualifications.push('Other');
                }
                this.translatedDisqualification = this.sharedService.translateArray(this.disqualifications);
            })
        );
    }

    getTimerValue() {
        this.currentScore.timeOfExercise = Number(+this.seconds + this.milliseconds / 1000);
    }

    findTournamentById(tournaments: ITournament[], tournamentId: string): ITournament {
        return this.tournamentsService.findTournamentById(tournaments, tournamentId);
    }

    filterCompetitorsByValue(competitors: ICompetitor[]): ICompetitor[] {
        return competitors.filter((competitor: ICompetitor) => competitor.active || competitor.safetyTechnique);
    }

    findStageIndex(stages: IStage[], stageId: string): number {
        return stages.findIndex((stage: IStage) => stage.id === stageId);
    }

    findCompetitor(competitors: ICompetitor[], score: IScore) {
        return competitors.find((competitor: ICompetitor) =>
            competitor.number === score.mark || competitor.rfidCode === score.mark);
    }

    initAlert(message: string) {
        this.alertSubs.sink = this.dialogsService.getDialogAlert(message)
            .subscribe((result) => this.alertSubs.unsubscribe());
    }

    initPrompt() {
        const options: IDialogPrompt = this.getPromptOptions({
            title: ' ',
            cancelButtonText: '',
            okButtonText: '',
            inputType: 'number'
        });
        this.promptSubs.sink = this.dialogsService.getDialogPrompt(options)
            .subscribe((result) => {
                this.currentScore.mark = result.text;
                this.currentScore.type = 'NUMBER';
                this.findCompetitorByNumber();
                this.promptSubs.unsubscribe();
            });
    }

    getPromptOptions(options): IDialogPrompt {
        return this.dialogsService.getPromptOptions(options);
    }

    initDialog() {
        const options = this.getDialogActionsOptions(this.translatedDisqualification);
        this.dialogSubs.sink = this.dialogsService.getDialogAction(options)
            .subscribe((result) => {
                this.actionDialogResult(result);
                this.dialogSubs.unsubscribe();
            });
    }

    getDialogActionsOptions(actions: string[]): IDialogActions {
        return this.dialogsService.getDialogOptions(' ', actions);
    }

    actionDialogResult(result: string) {
        switch (result) {
            case '':
                return;
            case '':
                return this.initDisqualificationPrompt();
            default:
                const index = this.findDisqualificationIndex(result);
                return this.currentScore.disqualificationReason = this.disqualifications[index];

        }
    }

    initDisqualificationPrompt() {
        const options = this.getPromptOptions({
            title: '  ',
            cancelButtonText: '',
            okButtonText: ''
        });
        this.promptSubs.sink = this.dialogsService.getDialogPrompt(options)
            .subscribe((result) => {
                this.currentScore.disqualificationReason = result.text;
                this.promptSubs.unsubscribe();
            });
    }

    findDisqualificationIndex(disqualification: string) {
        return this.translatedDisqualification.findIndex((item) => item === disqualification);
    }

    enableNextCmpButton() {
        return this.currentCompetitor !== null && this.passedCompetitors.length !== 0 &&
            (this.competitors.length - this.passedCompetitors.length !== 0)
            && this.passedTargets.length === this.stages[this.stageNumber - 1].allTargets
            || this.currentScore.disqualificationReason !== '';
    }

    enableNextTargetBtn() {
        return this.passedTargets.length < this.stages[this.stageNumber - 1].allTargets &&
            this.currentCompetitor !== null && !this.currentScore.disqualificationReason;
    }

    enableNextStageBtn() {
        if (this.connectionStatusCondition) {
            return this.passedCompetitors.length === this.competitors.length
                && this.passedTargets.length === this.stages[this.stageNumber - 1].allTargets ||
                this.passedCompetitors.length === this.competitors.length;
        } else {
            return !this.storageService.hasScoreList(this.tournamentId, this.stageId) && this.passedCompetitors.length !== 0;
        }
    }

    get connectionStatusCondition() {
        return this.connectionStatus && this.connectionStatus !== this.connectionService.NO_CONNECTION;
    }

    enableDisqualificationBtn() {
        return this.currentCompetitor !== null;
    }

    get saveStatus() {
        return this.currentScore.timeOfExercise === 0
            && !this.currentScore.disqualificationReason.length
            && this.currentCompetitor !== null;
    }


    goBack() {
        this.location.back();
    }

    onReadMark() {
        this.markService.readMark();
        this.markSubs.sink = this.markService.mark$
            .subscribe((mark: string) => {
                if (mark) {
                    this.currentScore.mark = mark;
                    this.currentScore.type = 'RFID';
                    this.markSubs.unsubscribe();
                    this.findCompetitorByNumber();
                }
            });
    }

    onSetTargetScore(item: number) {
        if ((this.targetScore + item) < 0) {
            this.targetScore = 0;
            return;
        }
        this.targetScore = this.targetScore + item;
    }
}
