import {IStage} from '@models/interfaces/stage';
import {ActionTypes, StagesAction} from '../actions/actions';

export interface State {
    stages: IStage[];
    classifiers: IStage[];
    weaponTypes: any;
    errors?: any;
}

export const initialState: State = {
    stages: [],
    classifiers: [],
    weaponTypes: [],
};

export function reducer (state: State = initialState, action: StagesAction) {
    switch (action.type) {
        case ActionTypes.LoadedStages:
            return {...state, stages: action.payload};
        case ActionTypes.ErrorStages:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
