import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromStages from '@stages/reducers/reducer';

export const selectStages = createFeatureSelector<fromStages.State>('stages');

export const selectStagesData = createSelector(
    selectStages,
    (state: fromStages.State) => state.stages
);

export const selectStagesErrors = createSelector(
    selectStages,
    (state: fromStages.State) => state.errors
);
