import {createFeatureSelector, createSelector} from '@ngrx/store';

import * as fromQuiz from '@quiz/reducers/reducer';

export const selectQuiz = createFeatureSelector<fromQuiz.State>('quiz');

export const selectSubjects = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.subjects
);

export const selectSubjectTests = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.tests
);

export const selectTestQuestions = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.questions
);

export const selectQuestionbyId = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.currentQuestion
);

export const selectQuizErrors = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.errors
);

export const selectCategories = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.standardsCategories
);

export const selectConditions = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.standardsConditions
);

export const selectUnits = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.units
);

export const selectStandards = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.standards
);

export const selectCurrentStandard = createSelector(
    selectQuiz,
    (state: fromQuiz.State) => state.currentStandard
);

