import {Component, OnInit} from '@angular/core';
import {Router, RouterEvent} from '@angular/router';
import {filter, take} from 'rxjs/operators';
import {TranslateService} from '@ngx-translate/core';
import {Spinkit} from 'ng-http-loader';
import {Store} from '@ngrx/store';

import {
    LoadClassifiers,
    LoadCompetitorMarks,
    LoadDisqualificationTypes,
    LoadLevels,
    LoadRanks,
    LoadStatuses,
    LoadTimeIntervals,
    LoadUserById,
    LoadValidations,
    LoadWeaponTypes
} from '@shared/actions/actions';
import {LoadAllData} from '@tournaments/actions/actions';
import {ConnectionService} from '@services/connection.service';
import {MessageService} from '@services/message.service';
import {TokenService} from '@services/token.service';
import {IMqttMessage, MqttService} from 'ngx-mqtt';
import {UnsubscribeOndestroyAdapter} from './common/adstracts/unsubscribe-ondestroy-adapter';
import {LoadCompetitorsRating} from '@competitors/actions/actions';
import {LoadCurrentWorkspace} from '@workspace/actions/actions';
import {ModalService} from '@modal/services/modal.service';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public showHeader: boolean;
    public currentRoute: boolean;
    public spinkit = Spinkit;
    private connectionStatus: string;
    public role: string;

    constructor(private router: Router,
                private translate: TranslateService,
                private connectionService: ConnectionService,
                private messageService: MessageService,
                private tokenService: TokenService,
                private store: Store<any>,
                private _mqttService: MqttService,
                private modalService: ModalService) {
        super();
        this.role = localStorage.getItem('role');
        this.getRouterEvents();
    }

    ngOnInit(): void {
        this.setLanguage();
        this.translate.setDefaultLang(localStorage.getItem('lang') || 'ru');
        this.getConnectionStatus();
        if (this.role === 'GUEST') {
            this.openDialogModal('testQuestion');
        }
    }

    getConnectionStatus() {
        if (this.connectionService.status) {
            this.connectionService.status.pipe(take(1)).subscribe(status => {
                if (status) {
                    this.connectionStatus = status;
                    if (this.tokenService.getToken()) {
                        this.dispatchAllData();
                    }
                }
            });
        }
    }

    getRouterEvents() {
        this.router.events.pipe(
            filter(e => e instanceof RouterEvent)
        ).subscribe((e: RouterEvent) => {
            this.showHeader = e.url !== '/login';
            this.currentRoute = e.url !== '/login' && e.url !== '/dashboard' &&
                !(e.url.includes('/tournaments/rating?'));
        });
    }

    dispatchAllData() {
        const userId = localStorage.getItem('userId');
        const role = localStorage.getItem('role');
        if (this.hasConnection()) {
            if (role === 'ADMIN') {
                this.store.dispatch(new LoadClassifiers());
                this.store.dispatch(new LoadWeaponTypes());
                this.store.dispatch(new LoadLevels());
                this.store.dispatch(new LoadCompetitorMarks());
                this.store.dispatch(new LoadDisqualificationTypes());
                this.store.dispatch(new LoadAllData());
            }
            this.store.dispatch(new LoadValidations());
            this.store.dispatch(new LoadStatuses());
            this.store.dispatch(new LoadTimeIntervals());
            this.store.dispatch(new LoadUserById(userId));
            this.store.dispatch(new LoadRanks());
        } else {
            this.messageService.showPushNotification('No internet connection!');
        }
    }

    getTournamentTopic() {
        this.subscriptions.sink = this._mqttService.observe('competition').subscribe((message: IMqttMessage) => {
            if (message.payload) {
                const resultString = String.fromCharCode(...message.payload);
                const parsedResult = JSON.parse(resultString);
                this.goToRating(parsedResult.id);
                this.store.dispatch(new LoadCompetitorsRating(parsedResult.id));
            }
        });
    }

    openDialogModal(message: string) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {message},
            (res) => {
                if (res && res === 'Yes') {
                    this.getCurrentWorkspace();
                } else {
                    this.getTournamentTopic();
                }
            });
    }

    getCurrentWorkspace() {
        this.store.dispatch(new LoadCurrentWorkspace());
    }

    goToRating(id: string) {
        this.router.navigate(['tournaments/rating'], {queryParams: {tournament: id}});
    }


    hasConnection() {
        return this.connectionStatus && this.connectionStatus !== this.connectionService.NO_CONNECTION;
    }

    setLanguage() {
        if (!localStorage.getItem('lang')) {
            localStorage.setItem('lang', 'ru');
        }
    }


}
