import {ITournament} from '@models/interfaces/tournament';
import {IStage} from '@models/interfaces/stage';
import {ICompetitor} from '@models/interfaces/human';

const translateWeaponType = (weaponType) => {
    switch (weaponType) {
        case '':
            return 'RIFLE';
        case '':
            return 'SHOTGUN';
        case '':
            return 'HANDGUN';
        case 'RIFLE':
            return '';
        case 'SHOTGUN':
            return '';
        case 'HANDGUN':
            return '';
    }
};

export const tournamentMapperFromServer = (data: any): ITournament => {
    return {
        id: data.id,
        name: data.name,
        eventDate: data.eventDate,
        location: data.location,
        director: data.director,
        level: data.level,
        mainJudge: data.mainJudge,
        stages: data.stages,
        statisticsJudge: data.statisticsJudge,
        weaponType: translateWeaponType(data.weaponType),
        competitors: data.competitors,
        humanAmount: data.competitors ? data.competitors.length : 0,
        stageCount: data.stages ? data.stages.length : 0,
        active: data.active
    };
};

export const tournamentMapperToServer = (data: any) => {
    return {
        id: data.id,
        name: data.name,
        active: data.active,
        eventDate: data.eventDate,
        location: data.location,
        director: data.director ? data.director.id : null,
        level: data.level,
        mainJudge: data.mainJudge ? data.mainJudge.id : null,
        stages: data.stages,
        competitors: data.competitors && data.competitors.map((competitor: ICompetitor) => competitorMapperToStore(competitor, true)),
        statisticsJudge: data.statisticsJudge ? data.statisticsJudge.id : null,
        weaponType: translateWeaponType(data.weaponType)
    };
};

export const stagesMapperFromServer = (data: any, index: number): IStage => {
    return {
        id: data.id,
        name: data.name,
        stageNumber: data.stageNumber || index + 1,
        location: data.location,
        ISC_classifier: data.ISC_classifier || '',
        allTargets: data.allTargets,
        targets: data.targets,
        popper: data.popper,
        noShoots: data.noShoots,
        notCounted: data.notCounted,
        minShoots: data.minShoots,
        maxPoints: data.maxPoints,
        readyCondition: data.readyCondition,
        startPosition: data.startPosition,
        procedure: data.procedure,
        pointCount: data.pointCount
    };
};

export const competitorMapperFromServer = (data: ICompetitor, index: number): ICompetitor => {
    return {
        id: data.id,
        numberInOrder: index + 1,
        userName: data.userName,
        registrationType: data.mark ? 'RFID' : 'NUMBER',
        birthDate: data.person.birthDate,
        safetyTechnique: data.active,
        mark: data.rfidCode || '',
        number: data.number,
        result: data.result,
        person: data.person
    };
};

export const competitorMapperToServer = (data: any) => {
    return {
        mark: data.mark,
        type: data.registrationType,
        active: data.safetyTechnique,
        userName: data.userName
    };
};

export const competitorMapperToServerWithBothFields = (data: any) => {
    return {
        active: data.safetyTechnique,
        number: data.number,
        rfid: data.rfidCode,
        userName: data.userName
    };
};

export const competitorMapperToStore = (data: any, isTournament: boolean = false) => {
    return {
        id: data.id,
        active: data.safetyTechnique || data.active,
        birthDate: data.person.birthDate,
        mark: data.mark,
        userName: data.userName,
        registrationType: data.registrationType,
        person: isTournament ? data.person.id : data.person,
        safetyTechnique: data.safetyTechnique,
        number: data.registrationType === 'NUMBER' ? data.mark : data.number,
        rfidCode: data.registrationType === 'RFID' ? data.mark : data.rfidCode,
        result: data.result
    };
};
