import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import * as fromJudge from '@judges/actions/judges.action';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {IJudge} from '@models/interfaces/human';
import {of} from 'rxjs';
import {JudgesService} from '@judges/service/judges.service';
import {ToastrMessageType, ToastrTitle, ToastrUsersType} from '@models/constants/toastr';
import {MessageService} from '@services/message.service';
import {ToastrService} from 'ngx-toastr';
import {Store} from '@ngrx/store';
import {judgeMapperFromServer} from '@mappers/judge.mapper';
import {selectJudgesData} from '@judges/selectors/judjes.selector';

@Injectable()
export class JudgesEffects {


    constructor(private actions$: Actions,
                private judgeService: JudgesService,
                private messageService: MessageService,
                private toastrService: ToastrService,
                private store: Store<any>) {
    }

    @Effect()
    loadJudges$ = this.actions$
        .pipe(
            ofType(fromJudge.ActionTypes.LoadJudges),
            mergeMap(() => this.judgeService.getJudges()
                .pipe(
                    map((judges: IJudge[]) => new fromJudge.LoadedJudges(judges)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromJudge.ErrorLoadedJudges(err));
                    })
                ))
        );

    @Effect()
    refreshJudges$ = this.actions$
        .pipe(
            ofType(fromJudge.ActionTypes.RefreshJudges),
            map((action: any) => new fromJudge.LoadedJudges(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromJudge.ErrorLoadedJudges(err));
            })
        );

    @Effect()
    createJudge$ = this.actions$
        .pipe(
            ofType(fromJudge.ActionTypes.CreateJudge),
            withLatestFrom(this.store.select(selectJudgesData)),
            switchMap(([action, judges]: any[]) => this.judgeService.createJudge(action.payload)
                .pipe(
                    map((judge: IJudge) => {
                        const message = {user: ToastrUsersType.JUDGE, type: ToastrMessageType.CREATE};
                        if (judge) {
                            const mappedJudge = judgeMapperFromServer(judge);
                            judges.push(mappedJudge);
                            this.messageService.showToastrSuccess(message);
                        }
                        return new fromJudge.RefreshJudges(judges);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromJudge.ErrorLoadedJudges(err));
                    })
                )
            )
        );

    @Effect()
    updateJudge$ = this.actions$
        .pipe(
            ofType(fromJudge.ActionTypes.UpdateJudge),
            withLatestFrom(this.store.select(selectJudgesData)),
            switchMap(([action, judges]: Array<any>) => {
                    const {judge, id} = action.payload;
                    return this.judgeService.updateJudge(judge, id)
                        .pipe(
                            map((updatedJudge: IJudge) => {
                                const message = {user: ToastrUsersType.JUDGE, type: ToastrMessageType.UPDATE};
                                if (updatedJudge) {
                                    const mappedJudge = judgeMapperFromServer(updatedJudge);
                                    judges = judges.map((item: IJudge) => item.id === mappedJudge.id ? mappedJudge : item);
                                    this.messageService.showToastrSuccess(message);
                                }
                                return new fromJudge.RefreshJudges(judges);
                            }),
                            catchError((err) => {
                                this.messageService.showPushNotification(err);
                                this.messageService.showToastrError(err);
                                return of(new fromJudge.ErrorLoadedJudges(err));
                            })
                        );
                }
            ),
        );

    @Effect()
    deleteJudge$ = this.actions$
        .pipe(
            ofType(fromJudge.ActionTypes.DeleteJudge),
            withLatestFrom(this.store.select(selectJudgesData)),
            switchMap(([action, judges]: Array<any>) => {
                return this.judgeService.deleteJudge(action.payload)
                    .pipe(
                        map(() => {
                            const message = {user: ToastrUsersType.JUDGE, type: ToastrMessageType.REMOVE};
                            judges = judges.filter((judge: IJudge) => judge.id !== action.payload);
                            this.messageService.showToastrSuccess(message);
                            return new fromJudge.RefreshJudges(judges);
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromJudge.ErrorLoadedJudges(err));
                        }));
            })
        );


    @Effect()
    changePassword$ = this.actions$
        .pipe(
            ofType(fromJudge.ActionTypes.ChangePassword),
            mergeMap((action: any) => this.judgeService.changeUserPassword(action.payload)
                .pipe(
                    map(() => {
                        const successMessage = 'Congratulation. Password was changed successfully';
                        this.toastrService.success(successMessage, ToastrTitle.SUCCESS);
                        return new fromJudge.LoadJudges();
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromJudge.ErrorLoadedJudges(err));
                    })
                ))
        );

}
