import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromCourse from '@course/reducers/reducer';

export const selectCourses = createFeatureSelector<fromCourse.State>('courses');

export const selectCourseData = createSelector(
    selectCourses,
    (state: fromCourse.State) => state.courses
);

export const selectCourseErrors = createSelector(
    selectCourses,
    (state: fromCourse.State) => state.errors
);
