import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {Store} from '@ngrx/store';

import * as fromWeapons from '@weapon/actions/actions';
import {MessageService} from '@services/message.service';
import {WeaponsService} from '@weapon/services/weapons.service';
import {IWeapon, IWeaponTypes} from '@models/interfaces/weapons';
import {of} from 'rxjs';
import {WeaponsActions} from '@weapon/actions/actions';
import {selectWeaponList, selectWeaponTypesData} from '@weapon/selectors/selectors';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';

@Injectable()
export class WeaponsEffects {

    constructor(
        private actions$: Actions,
        private messageService: MessageService,
        private store: Store<any>,
        private weaponService: WeaponsService
    ) {
    }

    @Effect()
    loadWeaponTypesForList$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.LoadWeaponTypesForList),
            mergeMap(() => this.weaponService.getWeaponTypes()
                .pipe(
                    map((weaponTypes: IWeaponTypes[]) => new fromWeapons.LoadedWeaponTypesForList(weaponTypes)),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshWeaponTypes$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.RefreshWeaponTypes),
            map((action: WeaponsActions) => new fromWeapons.LoadedWeaponTypesForList(action.payload)),
            catchError((err) => {
                this.messageService.showToastrError(err);
                this.messageService.showPushNotification(err);
                return of(new fromWeapons.WeaponErrors(err));
            })
        );

    @Effect()
    createWeaponType$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.CreateWeaponType),
            withLatestFrom(this.store.select(selectWeaponTypesData)),
            switchMap(([action, weaponTypes]: any[]) => this.weaponService.createWeaponType(action.payload)
                .pipe(
                    map((weaponType: IWeaponTypes) => {
                        const message = {user: ToastrUsersType.WEAPON_TYPE, type: ToastrMessageType.CREATE};
                        if (weaponType) {
                            this.messageService.showToastrSuccess(message);
                            weaponTypes.push(weaponType);
                        }

                        return new fromWeapons.RefreshWeaponTypes(weaponTypes);
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    updateWeaponType$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.UpdateWeaponType),
            withLatestFrom(this.store.select(selectWeaponTypesData)),
            switchMap(([action, weaponTypes]: any[]) => this.weaponService.updateWeaponType(action.payload)
                .pipe(
                    map((weaponType: IWeaponTypes) => {
                        const message = {user: ToastrUsersType.WEAPON_TYPE, type: ToastrMessageType.UPDATE};
                        if (weaponType) {
                            this.messageService.showToastrSuccess(message);
                            weaponTypes = weaponTypes.map((item: IWeaponTypes) =>
                                item.id === weaponType.id ? weaponType : item);
                        }

                        return new fromWeapons.RefreshWeaponTypes(weaponTypes);
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteWeaponType$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.DeleteWeaponType),
            withLatestFrom(this.store.select(selectWeaponTypesData)),
            switchMap(([action, weaponTypes]: any[]) => this.weaponService.deleteWeaponType(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.WEAPON_TYPE, type: ToastrMessageType.CREATE};
                        this.messageService.showToastrSuccess(message);
                        weaponTypes = weaponTypes.filter((item: IWeaponTypes) => item.id !== action.payload);

                        return new fromWeapons.RefreshWeaponTypes(weaponTypes);
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadWeaponList$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.LoadWeaponList),
            mergeMap((action: WeaponsActions) => this.weaponService.getWeaponListByDivision(action.payload)
                .pipe(
                    map((weapons: IWeapon[]) => new fromWeapons.LoadedWeaponList(weapons)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshWeaponList$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.RefreshWeaponList),
            map((action: WeaponsActions) => new fromWeapons.LoadedWeaponList(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromWeapons.WeaponErrors(err));
            })
        );

    @Effect()
    createWeaponListItem$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.CreateWeaponListItem),
            withLatestFrom(this.store.select(selectWeaponList)),
            switchMap(([action, weapons]: any[]) => this.weaponService.createWeaponListItem(action.payload)
                .pipe(
                    map((weapon: IWeapon) => {
                        if (weapon) {
                            const isUpdated = weapons.some((item: IWeapon) => item.id === weapon.id);
                            if (isUpdated) {
                                const message = {user: ToastrUsersType.WEAPON, type: ToastrMessageType.UPDATE};
                                weapons = weapons.map((item: IWeapon) => item.id === weapon.id ? weapon : item);
                                this.messageService.showToastrSuccess(message);
                            } else {
                                const message = {user: ToastrUsersType.WEAPON, type: ToastrMessageType.CREATE};
                                weapons.push(weapon);
                                this.messageService.showToastrSuccess(message);
                            }
                        }

                        return new fromWeapons.RefreshWeaponList(weapons);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteWeaponListItem$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.DeleteWeaponListItem),
            withLatestFrom(this.store.select(selectWeaponList)),
            switchMap(([action, weapons]: any[]) => this.weaponService.deleteWeaponListItem(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.WEAPON, type: ToastrMessageType.CREATE};
                        this.messageService.showToastrSuccess(message);
                        weapons = weapons.filter((item: IWeapon) => item.id !== action.payload);

                        return new fromWeapons.RefreshWeaponList(weapons);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    unlinkWeapon$ = this.actions$
        .pipe(
            ofType(fromWeapons.ActionTypes.UnlinkWeapon),
            withLatestFrom(this.store.select(selectWeaponList)),
            switchMap(([action, weapons]: any) => this.weaponService.unLinkWeaponListItem(action.payload)
                .pipe(
                    map(() => new fromWeapons.RefreshWeaponList(weapons)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadPersonsWeaponList$ = this.actions$.pipe(
        ofType(fromWeapons.ActionTypes.LoadPersonsWeaponList),
        mergeMap((action: any) => {
            const {personName, divisionId} = action.payload;
            return this.weaponService.getPersonsWeaponList(personName, divisionId)
                .pipe(
                    map((weapons: IWeapon[]) => new fromWeapons.LoadedWeaponList(weapons)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromWeapons.WeaponErrors(err));
                    })
                );
        })
    );
}
