import {Injectable, ViewContainerRef} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap} from 'rxjs/operators';
import {of} from 'rxjs';

import {MessageService} from '@services/message.service';
import {CombatListService} from '@combat-list/services/combat-list.service';
import * as fromCombatList from '@combat-list/actions/actions';

@Injectable()
export class CombatListEffects {

    constructor(private actions$: Actions,
                private messageService: MessageService,
                private combatListService: CombatListService) {
    }

    @Effect()
    createCheckin$ = this.actions$
        .pipe(
            ofType(fromCombatList.ActionTypes.CreateCheckin),
            mergeMap((action: any) => this.combatListService.createCheckinByDivision(action.payload)
                .pipe(
                    map((response) => {
                        const date = new Date().toISOString();
                        return new fromCombatList.CreateCombatList({
                            combatId: response[0].officer,
                            date: date,
                            divisionId: response[0].divisionId
                        });
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCombatList.CombatListErrors(err));
                    })
                )
            )
        );

    @Effect()
    createCombatList$ = this.actions$
        .pipe(
            ofType(fromCombatList.ActionTypes.CreateCombatList),
            mergeMap((action: any) => this.combatListService.createCombatListAfterCheckin(action.payload)
                .pipe(
                    map((combatList) => new fromCombatList.LoadedCurrentCombatList(combatList)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCombatList.CombatListErrors(err));
                    })
                )
            )
        );

    @Effect()
    clearCombatList$ = this.actions$
        .pipe(
            ofType(fromCombatList.ActionTypes.ClearCombatList),
            map((action: any) => new fromCombatList.LoadedCurrentCombatList(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromCombatList.CombatListErrors(err));
            })
        );
}
