import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {IPerson} from '@models/interfaces/human';
import {environment} from '@environments/environment';
import {personMapperFromServer, personMapperToServer} from '@mappers/person.mapper';
import {IPage} from '@persons/actions/actions';
import {ActivatedRoute, Router} from '@angular/router';

@Injectable({
    providedIn: 'root'
})
export class PersonService {
    basePersonUrl: string = environment.BASE_URL + '/api/persons/' + environment.version;

    constructor(private http: HttpClient, private router: Router, private route: ActivatedRoute) {
    }

    getPersons(): Observable<IPerson[]> {
        return this.http.get<IPerson[]>(this.basePersonUrl + '/all')
            .pipe(map((response) => response.map((item) => personMapperFromServer(item))));
    }

    getPersonsPerPage(pageNumber: number, pageSize: number): Observable<IPerson[]> {
        return this.http.get<IPerson[]>(this.basePersonUrl + `/all/${pageNumber}/${pageSize}`)
            .pipe(map((response) => response.map((item) => personMapperFromServer(item))));
    }

    getPersonsPerPageByDivision(pageNumber: number, pageSize: number, divisionId: string): Observable<IPerson[]> {
        return this.http.get<IPerson[]>(this.basePersonUrl + `/all/${divisionId}/${pageNumber}/${pageSize}`)
            .pipe(map((response) => response.map((item) => personMapperFromServer(item))));
    }

    getPersonsCount(): Observable<Object> {
        return this.http.get(this.basePersonUrl + '/count')
            .pipe(map((response) => response));
    }

    createPerson(data: IPerson): Observable<IPerson> {
        const mappedData = personMapperToServer(data);
        return this.http.post<IPerson>(this.basePersonUrl + '/create', mappedData)
            .pipe(map((response) => personMapperFromServer(response)));
    }

    updatePerson(data: IPerson, id: string): Observable<IPerson> {
        const mappedData = personMapperToServer(data);
        return this.http.put<IPerson>(this.basePersonUrl + `/${id}`, mappedData)
            .pipe((map((response) => personMapperFromServer(response))));
    }

    deletePerson(id: string): Observable<IPerson> {
        return this.http.delete<IPerson>(this.basePersonUrl + `/${id}`)
            .pipe(map((response) => response));
    }

    personNavigate(data: IPage) {
        this.router.navigate([], {
            queryParams: {
                page: data.pageNumber,
                size: data.pageSize,
            },
            queryParamsHandling: 'merge',
            relativeTo: this.route
        });
    }
}
