import {ICourse} from '@models/interfaces/courses';
import {ActionTypes, CoursesActions} from '../actions/actions';

export interface State {
    courses: ICourse[];
    errors?: any;
}

export const initialState: State = {
    courses: undefined,
};

export function reducer(state: State = initialState, action: CoursesActions) {
    switch (action.type) {
        case ActionTypes.LoadedCourses:
            return {...state, courses: action.payload};
        case ActionTypes.CoursesErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
