import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromPerson from '@persons/reducers/reducer';


export const selectPerson = createFeatureSelector<fromPerson.State>('persons');

export const selectPersonData = createSelector(
    selectPerson,
    (state: fromPerson.State) => state.persons
);

export const selectPersonDataPerPage = createSelector(
    selectPerson,
    (state: fromPerson.State) => state.personsPerPage
);

export const selectPersonErrors = createSelector(
    selectPerson,
    (state: fromPerson.State) => state.errors
);

export const selectPersonsCount = createSelector(
    selectPerson,
    (state: fromPerson.State) => state.personsCount
);

export const selectPersonsByDivision = createSelector(
    selectPerson,
    (state: fromPerson.State) => state.personsByDivision
);

