import {Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';

import {Icons} from '@models/constants/icons';
import {selectCompetitorsMarks} from '@shared/selectors/selectors';
import {PageTypes} from '@models/constants/page-types';
import {Animations} from '@models/constants/animations';

@Component({
    selector: 'app-base-modal',
    templateUrl: './base-modal.component.html',
    styleUrls: ['./base-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations: [Animations.enterLeaveOpacity]
})
export class BaseModalComponent implements OnInit {

    public icons = Icons;
    public object: any;
    public edit: boolean;
    public objectType: string;
    public page = PageTypes;
    public maxWidth: string;
    public rows: string[] = [];
    public selectedDate: any = new Date();

    @Input() mobileTitle: string;
    @Input() isOpened: boolean;
    @Input() pageType: string;
    @Input() disqualificationTypes: string[];
    @Input() divisionLength: number;
    @Input() isDatePicker: boolean;

    @Input() set mobileObject(data: any) {
        this.object = data;
        this.getRows();
    }

    @Output() closeMobile: EventEmitter<boolean> = new EventEmitter();
    @Output() submitMobile: EventEmitter<any> = new EventEmitter();
    @Output() disqualificationReason: EventEmitter<string> = new EventEmitter();

    constructor(public activeModal: NgbActiveModal) {
    }

    get title() {
        return this.edit ? `edit${this.objectType}` : `add${this.objectType}`;
    }

    ngOnInit() {
    }

    getRows() {
        if (this.object.statList) {
            for (let i = 0; i < this.object.statList.length; i++) {
                this.rows.push('30');
            }
        }
    }

    // Don't remove this method.
    // It needs for mobile.
    onDateChanged(value) {
        this.selectedDate = value;
    }

    // Don't remove this method.
    // It needs for mobile version.
    onClose(value?: string) {
        this.closeMobile.emit(this.isOpened);
    }

    // Don't remove this method.
    // It needs for mobile version.
    getDisqualificationReason(value) {
        this.disqualificationReason.emit(value);
    }

    // Don't remove this method.
    // It needs for mobile version.
    onSubmit(object: any) {
        this.submitMobile.emit(object);
    }

}
