import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {Location} from '@angular/common';
import {tap} from 'rxjs/internal/operators/tap';

import {ModalService} from '@modal/services/modal.service';
import {PageTypes} from '@models/constants/page-types';
import {ITest} from '@models/interfaces/quiz';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {QuizMocks} from '@models/mocks/quiz.mocks';
import {CreateTestForSubject, DeleteTestFromSubject, LoadTestsBySubject, UpdateTest} from '@quiz/actions/actions';
import {selectSubjectTests} from '@quiz/selectors/selectors';
import {Actions} from '@models/constants/actions';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {Icons} from '@models/constants/icons';
import {Animations} from '@models/constants/animations';
import {GetQueries} from '@shared/actions/actions';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-tests',
    templateUrl: './tests.component.html',
    styleUrls: ['./tests.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class TestsComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public tests$: Observable<ITest[]>;
    public actions: string[];
    public icons = Icons;

    public objectType = PageTypes;
    private subjectId: string;
    public subjectName: string;

    constructor(private store: Store<any>,
                private modalService: ModalService,
                private route: ActivatedRoute,
                private router: Router,
                private location: Location) {
        super();
    }

    ngOnInit() {
        this.getRouteParams();
        this.actions = [Actions.CHANGE, Actions.QUESTIONS, Actions.REMOVE];
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams
            .subscribe((params) => {
                this.store.dispatch(new GetQueries(params));
                this.subjectId = params['subject'];
                this.subjectName = params['name'];
                this.getTests(this.subjectId);
            });
    }

    getTests(subjectId: string) {
        this.tests$ = this.store.select(selectSubjectTests)
            .pipe(tap((tests: ITest[]) => !tests && this.fetchTests(subjectId)));
    }

    fetchTests(subjectId: string) {
        this.store.dispatch(new LoadTestsBySubject(subjectId));
    }


    add() {
        this.openTestModal(QuizMocks.emptyQuiz, false);
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openTestModal(event.item, true);
            case Actions.QUESTIONS:
                return this.navigateToQuestions(event.item);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            default:
                return;
        }
    }

    openTestModal(test: ITest, edit: boolean) {
        const objectType = this.objectType.TESTS;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...test}, edit, objectType},
            (res) => {
                if (res) {
                    res.questionList = test.questionList;
                    edit ? this.updateTest(test.id, res) : this.createTest(res, this.subjectId);
                }
            });
    }

    openDialogModal(message: string, item: ITest) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.ru, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeTest(item.id, this.subjectId);
                }
            });
    }

    createTest(test: ITest, subjectId: string) {
        test.subject = subjectId;
        this.store.dispatch(new CreateTestForSubject(test));
    }

    updateTest(testId: string, test: ITest) {
        test.subject = this.subjectId;
        this.store.dispatch(new UpdateTest({testId, test}));
    }

    removeTest(testId: string, subjectId: string) {
        this.store.dispatch(new DeleteTestFromSubject({testId, subjectId}));
    }

    navigateToQuestions(test: ITest) {
        this.router.navigate(['subjects/tests/questions'], {
            queryParams: {
                subject: this.subjectId,
                name: this.subjectName,
                test: test.id,
                testName: test.ru
            }
        });
    }

    back() {
        this.location.back();
    }
}
