import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '@environments/environment';
import {Observable} from 'rxjs';
import {IWeapon, IWeaponTypes} from '@models/interfaces/weapons';
import {map} from 'rxjs/operators';
import {weaponMapperToServer} from '@mappers/weapons.mapper';

@Injectable({
    providedIn: 'root'
})
export class WeaponsService {
    private weaponTypeBaseUrl: string = environment.BASE_URL + '/api/weapon/type/' + environment.version;
    private weaponBaseUrl: string = environment.BASE_URL + '/api/weapon/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getWeaponTypes(): Observable<IWeaponTypes[]> {
        return this.http.get(this.weaponTypeBaseUrl + '/get/all')
            .pipe(map((response: IWeaponTypes[]) => response));
    }

    createWeaponType(weaponType: IWeaponTypes): Observable<IWeaponTypes> {
        return this.http.post(this.weaponTypeBaseUrl + '/create/weapontype', weaponType)
            .pipe(map((response: IWeaponTypes) => response));
    }

    updateWeaponType(weaponType: IWeaponTypes): Observable<IWeaponTypes> {
        return this.http.put(this.weaponTypeBaseUrl + `/update/weapontype/${weaponType.id}`, weaponType)
            .pipe(map((response: IWeaponTypes) => response));
    }

    deleteWeaponType(weaponTypeId: string): Observable<any> {
        return this.http.delete(this.weaponTypeBaseUrl + `/delete/weapontype/${weaponTypeId}`)
            .pipe(map((response) => response));
    }

    getWeaponListByDivision(divisionId: string): Observable<IWeapon[]> {
        return this.http.get(this.weaponBaseUrl + `/all/by/division/${divisionId}`)
            .pipe(map((response: IWeapon[]) => response));
    }

    createWeaponListItem(weapon: IWeapon): Observable<IWeapon> {
        const mappedBody = weaponMapperToServer(weapon);
        return this.http.post(this.weaponBaseUrl + '/create/weapon', mappedBody)
            .pipe(map((response: IWeapon) => response));
    }

    deleteWeaponListItem(weaponId: string): Observable<any> {
        return this.http.delete(this.weaponBaseUrl + `/delete/weapon/${weaponId}`)
            .pipe(map((response) => response));
    }

    unLinkWeaponListItem(weaponId: string): Observable<IWeapon> {
        return this.http.post(this.weaponBaseUrl + `/${weaponId}/remove`, null)
            .pipe(map((response: IWeapon) => response));
    }

    linkWeaponListItem(weaponId: string, personId: string): Observable<IWeapon> {
        return this.http.post(this.weaponBaseUrl + `/${weaponId}/${personId}`, null)
            .pipe(map((response: IWeapon) => response));
    }

    changeFireCount(weaponId: string, fireCount: string): Observable<IWeapon> {
        return this.http.post(this.weaponBaseUrl + `/${weaponId}/add/firedcount/${fireCount}`, null)
            .pipe(map((response: IWeapon) => response));
    }

    getPersonsWeaponList(personName: string, divisionId: string): Observable<IWeapon[]> {
        return this.http.get<IWeapon[]>(`${this.weaponBaseUrl}/all/by/person/${personName}/division/${divisionId}`)
            .pipe(map((response: IWeapon[]) => response));
    }
}
