import {Component, OnDestroy, OnInit, ViewContainerRef} from '@angular/core';
import {Observable, Subscription, timer} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {tap} from 'rxjs/operators';
import {Location} from '@angular/common';
import {SubSink} from 'subsink/dist/subsink';

import {selectCurrentStandard} from '@quiz/selectors/selectors';
import {ICurrentStandard} from '@quiz/actions/actions';
import {IStandard} from '@models/interfaces/standard';
import {IPerson} from '@models/interfaces/human';
import {TimerService} from '@exercises/timer.service';
import {ModalService} from '@modal/services/modal.service';
import {ModalRootComponent} from '@shared/components/modal-root/modal-root.component';
import {DialogsService} from '@shared/services/dialogs.service';

@Component({
    selector: 'app-standard-exam',
    templateUrl: './standard-exam.component.html',
    styleUrls: ['./standard-exam.component.scss']
})
export class StandardExamComponent implements OnInit, OnDestroy {
    public currentStandard$: Observable<ICurrentStandard>;
    public standard: IStandard;
    public percentOfMembers: number;
    public standardMembers: IPerson[] = [];
    public isStarted = false;
    public ticks: number;

    private timerSubs: Subscription[] = [];
    private dialogSubs: SubSink = new SubSink();

    constructor(private store: Store<any>,
                private timerService: TimerService,
                private modalService: ModalService,
                private vcRef: ViewContainerRef,
                private location: Location,
                private dialogService: DialogsService) {
    }

    ngOnInit() {
        this.getCurrentStandard();
        this.generateSubscriptions();
    }

    getCurrentStandard() {
        this.currentStandard$ = this.store.pipe(
            select(selectCurrentStandard),
            tap((currentStandard: ICurrentStandard) => {
                this.standard = currentStandard.standard;
                this.percentOfMembers = currentStandard.percentOfMembers;
                this.standardMembers = currentStandard.standardMembers;
            }));
    }

    generateSubscriptions() {
        this.standardMembers.forEach(() => this.timerSubs.push(timer(0, 1000).subscribe()));
    }

    onStart() {
        this.standardMembers.forEach((member: IPerson, i: number) => this.onStartItem(i));
        this.isStarted = !this.isStarted;
    }

    onStartItem(item) {
        const index = item.hasOwnProperty('index') ? item.index : item;
        this.standardMembers[index].started ? this.stopTimer(index) : this.startTimer(index);
        this.standardMembers[index].started = !this.standardMembers[index].started;
    }

    startTimer(index) {
        const $timer = timer(0, 1000);
        this.timerSubs[index] = $timer.subscribe(ticks => {
            this.ticks = ticks;
            this.standardMembers[index].timer = this.generateTimeString(ticks);
        });
    }

    stopTimer(index) {
        this.timerSubs[index].unsubscribe();
    }

    public generateTimeString(time: number) {
        return this.timerService.generateTimeString(time);
    }

    initErrorsDialog() {
        const options = this.getErrorDialogOptions();
        this.dialogSubs.sink = this.modalService.initModal(ModalRootComponent, options)
            .subscribe((result) => console.log(result));
    }

    getErrorDialogOptions() {
        this.standard.failsList = this.setCheckedProp(this.standard, 'failsList');
        return this.modalService.getModalDialogOptions({
            vcRef: this.vcRef,
            context: {
                standard: this.standard,
                path: 'standard-errors-modal'
            },
            fullscreen: false,
        });
    }

    initConditionsDialog() {
        const options = this.getConditionsDialogOptions();
        this.dialogSubs.sink = this.modalService.initModal(ModalRootComponent, options)
            .subscribe((result) => console.log(result));
    }

    getConditionsDialogOptions() {
        this.standard.conditionsList = this.setCheckedProp(this.standard, 'conditionsList');
        return this.modalService.getModalDialogOptions({
            vcRef: this.vcRef,
            context: {
                standard: this.standard,
                path: 'standard-conditions-modal'
            },
            fullscreen: false,
        });
    }

    initScoreDialog() {
        const options = this.dialogService.getDialogOptions('', ['5', '4', '3', '2', '1']);
        this.dialogSubs.sink = this.dialogService.getDialogAction(options)
            .subscribe((result) => console.log(result));
    }

    setCheckedProp(standard: IStandard, arrayName: string): any[] {
        return standard[arrayName].map((item: any) => {
            item['checked'] = false;
            return item;
        });
    }

    onBack() {
        this.location.back();
    }

    ngOnDestroy() {
        this.timerSubs.forEach(sub => sub.unsubscribe());
    }

}
