import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {ITournament} from '@models/interfaces/tournament';
import {environment} from '@environments/environment';
import {tournamentMapperFromServer, tournamentMapperToServer} from '@mappers/tournament.mapper';
import {IExercise} from '@models/interfaces/exercise';

@Injectable({
    providedIn: 'root'
})
export class TournamentsService {

    baseUrl: string = environment.BASE_URL + '/api/competitions/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getTournaments(): Observable<ITournament[]> {
        return this.http.get<ITournament[]>(this.baseUrl + '/all').pipe(
            map((response) => response.map((item) => tournamentMapperFromServer(item)))
        );
    }

    createTournament(data: ITournament): Observable<ITournament> {
        const mappedData = tournamentMapperToServer(data);
        return this.http.post<ITournament>(this.baseUrl + '/create', mappedData)
            .pipe(map((response) => tournamentMapperFromServer(response)));
    }

    updateTournament(id: string, data: ITournament): Observable<ITournament> {
        const mappedData = tournamentMapperToServer(data);
        return this.http.put<ITournament>(`${this.baseUrl}/${id}`, mappedData)
            .pipe(map((response) => tournamentMapperFromServer(response)));
    }

    removeTournament(id: string): Observable<ITournament> {
        return this.http.delete<ITournament>(`${this.baseUrl}/${id}`);
    }

    getTournamentById(id: string): Observable<ITournament> {
        return this.http.get(`${this.baseUrl}/${id}`)
            .pipe(map((response) => tournamentMapperFromServer(response)));
    }

    addScoreList(tournamentId: string, stageId: string, scores: IExercise[]) {
        return this.http.post(this.baseUrl + `/competition/${tournamentId}/stage/${stageId}/score/list`, scores)
            .pipe(map((response: any) => response));
    }

    startTournament(tournamentId: string): Observable<any> {
        return this.http.post(this.baseUrl + `/competition/${tournamentId}/start`, null)
            .pipe(map((response) => response));
    }

    findTournamentById(tournaments: ITournament[], tournamentId: string): ITournament {
        return tournaments.find((tournament: ITournament) => tournament.id === tournamentId);
    }

    filterTournaments(tournaments: ITournament[]): ITournament[] {
        return tournaments.filter((tournament: ITournament) => tournament.active);
    }
}
