import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromUser from '@user/reducers/reducer';

export const selectUsers = createFeatureSelector<fromUser.State>('users');

export const selectUsersData = createSelector(
    selectUsers,
    (state: fromUser.State) => state.users
);

export const selectUsersPerPage = createSelector(
    selectUsers,
    (state: fromUser.State) => state.usersPerPage
);

export const selectUsersCount = createSelector(
    selectUsers,
    (state: fromUser.State) => state.usersCount
);

export const selectUsersErrors = createSelector(
    selectUsers,
    (state: fromUser.State) => state.errors
);
