import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromSpecialities from '@specialities/reducers/reducer';

export const selectSpecialities = createFeatureSelector<fromSpecialities.State>('specialities');

export const selectSpecialitiesData = createSelector(
    selectSpecialities,
    (state: fromSpecialities.State) => state.specialities
);

export const selectSpecialitiesErrors = createSelector(
    selectSpecialities,
    (state: fromSpecialities.State) => state.errors
);
