import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {IJudge, IUser, IUserRegistration} from '@models/interfaces/human';
import {environment} from '@environments/environment';
import {judgeMapperFromServer, judgeMapperToServer, judgeRegistrationMapperToServer} from '@mappers/judge.mapper';

@Injectable({
  providedIn: 'root'
})
export class JudgesService {
  judgeBaseUrl: string = environment.BASE_URL + '/api/users/' + environment.version;
  constructor(private http: HttpClient) { }

  getJudges(): Observable<IJudge[]> {
    return this.http.get<IJudge[]>(this.judgeBaseUrl + '/all/judge')
        .pipe(map((response) => response.map((item) => judgeMapperFromServer(item))));
  }

  createJudge(data: IUserRegistration): Observable<IJudge> {
    const mappedData = judgeRegistrationMapperToServer(data);
    return this.http.post<IJudge>(this.judgeBaseUrl + '/create', mappedData)
        .pipe(map((response) => judgeMapperFromServer(response)));
  }

  deleteJudge(id: string): Observable<IJudge | IUser> {
    return this.http.delete<IJudge | IUser>(this.judgeBaseUrl + `/${id}`)
        .pipe((map((response) => response)));
  }

  updateJudge(judge: IJudge, id: string): Observable<IJudge> {
    const mappedData = judgeMapperToServer(judge);
    return this.http.put<IJudge>(this.judgeBaseUrl + `/${id}`, mappedData)
        .pipe((map((response) => judgeMapperFromServer(response))));
  }

  changeUserPassword(data: {data: IJudge | IUser, newPassword: string}): Observable<IJudge | IUser> {
    const body = {id: data.data.id, newPassword: data.newPassword};
    return this.http.put(this.judgeBaseUrl + `/changepassword/${data.data.id}`, body)
        .pipe(map((response) => judgeMapperFromServer(response)));
  }
}
