import {IPerson} from '@models/interfaces/human';

const HANDGUN = 'HANDGUN';
const SHOTGUN = 'SHOTGUN';
const RIFLE = 'RIFLE';

const getCode = (codes: { code: string, name: string }[], type) => {
    return codes.find(item => item.name === type);
};

const getCodes = (handgunCodeIpsc: any, shotgunCodeIpsc: any, rifleCodeIpsc: any): { code: string, name: string }[] => {
    const codes: { code: string, name: string }[] = [];
    if (typeof handgunCodeIpsc === 'string') {
        codes.push({code: handgunCodeIpsc, name: HANDGUN});
    } else {
        codes.push(handgunCodeIpsc);
    }
    if (typeof shotgunCodeIpsc === 'string') {
        codes.push({code: shotgunCodeIpsc, name: SHOTGUN});
    } else {
        codes.push(shotgunCodeIpsc);
    }
    if (typeof rifleCodeIpsc === 'string') {
        codes.push({code: rifleCodeIpsc, name: RIFLE});
    } else {
        codes.push(rifleCodeIpsc);
    }
    return codes;
};

export const personMapperToServer = (data: any) => {
    return {
        id: data.id,
        active: data.active,
        roleName: data.roleName,
        userName: data.userName,
        birthDate: data.birthDate,
        rank: data.rank.id,
        division: data.division.id,
        level: data.level,
        codes: getCodes(data.handgunCodeIpsc, data.shotgunCodeIpsc, data.rifleCodeIpsc),
        address: data.address,
    };
};

export const personMapperFromServer = (data: any) => {
    return {
        id: data.id,
        userName: data.userName,
        birthDate: data.birthDate,
        rank: data.rank,
        division: data.division,
        level: data.level,
        handgunCodeIpsc: getCode(data.codes, HANDGUN) || '',
        shotgunCodeIpsc: getCode(data.codes, SHOTGUN) || '',
        rifleCodeIpsc: getCode(data.codes, RIFLE) || '',
        active: data.active,
        member: false,
        roleName: data.roleName,
        address: data.address,
    };
};

export const checkinToServer = (data: IPerson) => {
    return {
        person: data.id,
        status: data.status
    };
};

