import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';

import {IStage} from '@models/interfaces/stage';
import {environment} from '@environments/environment';
import {map} from 'rxjs/operators';
import {stagesMapperFromServer} from '@mappers/tournament.mapper';

@Injectable({
    providedIn: 'root'
})
export class StagesService {
    stagesBaseUrl: string = environment.BASE_URL + '/api/competitions/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getStages(id: string): Observable<IStage[]> {
        return this.http.get<IStage[]>(this.stagesBaseUrl + `/${id}/stage/all`)
            .pipe(map((res: IStage[]) =>
                res.map((item: IStage, index: number) => stagesMapperFromServer(item, index))));
    }

    getClassifier(): Observable<IStage[]> {
        return this.http.get<IStage[]>(this.stagesBaseUrl + '/enum');
    }

    getWeaponTypes(): Observable<any> {
        return this.http.get(this.stagesBaseUrl + '/enum/weapon');
    }

    getLevels(): Observable<any> {
        return this.http.get(this.stagesBaseUrl + '/enum/level');
    }

    createStage(stage: IStage, tournamentId: string): Observable<IStage> {
        return this.http.post(this.stagesBaseUrl + `/${tournamentId}/stage`, stage)
            .pipe(map((response: IStage) => response));
    }

    deleteStage(tournamentId: string, stageId: string): Observable<IStage> {
        return this.http.delete(this.stagesBaseUrl + `/${tournamentId}/stage/${stageId}`)
            .pipe(map((response: any) => response));
    }

    updateStage(stage: IStage, tournamentId: string): Observable<IStage> {
        return this.http.put(this.stagesBaseUrl + `/${tournamentId}/stage/${stage.id}`, stage)
            .pipe(map((response: IStage) => response));
    }
}
