import {EventEmitter, Input, Output} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';

import {FormsService} from '@forms/services/forms.service';
import {FormFieldName} from '@models/constants/fields';
import {PageTypes} from '@models/constants/page-types';
import {IJudge, IPerson, IUser} from '@models/interfaces/human';
import {ITournament} from '@models/interfaces/tournament';
import {IStage} from '@models/interfaces/stage';
import {IDivision} from '@models/interfaces/division';
import {IRank} from '@models/interfaces/ranks';
import {IConditionUnits, IStandard} from '@models/interfaces/standard';
import {IWeaponTypes} from '@models/interfaces/weapons';
import {FieldsForMobile, FieldsMocks} from '@models/mocks/fields.mocks';
import {UnsubscribeOndestroyAdapter} from '../../adstracts/unsubscribe-ondestroy-adapter';

const OBJECT_TYPE = 'object';

export abstract class AbstractForm extends UnsubscribeOndestroyAdapter {

    public form: FormGroup;
    public fields = FormFieldName;
    public controls: string[] = [];
    public submitted = false;
    public divisions: IDivision[] = [];
    public judges: IJudge[];
    public classifier: IStage[];
    public ranks: IRank[] = [];
    public units: IConditionUnits[] = [];
    public personsByDivision: IPerson[] = [];
    public weaponTypes = [];
    public weaponTypesWeaponList: IWeaponTypes[] = [];
    public selectedItem: string;
    public gridRows: string[] = [];
    public gridValue = '80';
    public levelNames: string[];
    public validations: any;
    public currentLanguage: string;
    public translatedWeaponTypes: string[];
    public competitorMark: any;

    @Input() object: any;
    @Input() objectType: string;
    @Input() isMobile: boolean;
    @Input() mobileSelectArray: string[];

    @Input() set mark(value: any) {
        if (this.form && value) {
            this.form.controls['mark'].setValue(value);
        } else {
            this.competitorMark = value;
        }
    }

    @Output() save = new EventEmitter<IUser | IPerson | ITournament | IStandard>();
    @Output() cancel = new EventEmitter();

    protected constructor(protected formsService: FormsService,
                          protected translateService: TranslateService) {
        super();
    }


    createForm() {
        this.form = new FormGroup({});
        if (this.object) {
            this.addControls(this.form, this.object);
            if (this.object.address) {
                const addressForm = new FormGroup({});
                this.form.addControl('address', addressForm);
                this.addControls(addressForm, this.object.address);
            }
            this.controls = this.createArrayOfControls(this.form);
        }
        if (this.competitorMark) {
            this.form.controls['mark'].setValue(this.competitorMark);
        }
    }

    addControls(form: FormGroup, formObject: any) {
        for (const key in formObject) {
            if (formObject.hasOwnProperty(key)) {
                if (typeof formObject[key] !== OBJECT_TYPE) {
                    if (!(this.getNotDisplayedFields(key))) {
                        form.addControl(key, new FormControl(formObject[key], this.getValidation(key)));
                    }
                }

                if (typeof formObject[key] === OBJECT_TYPE) {
                    this.addControlsForObjectCase(form, formObject, key);
                }
            }
        }
    }

    addControlsForObjectCase(form: FormGroup, formObject: any, key: string) {
        switch (key) {
            case this.fields.DIVISION:
                form.addControl(key, new FormControl(formObject[key]['name'], this.getValidation(key)));
                break;
            case this.fields.MAIN_JUDGE:
            case this.fields.STATISTICS_JUDGE:
            case this.fields.DIRECTOR:
                form.addControl(key, new FormControl(formObject[key]['userName'], this.getValidation(key)));
                break;
            case this.fields.RANK:
                const lang = localStorage.getItem('lang') === 'ru' ? 'rus' : 'kz';
                form.addControl(key, new FormControl(formObject[key][lang], this.getValidation(key)));
                break;
            case this.fields.UNITS:
                form.addControl(key, new FormControl(formObject[key]['units'], this.getValidation(key)));
                break;
            case this.fields.HANDGUN_IPSC_CODE:
            case this.fields.SHOTGUN_IPSC_CODE:
            case this.fields.RIFLE_IPSC_CODE:
                form.addControl(key, new FormControl(formObject[key]['code'], this.getValidation(key)));
        }
    }

    getValidation(control: string | any): Validators {
        switch (this.objectType) {
            case PageTypes.TOURNAMENT:
                return this.formsService.generateValidationListByBean(this.validations.CompetitionBean, control);
            case PageTypes.STAGE:
                return this.formsService.generateValidationListByBean(this.validations.Stage, control);
            case PageTypes.PERSON:
                return this.formsService.generateValidationListByBean(this.validations.PersonBean, control);
            case PageTypes.JUDGE:
                return this.formsService.generateValidationListByBean(this.validations.UserSignupBean, control);
            case PageTypes.DIVISION:
                return this.formsService.generateValidationListByBean(this.validations.DivisionBean, control);
            case PageTypes.SPECIALITIES:
                return this.formsService.generateValidationListByBean(this.validations.Speciality, control);
            case PageTypes.WEAPON:
                return this.formsService.generateValidationListByBean(this.validations.Weapon, control);
            case PageTypes.USER:
                return this.formsService.generateValidationListByBean(this.validations.UserUpdateBean, control);
            case PageTypes.COMPETITOR:
                control = control === 'safetyTechnique' ? 'active' : control;
                return this.formsService.generateValidationListByBean(this.validations.CompetitorMark, control);
            case PageTypes.UNIT:
                return this.formsService.generateValidationListByBean(this.validations.UnitBean, control);
            case PageTypes.TESTS:
                return this.formsService.generateValidationListByBean(this.validations.QuizBean, control);
            case PageTypes.CATEGORY:
                return this.formsService.generateValidationListByBean(this.validations.Categories, control);
            case PageTypes.WEAPON_TYPES:
                return this.formsService.generateValidationListByBean(this.validations.WeaponType, control);
            case PageTypes.COURSE:
                return this.formsService.generateValidationListByBean(this.validations.CourseBean, control);
            case PageTypes.CONDITION:
                return [Validators.required];
            default:
                return null;
        }
    }

    createArrayOfControls(group: FormGroup): string[] {
        return this.formsService.createArrayOfControls(group);
    }

    detectFormValueChanges() {
        return this.form.valueChanges.subscribe(value => {
            this.object = value;
            for (const key in this.object) {
                if (this.object.hasOwnProperty(key) && this.object[key]) {
                    this.formDetectionCase(key);
                }
            }
        });
    }

    formDetectionCase(key) {
        switch (key) {
            case this.fields.MAIN_JUDGE:
            case this.fields.STATISTICS_JUDGE:
            case this.fields.DIRECTOR:
                this.object[key] = this.findObject(this.judges, this.object, key);
                return;
            case this.fields.DIVISION:
                this.object[key] = this.findObject(this.divisions, this.object, key);
                return;
            case this.fields.RANK:
                this.object[key] = this.findObject(this.ranks, this.object, key);
                return;
            case this.fields.ISC_CLASSIFIER:
                this.object[key] = this.findObject(this.classifier, this.object, key);
                return;
        }
    }

    findObject(array: any[], object: any, key: any) {
        if (typeof object[key] === 'string') {
            return this.findObjectByName(array, object, key);
        }

        return this.findObjectById(array, object, key);
    }

    findObjectById(array: any[], object: any, key: any) {
        return array.find((item: any) =>
            item.id === object[key] || item.id === object[key].id);

    }

    findObjectByName(array: any[], object: any, key: any) {
        if (key === this.fields.RANK) {
            const lang = localStorage.getItem('lang') === 'ru' ? 'rus' : 'kz';
            return array.find((item: IRank) =>
                item[lang] === object[key] || item[lang] === object[key][lang]);
        }
        return array.find((item: any) => {
            if (item.hasOwnProperty('userName')) {
                return item.userName === object[key] || item.userName === object[key].userName;
            }
            return item.name === object[key] || item.name === object[key].name;
        });
    }

    onSwitcherValueChange(control: string, value?: any) {
        if (value && this.formControls[control].value === value.value) {
            return;
        }
        const controlValue = this.formControls[control].value;
        this.formControls[control].setValue(!controlValue);
    }

    onItemSelected(control: any, array: string[], value: any) {
        if (!(FieldsMocks.notSelectableFields.includes(control))) {
            const selectedItem = array.find((item: any) => item === value);
            this.formControls[control].setValue(selectedItem);
            switch (control) {
                case this.fields.ISC_CLASSIFIER:
                    return this.form.patchValue(this.setDefaultValuesByClassifier(value));
                default:
                    return;
            }
        }
    }

    onDateSelected(control: string, value: string) {
        this.formControls[control].setValue(value);
    }

    selectItems(control: string): any {
        switch (control) {
            case this.fields.RANK:
                return this.ranks;
            case this.fields.WEAPON_TYPE:
            case this.fields.SHOOTING_TYPE:
                return this.translatedWeaponTypes;
            case this.fields.LEVEL:
                return this.levelNames;
            case this.fields.MAIN_JUDGE:
            case this.fields.STATISTICS_JUDGE:
            case this.fields.DIRECTOR:
                return this.judges;
            case this.fields.ISC_CLASSIFIER:
                return this.classifier;
            case this.fields.DIVISION:
                return this.divisions;
            case this.fields.UNITS:
                return this.units;
            case this.fields.OWNER:
                return this.personsByDivision;
            case this.fields.WEAPON_NAME:
                return this.weaponTypesWeaponList;
            default:
                return;
        }
    }

    getBindLabel(control) {
        switch (control) {
            case this.fields.RANK:
                this.translateService.onLangChange
                    .subscribe((lang) => this.currentLanguage = lang);
                return this.currentLanguage === 'ru' ? 'rus' : 'kz';
            case this.fields.MAIN_JUDGE:
            case this.fields.STATISTICS_JUDGE:
            case this.fields.DIRECTOR:
            case this.fields.OWNER:
                return 'userName';
            case this.fields.DIVISION:
            case this.fields.WEAPON_TYPE:
            case this.fields.UNITS:
            case this.fields.ISC_CLASSIFIER:
            case this.fields.SHOOTING_TYPE:
            case this.fields.WEAPON_NAME:
                return 'name';
            default:
                return null;
        }
    }

    setDefaultValuesByClassifier(value: IStage): IStage {
        return {
            targets: value.targets,
            popper: value.popper,
            noShoots: value.noShoots,
            notCounted: value.notCounted,
            minShoots: value.minShoots,
            maxPoints: value.maxPoints,
        };
    }

    textareaCase(control): boolean {
        return FieldsMocks.textareaFields.includes(control);
    }

    ngSelectCase(control): boolean {
        return FieldsMocks.selectFields.includes(control);
    }

    datePickerCase(control): boolean {
        return FieldsMocks.datePickerFields.includes(control);
    }

    numberTypeFieldCase(control): boolean {
        return FieldsMocks.numberFields.includes(control);
    }

    getDivisionNames(divisions: IDivision[]): string[] {
        return divisions.map((division: IDivision) => division.name);
    }

    getLevelNames(levels: any[]): string[] {
        return levels.map((level: any) => level.classificationBreaks);
    }

    getNotDisplayedFields(key): boolean {
        if (this.isMobile && this.objectType === 'Competitor') {
            return FieldsForMobile.notDisplayedCompetitorsFields.includes(key);
        }

        if (this.objectType === 'Course') {
            const fields = [this.fields.DIVISION];
            return fields.concat(FieldsMocks.notDisplayedFields).includes(key);
        }
        return FieldsMocks.notDisplayedFields.includes(key);
    }

    getGridRowsByControls() {
        this.controls.forEach(() => this.gridRows.push(this.gridValue));
    }


    get formControls() {
        return this.form.controls;
    }

    onCancel() {
        this.cancel.next();
    }

    onSave() {
        this.submitted = true;
        if (this.form.invalid) {
            return;
        }
        this.save.next(this.object);
    }
}
