import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';

import {CombatListComponent} from './combat-list/combat-list.component';
import {CombatListService} from '@combat-list/services/combat-list.service';
import {CombatListModalViewComponent} from '@modal/components/combat-list-modal-view/combat-list-modal-view.component';
import {ModalRootComponent} from '@shared/components/modal-root/modal-root.component';

const routes: Routes = [
    {
        path: '',
        component: CombatListComponent,
        children: [
            {
                path: 'combat-list-modal-view',
                component: CombatListModalViewComponent
            }
        ]
    }
];

@NgModule({
    declarations: [CombatListComponent],
    imports: [
        CommonModule,
        RouterModule.forChild(routes)
    ],
    providers: [CombatListService],
    entryComponents: [ModalRootComponent]
})
export class CombatListModule {
}
