import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router';

import {TokenService} from '@services/token.service';

@Injectable()
export class AuthGuard implements CanActivate {

    constructor(private router: Router,
                private tokenService: TokenService) {
    }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        const token = this.tokenService.getToken();
        if (token) {
            const role = this.parseJwt(token).role;
            const userId = this.parseJwt(token).id;
            localStorage.setItem('role', role);
            localStorage.setItem('userId', userId);
            const permission = route.data['permission'];

            let canActivate: boolean;

            if (!permission) {
                throw new Error('Permissions is not setup!');
            } else if (!permission.only.length) {
                throw new Error('Roles are not setup!');
            }

            canActivate = permission.only.includes(role);

            if (!canActivate) {
                this.router.navigate([permission.redirectTo]);
            }
            return true;
        } else {
            this.router.navigate(['/login']);
        }
        return false;
    }

    parseJwt(token) {
        try {
            return JSON.parse(atob(token.split('.')[0]));
        } catch (e) {
            return null;
        }
    }
}
