import {IPerson} from '@models/interfaces/human';
import {ActionTypes, PersonActions} from '@persons/actions/actions';

export interface State {
    persons: IPerson[];
    personsPerPage: IPerson[];
    personsCount: number;
    personsByDivision: IPerson[];
    errors?: any;
}

export const initialState: State = {
    persons: undefined,
    personsPerPage: undefined,
    personsByDivision: [],
    personsCount: null
};

export function reducer (state: State = initialState, action: PersonActions) {
    switch (action.type) {
        case ActionTypes.LoadedPersons:
            return {...state, persons: action.payload};
        case ActionTypes.LoadedPersonsPerPage:
            return {...state, personsPerPage: action.payload};
        case ActionTypes.LoadedPersonsByDivision:
            return {...state, personsByDivision: action.payload};
        case ActionTypes.ErrorLoadedPersons:
            return {...state, errors: action.payload};
        case ActionTypes.LoadedPersonsCount:
            return {...state, personsCount: action.payload};
        default:
            return state;
    }
}


