import {IJudge, IUserRegistration} from '@models/interfaces/human';

export const judgeMapperFromServer = (data): any => {
    return {
        id: data.id,
        login: data.login,
        userName: data.userName,
        birthDate: data.birthDate,
        active: data.active
    };
};

export const judgeMapperToServer = (judge: IJudge) => {
    return {
        id: judge.id,
        userName: judge.userName,
        birthDate: judge.birthDate,
        login: judge.login,
        active: judge.active
    };
};

export const judgeRegistrationMapperToServer = (data: IUserRegistration) => {
    return {
        birthDate: data.birthDate,
        login: data.login,
        password: data.password,
        userName: data.userName
    };
};
