import { Injectable } from '@angular/core';
import {IDialogActions, IDialogConfirm, IDialogPrompt} from '@models/interfaces/human';
import {from, Observable} from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class DialogsService {
    constructor() {}

    getDialogAction(options: IDialogActions): Observable<any> {
        return;
    }

    getDialogAlert(message: string): Observable<any> {
        return from([]);
    }

    getDialogOptions(title: string, actions: string[]): IDialogActions {
        return {
            title: ` ${title}`,
            cancelButtonText: '',
            actions: actions,
        };
    }

    getDialogPrompt(options: IDialogPrompt): Observable<any> {
        return;
    }

    getPromptOptions(options: IDialogPrompt): IDialogPrompt {
        return;
    }

    getDialogConfirm(options: IDialogConfirm): Observable<any> {
        return;
    }

    getDialogConfirmOptions(options: IDialogConfirm): IDialogConfirm {
        return;
    }

    initLoader(message: string) {
        return message;
    }

    hideLoader() {
        return;
    }
}
