import * as fromShared from '@shared/reducers/reducer';
import {createFeatureSelector, createSelector} from '@ngrx/store';

export const selectShared = createFeatureSelector<fromShared.State>('shared');

export const selectLevels = createSelector(
    selectShared,
    (state: fromShared.State) => state.levels
);

export const selectClassifiers = createSelector(
    selectShared,
    (state: fromShared.State) => state.classifiers
);

export const selectWeaponTypes = createSelector(
    selectShared,
    (state: fromShared.State) => state.weaponTypes
);

export const selectValidations = createSelector(
    selectShared,
    (state: fromShared.State) => state.validations
);

export const selectCompetitorsMarks = createSelector(
    selectShared,
    (state: fromShared.State) => state.competitorMarks
);

export const selectSharedErrors = createSelector(
    selectShared,
    (state: fromShared.State) => state.errors
);

export const selectSharedDisqualificationTypes = createSelector(
    selectShared,
    (state: fromShared.State) => state.disqualificationTypes
);

export const selectQueries = createSelector(
    selectShared,
    (state: fromShared.State) => state.queries
);

export const selectStatuses = createSelector(
    selectShared,
    (state: fromShared.State) => state.statuses
);

export const selectIntervals = createSelector(
    selectShared,
    (state: fromShared.State) => state.intervals
);

export const selectUser = createSelector(
    selectShared,
    (state: fromShared.State) => state.user
);

export const selectRanks = createSelector(
    selectShared,
    (state: fromShared.State) => state.ranks
);
