import {Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs';
import {ActivatedRoute} from '@angular/router';
import {select, Store} from '@ngrx/store';
import {tap} from 'rxjs/internal/operators/tap';

import {IUser, IUserRegistration} from '@models/interfaces/human';
import {CreateUser, DeleteUser, LoadUsers, UpdateUser} from '@user/actions/actions';
import {selectUsersData, selectUsersErrors} from '@user/selectors/selectors';
import {PageTypes} from '@models/constants/page-types';
import {Actions} from '@models/constants/actions';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {ChangePasswordModalComponent} from '@modal/components/change-password-modal/change-password-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {Mocks} from '@models/mocks/persons.mock';
import {ModalService} from '@modal/services/modal.service';
import {ChangePassword} from '@judges/actions/judges.action';
import {MessageService} from '@services/message.service';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-user',
    templateUrl: './user.component.html',
    styleUrls: ['./user.component.scss']
})
export class UserComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public page: number;
    public size: number;
    public pageTypes = PageTypes;
    public actions: string[];

    public users$: Observable<IUser[]>;

    constructor(private route: ActivatedRoute,
                private store: Store<any>,
                private modalService: ModalService) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.CHANGE_PASSWORD, Actions.REMOVE];
        this.getUsers();
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openUserModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            default:
                return this.openChangePasswordModal(event.item);
        }
    }

    getUsers() {
        this.users$ = this.store.select(selectUsersData)
            .pipe(tap((users: IUser[]) => !users && this.fetchUsers()));
    }

    fetchUsers() {
        this.store.dispatch(new LoadUsers());
    }

    openUserModal(user: IUser, edit: boolean) {
        const objectType = this.pageTypes.USER;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...user}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateUser(res, user.id) : this.createUser(res);
                }
            });
    }

    openChangePasswordModal(user: IUser) {
        this.modalService.openModal(ChangePasswordModalComponent,
            {centered: true},
            null,
            (res) => {
                if (res) {
                    const data = {data: user, newPassword: res};
                    this.changeUserPassword(data);
                }
            });
    }

    openDialogModal(message: string, item: IUser) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.userName, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteUser(item.id);
                }
            });
    }

    add() {
        this.openUserModal(Mocks.emptyJudge, false);
    }

    createUser(user: IUserRegistration) {
        this.store.dispatch(new CreateUser(user));
    }

    updateUser(user: IUser, userId: string) {
        user.id = userId;
        this.store.dispatch(new UpdateUser(user));
    }

    deleteUser(userId: string) {
        this.store.dispatch(new DeleteUser(userId));
    }

    changeUserPassword(item: { data: IUser, newPassword: string }) {
        this.store.dispatch(new ChangePassword(item));
    }
}
