import {IJudge} from '@models/interfaces/human';
import {ActionTypes, JudgesAction} from '@judges/actions/judges.action';

export interface State {
    judges: IJudge[];
    errors?: any;
}

export const initialState: State = {
    judges: undefined,
};

export function reducer(state: State = initialState, action: JudgesAction) {
    switch (action.type) {
        case ActionTypes.LoadedJudges:
            return {...state, judges: action.payload};
        case ActionTypes.ErrorLoadedJudges:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
