import {Action} from '@ngrx/store';
import {IUser, IUserRegistration} from '@models/interfaces/human';
import {IPage} from '@persons/actions/actions';

export enum ActionTypes {
    LoadUsers = '[Users Page] Load Users',
    LoadedUsers = '[Users Page] Loaded Users',
    RefreshUsers = '[Users Page] Refresh Users',
    CreateUser = '[User Page] Create User',
    UpdateUser = '[User Page] Update User',
    DeleteUser = '[User Page] Delete User',
    UserErrors = '[User Page] User Errors',
    LoadUsersPerPage = '[User Page] Load Users Per Page',
    LoadedUsersPerPage = '[User Page] Loaded Users Per Page',
    LoadUserCount = '[User Page] Load Users Count',
    LoadedUserCount = '[User Page] Loaded Users Count'
}

export class LoadUsers implements Action {
    readonly type = ActionTypes.LoadUsers;
}

export class LoadedUsers implements Action {
    readonly type = ActionTypes.LoadedUsers;

    constructor(public payload: IUser[]) {
    }
}

export class LoadUsersPerPage implements Action {
    readonly type = ActionTypes.LoadUsersPerPage;

    constructor(public payload: IPage) {
    }
}

export class LoadedUsersPerPage implements Action {
    readonly type = ActionTypes.LoadedUsersPerPage;

    constructor(public payload: IUser[]) {
    }
}

export class RefreshUsers implements Action {
    readonly type = ActionTypes.RefreshUsers;

    constructor(public payload: IUser[]) {
    }
}

export class CreateUser implements Action {
    readonly type = ActionTypes.CreateUser;

    constructor(public payload: IUserRegistration) {
    }
}

export class UpdateUser implements Action {
    readonly type = ActionTypes.UpdateUser;

    constructor(public payload: IUser) {
    }
}

export class DeleteUser implements Action {
    readonly type = ActionTypes.DeleteUser;

    constructor(public payload: string) {
    }
}

export class UserErrors implements Action {
    readonly type = ActionTypes.UserErrors;

    constructor(public payload: any) {
    }
}

export class LoadUserCount implements Action {
    readonly type = ActionTypes.LoadUserCount;
}

export class LoadedUserCount implements Action {
    readonly type = ActionTypes.LoadedUserCount;

    constructor(public payload: number) {
    }
}

export type UserActions = LoadUsers & LoadedUsers &
    RefreshUsers & CreateUser & UpdateUser & DeleteUser &
    UserErrors & LoadUsersPerPage & LoadedUsersPerPage &
    LoadUserCount & LoadedUserCount;
