import {Component, OnInit} from '@angular/core';
import {ToastrService} from 'ngx-toastr';
import {Observable} from 'rxjs';
import {Store} from '@ngrx/store';
import {tap} from 'rxjs/internal/operators/tap';

import {IJudge, IUserRegistration} from '@models/interfaces/human';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {ChangePasswordModalComponent} from '@modal/components/change-password-modal/change-password-modal.component';
import {Actions} from '@models/constants/actions';
import {Mocks} from '@models/mocks/persons.mock';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {selectJudgesData} from '@judges/selectors/judjes.selector';
import {ChangePassword, CreateJudge, DeleteJudge, LoadJudges, UpdateJudge} from '@judges/actions/judges.action';
import {JudgesService} from '@judges/service/judges.service';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {Animations} from '@models/constants/animations';

@Component({
    selector: 'app-judge',
    templateUrl: './judges.component.html',
    styleUrls: ['./judges.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class JudgesComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public actions: string[];
    public judges$: Observable<IJudge[]>;

    constructor(private modalService: ModalService,
                private toastrService: ToastrService,
                private judgeService: JudgesService,
                private store: Store<any>
    ) {
        super();
    }

    ngOnInit() {
        this.getJudges();
        this.actions = [Actions.CHANGE, Actions.CHANGE_PASSWORD, Actions.REMOVE];
    }

    getJudges() {
        this.judges$ = this.store.select(selectJudgesData)
            .pipe(tap((judges: IJudge[]) => !judges && this.fetchJudges()));
    }

    fetchJudges() {
        this.store.dispatch(new LoadJudges());
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openJudgeModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            default:
                return this.openChangePasswordModal(event.item);
        }
    }

    openJudgeModal(judge: IJudge, edit: boolean) {
        const objectType = 'Judge';
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...judge}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateJudge(judge.id, res) : this.createJudge(res);
                }
            });
    }

    openChangePasswordModal(judge: IJudge) {
        this.modalService.openModal(ChangePasswordModalComponent,
            {centered: true},
            null,
            (res) => {
                if (res) {
                    const data = {data: judge, newPassword: res};
                    this.changeJudgePassword(data);
                }
            });
    }

    openDialogModal(message: string, item: IJudge) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.userName, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeJudge(item);
                }
            });
    }

    add() {
        this.openJudgeModal(Mocks.emptyJudge, false);
    }

    createJudge(credentials: IUserRegistration) {
        this.store.dispatch(new CreateJudge(credentials));
    }

    removeJudge(judge: IJudge) {
        this.store.dispatch(new DeleteJudge(judge.id));
    }

    updateJudge(judgeId: string, judge: IJudge) {
        judge.id = judgeId;
        this.store.dispatch(new UpdateJudge({judge, id: judgeId}));
    }

    changeJudgePassword(item: { data: IJudge, newPassword: string }) {
        this.store.dispatch(new ChangePassword(item));
    }
}
