import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {Location} from '@angular/common';
import {tap} from 'rxjs/internal/operators/tap';

import {Actions} from '@models/constants/actions';
import {IQuestion} from '@models/interfaces/quiz';
import {selectQuestionbyId, selectTestQuestions} from '@quiz/selectors/selectors';
import {
    CreateQuestionForTest,
    DeleteQuestionFromTest,
    GetQuestionsByTest,
    LoadQuestionById,
    RefreshQuestions,
    UpdateQuestion
} from '@quiz/actions/actions';
import {ModalService} from '@modal/services/modal.service';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {PageTypes} from '@models/constants/page-types';
import {QuizMocks} from '@models/mocks/quiz.mocks';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {TranslateService} from '@ngx-translate/core';
import {Icons} from '@models/constants/icons';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-questions',
    templateUrl: './questions.component.html',
    styleUrls: ['./questions.component.scss']
})
export class QuestionsComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public actions: string[];
    public objectType = PageTypes;
    public icons = Icons;
    public subjectName: string;
    public testName: string;
    public questions$: Observable<IQuestion[]>;

    private testId: string;
    private subjectId: string;
    private currentQuestion: object;

    constructor(private route: ActivatedRoute,
                private store: Store<any>,
                private modalService: ModalService,
                private translateService: TranslateService,
                private location: Location) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getRouteParams();
        this.getQuestions(this.testId);
        this.refreshQuestions();
    }

    refreshQuestions() {
        this.store.dispatch(new RefreshQuestions(null));
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams.subscribe((param) => {
            this.testId = param['test'];
            this.subjectId = param['subject'];
            this.subjectName = param['name'];
            this.testName = param['testName'];
        });
    }

    getQuestions(testId: string) {
        this.questions$ = this.store.select(selectTestQuestions)
            .pipe(tap((questions: IQuestion[]) => !questions && this.fetchQuestions(testId)));
    }

    fetchQuestions(testId: string) {
        this.store.dispatch(new GetQuestionsByTest(testId));
    }

    add() {
        this.openQuestionModal(QuizMocks.emptyQuestion, false);
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.CHANGE:
                this.getQuestionById(this.testId, event.item.id);
        }
    }

    getQuestionById(testId: string, questionId: string) {
        this.store.dispatch(new LoadQuestionById({testId, questionId}));
        this.subscriptions.sink = this.store.select(selectQuestionbyId)
            .subscribe((question) => {
                if (this.currentQuestion !== question) {
                    if (question.hasOwnProperty('answers')) {
                        this.currentQuestion = question;
                        this.openQuestionModal(this.currentQuestion, true);
                    }
                }
            });
    }

    openQuestionModal(question: any, edit: boolean) {
        const objectType = this.objectType.QUESTIONS;
        this.modalService.openModal(BaseModalComponent,
            {centered: true, size: 'lg'},
            {object: {...question}, edit, objectType, maxWidth: '1200px'},
            (res) => {
                if (res) {
                    res.id = question.id;
                    edit ? this.updateQuestion(this.testId, question.id, res) : this.createQuestion(res, this.testId);
                }
            });
    }

    openDialogModal(message: string, item: IQuestion) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeQuestion(item.id, this.testId);
                }
            });
    }

    createQuestion(question: IQuestion, testId: string) {
        this.store.dispatch(new CreateQuestionForTest({question, testId}));
    }

    updateQuestion(testId: string, questionId: string, question: any) {
        this.store.dispatch(new UpdateQuestion({testId, questionId, question}));
    }

    removeQuestion(questionId: string, testId: string) {
        this.store.dispatch(new DeleteQuestionFromTest({questionId, testId}));
    }

    back() {
        this.location.back();
    }
}
