import {Injectable} from '@angular/core';
import {environment} from '@environments/environment';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {IDivision} from '@models/interfaces/division';

@Injectable({
    providedIn: 'root'
})
export class DivisionsService {
    baseUrl: string = environment.BASE_URL + '/api/divisions/' + environment.version + '/division';

    constructor(private http: HttpClient) {
    }

    create(data: IDivision): Observable<IDivision> {
        return this.http.post<IDivision>(this.baseUrl + '/create', data)
            .pipe(map((response) => response));
    }

    update(data: IDivision, id: string | number): Observable<IDivision> {
        return this.http.put<IDivision>(this.baseUrl + `/${id}`, data)
            .pipe(map((response) => response));
    }

    remove(id: string): Observable<IDivision> {
        return this.http.delete<IDivision>(this.baseUrl + `/${id}`);
    }

    getDivisions(pageNumber?: number, pageSize?: number): Observable<IDivision[]> {
        return this.http.get<IDivision[]>(this.baseUrl + (pageNumber && pageSize ? `/all/${pageNumber}/${pageSize}` : '/all'))
            .pipe(map((response) => response));
    }

    getRoot(): Observable<IDivision> {
        return this.http.get<IDivision>(this.baseUrl + '/root')
            .pipe(map((response) => response));
    }

    findDivisonByName(divisions: IDivision[], divisionName: string): IDivision {
        return divisions.find((division: IDivision) => division.name === divisionName);
    }
}
