import {Action} from '@ngrx/store';
import {ITournament} from '@models/interfaces/tournament';

export enum ActionTypes {
    LoadTournaments = '[Tournaments Page] Load Tournaments',
    LoadedTournaments = '[Tournaments Page] Loaded Tournaments',
    ErrorTournaments = '[Tournaments Page] Error Tournaments',
    CreateTournament = '[Tournaments Page] Create Tournament',
    DeleteTournament = '[Tournaments Page] Delete Tournament',
    UpdateTournament = '[Tournaments Page] Update Tournament',
    ForceUpdateTournamentCompetitors = '[Tournaments Page] Force Update Tournament Competitors',
    ForceUpdateTournament = '[Tournaments Page] Force Update Tournament',
    LoadTournamentById = '[Tournaments Page] Load Tournament By Id',
    LoadedTournamentById = '[Tournaments Page] Loaded Tournament By Id',
    AddScoreList = '[Tournaments Page] Add Score List',
    RefreshTournaments = '[Tournaments Page] Refresh Tournaments',
    LoadAllData = '[Tournaments Page] Load All Data',
    LoadedAllData = '[Tournaments Page] Loaded All Data',
    StartTournament = '[Mobile Competitors Page] Start Tournament',
    LoadedStartTournament = '[Mobile Competitors Page] Loaded Start Tournament'
}

interface IPayload {
    tournament: ITournament;
    tournamentId: string;
}

export class LoadTournaments implements Action {
    readonly type = ActionTypes.LoadTournaments;

    constructor() {
    }
}

export class LoadedTournaments implements Action {
    readonly type = ActionTypes.LoadedTournaments;

    constructor(public payload: ITournament[]) {
    }
}

export class RefreshTournaments implements Action {
    readonly type = ActionTypes.RefreshTournaments;

    constructor(public payload: ITournament[]) {
    }
}

export class ErrorTournaments implements Action {
    readonly type = ActionTypes.ErrorTournaments;

    constructor(public payload: any) {
    }
}

export class CreateTournament implements Action {
    readonly type = ActionTypes.CreateTournament;

    constructor(public payload: ITournament) {
    }
}

export class DeleteTournament implements Action {
    readonly type = ActionTypes.DeleteTournament;

    constructor(public payload: string) {
    }
}

export class UpdateTournament implements Action {
    readonly type = ActionTypes.UpdateTournament;

    constructor(public payload: IPayload) {
    }
}

export class ForceUpdateTournamentCompetitors implements Action {
    readonly type = ActionTypes.ForceUpdateTournamentCompetitors;

    constructor(public payload: any) {
    }
}

export class ForceUpdateTournament implements Action {
    readonly type = ActionTypes.ForceUpdateTournament;

    constructor(public payload: any) {
    }
}

export class LoadTournamentById implements Action {
    readonly type = ActionTypes.LoadTournamentById;

    constructor(public payload: string) {
    }
}

export class LoadedTournamentById implements Action {
    readonly type = ActionTypes.LoadedTournamentById;

    constructor(public payload: ITournament) {
    }
}

export class AddScoreList implements Action {
    readonly type = ActionTypes.AddScoreList;

    constructor(public payload: any) {
    }
}

export class LoadAllData implements Action {
    readonly type = ActionTypes.LoadAllData;

    constructor() {
    }
}

export class LoadedAllData implements Action {
    readonly type = ActionTypes.LoadedAllData;

    constructor(public payload: any) {
    }
}

export class StartTournament implements Action {
    readonly type = ActionTypes.StartTournament;

    constructor(public payload: string) {
    }
}

export class LoadedStartTournament implements Action {
    readonly type = ActionTypes.LoadedStartTournament;

    constructor(public payload: string) {
    }
}


export type TournamentsAction = LoadTournaments &
    LoadedTournaments & ErrorTournaments & CreateTournament &
    DeleteTournament & UpdateTournament & LoadTournamentById &
    ForceUpdateTournamentCompetitors & ForceUpdateTournament &
    LoadedTournamentById & AddScoreList & RefreshTournaments &
    LoadAllData & LoadedAllData & StartTournament & LoadedStartTournament;
