import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {Location} from '@angular/common';
import {tap} from 'rxjs/operators';

import {ITournament} from '@models/interfaces/tournament';
import {ModalService} from '@modal/services/modal.service';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {Actions} from '@models/constants/actions';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {TournamentMocks} from '@models/mocks/tournaments.mock';
import {TournamentsService} from '@tournaments/services/tournaments.service';
import {JudgesService} from '@judges/service/judges.service';
import {MessageService} from '@services/message.service';
import {CreateTournament, DeleteTournament, LoadTournaments, StartTournament, UpdateTournament} from '@tournaments/actions/actions';
import {selectTournamentsData} from '@tournaments/selectors/selectors';
import {PageTypes} from '@models/constants/page-types';
import {Animations} from '@models/constants/animations';
import {LoadPersons} from '@persons/actions/actions';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {LoadJudges} from '@judges/actions/judges.action';
import EMPTY_TOURNAMENT = TournamentMocks.EMPTY_TOURNAMENT;

@Component({
    selector: 'app-tournaments',
    templateUrl: './tournaments.component.html',
    styleUrls: ['./tournaments.component.scss'],
    animations: [Animations.enterLeaveOpacity],
})
export class TournamentsComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public actions: string[];
    public tournaments$: Observable<ITournament[]>;
    public pageType = PageTypes;
    public pageTypes = PageTypes;
    public mobileActions: string[];
    public activeTournaments: ITournament[];

    constructor(private modalService: ModalService,
                private tournamentsService: TournamentsService,
                private judgesService: JudgesService,
                private messageService: MessageService,
                private router: Router,
                private store: Store<any>,
                private location: Location) {
        super();
    }

    ngOnInit() {
        this.getTournaments();
        this.fetchJudges();
        this.store.dispatch(new LoadPersons());
        this.mobileActions = [
            Actions.COMPETITORS,
            Actions.STAGES,
            Actions.START
        ];
        this.actions = [
            Actions.CHANGE,
            Actions.COMPETITORS,
            Actions.STAGES,
            Actions.RATING,
            Actions.REMOVE
        ];
    }

    getTournaments() {
        this.tournaments$ = this.store.pipe(
            select(selectTournamentsData),
            tap((tournaments: ITournament[]) => {
                if (!tournaments) {
                    return this.fetchTournaments();
                }

                this.activeTournaments = this.tournamentsService.filterTournaments(tournaments);
            })
        );
    }

    fetchTournaments() {
        this.store.dispatch(new LoadTournaments());
    }

    fetchJudges() {
        this.store.dispatch(new LoadJudges());
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openTournamentModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.COMPETITORS:
                return this.goToCompetitors(event.item.id, event.item.name);
            case Actions.STAGES:
                return this.goToStages(event.item.id, event.item.name);
            case Actions.RATING:
                return this.goToRating(event.item.id);
            case Actions.START:
                return this.goToExercise(event.item.id, event.item.stages[0].id);
        }
    }

    openTournamentModal(tournament: ITournament, edit: boolean) {
        const objectType = 'Tournament';
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...tournament}, edit, objectType},
            (updatedTournament) => {
                if (updatedTournament) {
                    edit ? this.updateTournament(tournament.id, updatedTournament) :
                        this.createTournament(updatedTournament);
                }
            });
    }

    openDialogModal(message: string, tournament: ITournament) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: tournament.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeTournament(tournament.id);
                }
            });
    }

    addTournament() {
        const tournament: ITournament = EMPTY_TOURNAMENT;
        this.openTournamentModal(tournament, false);
    }

    createTournament(tournament: ITournament) {
        this.store.dispatch(new CreateTournament(tournament));
    }

    removeTournament(tournamentId: string) {
        this.store.dispatch(new DeleteTournament(tournamentId));
    }

    updateTournament(tournamentId: string, tournament: ITournament) {
        tournament.id = tournamentId;
        this.store.dispatch(new UpdateTournament({tournament, tournamentId}));
    }

    goToStages(id: string, name: string) {
        this.router.navigate(['/tournaments/stages'], {queryParams: {tournament: id, name}});
    }

    goToCompetitors(id: string, name: string) {
        this.router.navigate(['/tournaments/competitors'], {queryParams: {tournament: id, name}});
    }

    goToRating(id: string) {
        this.router.navigate(['tournaments/rating'], {queryParams: {tournament: id}});
    }

    goToExercise(tournamentId: string, stageId: string) {
        this.store.dispatch(new StartTournament(tournamentId));
        this.router.navigate(['/exercise'], {queryParams: {tournament: tournamentId, stage: stageId}});
    }

    goBack() {
        this.location.back();
    }
}
