import {ISpecialities} from '@models/interfaces/specialities';
import {ActionTypes, SpecialitiesActions} from '@specialities/actions/actions';

export interface State {
    specialities: ISpecialities[];
    errors?: any;
}

export const initialState: State = {
    specialities: undefined
};

export function reducer(state: State = initialState, action: SpecialitiesActions) {
    switch (action.type) {
        case ActionTypes.LoadedSpecialities:
            return {...state, specialities: action.payload};
        case ActionTypes.SpecialitiesErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
