import {Component, OnInit} from '@angular/core';
import {Location} from '@angular/common';
import {ActivatedRoute, Router} from '@angular/router';
import {select, Store} from '@ngrx/store';

import {ModalService} from '@modal/services/modal.service';
import {StagesService} from '../../service/stages.service';
import {TournamentsService} from '@tournaments/services/tournaments.service';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {IStage} from '@models/interfaces/stage';
import {ITournament} from '@models/interfaces/tournament';
import {CreateStage, DeleteStage, UpdateStage} from '@stages/actions/actions';
import {selectTournamentsData} from '@tournaments/selectors/selectors';
import {Actions} from '@models/constants/actions';
import {PageTypes} from '@models/constants/page-types';
import {TournamentMocks} from '@models/mocks/tournaments.mock';
import {Icons} from '@models/constants/icons';
import {Animations} from '@models/constants/animations';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {stagesMapperFromServer} from '@mappers/tournament.mapper';

@Component({
    selector: 'app-stages',
    templateUrl: './stages.component.html',
    styleUrls: ['./stages.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class StagesComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public stages: IStage[];
    public actions: string[] = [];
    public pageTypes = PageTypes;
    public icons = Icons;

    private tournamentId: string;
    public tournamentName: string;
    public mobileActions: string[];
    private tournament: ITournament;

    constructor(private modalService: ModalService,
                private stagesService: StagesService,
                private route: ActivatedRoute,
                private store: Store<any>,
                private location: Location,
                private router: Router,
                private tournamentService: TournamentsService
    ) {
        super();
    }

    ngOnInit() {
        this.getRouteParams();
        this.mobileActions = [
            Actions.START
        ];
        this.actions = [Actions.CHANGE, Actions.REMOVE];
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams
            .subscribe((params) => {
                this.tournamentId = params['tournament'];
                this.tournamentName = params['name'];
                this.getTournaments(this.tournamentId);
            });
    }

    getTournaments(tournamentId: string) {
        this.subscriptions.sink = this.store
            .pipe(select(selectTournamentsData))
            .subscribe((tournaments: ITournament[]) => {
                if (tournaments && tournaments.length) {
                    this.tournament = this.findTournamentById(tournaments, tournamentId);
                    this.stages = this.getStages(this.tournament.stages);
                }
            });
    }

    getStages(stages: IStage[]): IStage[] {
        return stages.map((stage: IStage, index: number) => stagesMapperFromServer(stage, index));
    }

    findTournamentById(tournaments: ITournament[], tournamentId: string): ITournament {
        return this.tournamentService.findTournamentById(tournaments, tournamentId);
    }

    add() {
        this.openStageModal(TournamentMocks.emptyStage, false);
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openStageModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.START:
                return this.goToExercise(this.tournamentId, event.item.id);
            default:
                return;
        }
    }

    openStageModal(stage: IStage, edit: boolean) {
        const objectType = 'Stage';

        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...stage}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateStage(stage, res) : this.addStage(res);
                }
            });
    }

    openDialogModal(message: string, item: IStage) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeStage(this.tournamentId, item.id);
                }
            });
    }

    addStage(stage: IStage) {
        this.store.dispatch(new CreateStage({stage, tournament: this.tournament}));
    }

    removeStage(tournamentId: string, id: string) {
        this.store.dispatch(new DeleteStage({tournament: this.tournament, id}));
    }

    updateStage(item, updatedItem) {
        updatedItem.id = item.id;
        this.store.dispatch(new UpdateStage({
            stage: updatedItem,
            tournament: this.tournament
        }));
    }

    goToExercise(tournamentId: string, stageId: string) {
        this.router.navigate(['/exercise'], {queryParams: {tournament: tournamentId, stage: stageId}});
    }

    onBackClick() {
        this.location.back();
    }
}
