import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '@environments/environment';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {IUser, IUserRegistration} from '@models/interfaces/human';
import {judgeMapperFromServer, judgeRegistrationMapperToServer} from '@mappers/judge.mapper';

@Injectable({
    providedIn: 'root'
})
export class UserService {
    private userBaseUrl: string = environment.BASE_URL + '/api/users/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getUsers(): Observable<IUser[]> {
        return this.http.get(this.userBaseUrl + '/all/user')
            .pipe(map((response: IUser[]) => response.map((item) => judgeMapperFromServer(item))));
    }

    getUsersPerPage(pageNumber: number, pageSize: number): Observable<IUser[]> {
        return this.http.get(this.userBaseUrl + `/all/${pageNumber}/${pageSize}`)
            .pipe(map((response: IUser[]) => response));
    }

    getUsersCount(): Observable<number> {
        return this.http.get(this.userBaseUrl + '/count')
            .pipe(map((response: number) => response));
    }

    createUser(user: IUserRegistration): Observable<IUserRegistration> {
        const mappedData = judgeRegistrationMapperToServer(user);
        return this.http.post(this.userBaseUrl + '/createUser', mappedData)
            .pipe(map((response: IUserRegistration) => judgeMapperFromServer(response)));
    }
}
