import {Component, OnInit} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

import {Actions} from '@models/constants/actions';
import {IWeaponTypes} from '@models/interfaces/weapons';
import {selectWeaponErrors, selectWeaponTypesData} from '@weapon/selectors/selectors';
import {CreateWeaponType, DeleteWeaponType, LoadWeaponTypesForList, UpdateWeaponType} from '@weapon/actions/actions';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {PageTypes} from '@models/constants/page-types';
import {WeaponMocks} from '@models/mocks/weapon.mocks';
import {MessageService} from '@services/message.service';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {Animations} from '@models/constants/animations';

@Component({
    selector: 'app-weapon-types',
    templateUrl: './weapon-types.component.html',
    styleUrls: ['./weapon-types.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class WeaponTypesComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public actions: string[];
    public pageTypes = PageTypes;

    public weaponTypes$: Observable<IWeaponTypes[]>;

    constructor(private store: Store<any>,
                private modalService: ModalService,
                private messageService: MessageService) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getWeaponTypes();
    }

    getWeaponTypes() {
        this.weaponTypes$ = this.store.select(selectWeaponTypesData)
            .pipe(tap((weaponTypes: IWeaponTypes[]) => !weaponTypes && this.fetchWeaponTypes()));
    }

    fetchWeaponTypes() {
        this.store.dispatch(new LoadWeaponTypesForList());
    }

    handleAction(event: { action: string, item: IWeaponTypes }) {
        switch (event.action) {
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.CHANGE:
                this.openWeaponTypeModal(event.item, true);
        }
    }

    add() {
        this.openWeaponTypeModal(WeaponMocks.emptyWeaponType, false);
    }

    openWeaponTypeModal(weaponType: IWeaponTypes, edit: boolean) {
        const objectType = this.pageTypes.WEAPON_TYPES;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...weaponType}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateWeaponType(weaponType, res) : this.createWeaponType(res);
                }
            });
    }

    openDialogModal(message: string, item: IWeaponTypes) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteWeaponType(item.id);
                }
            });
    }

    createWeaponType(weaponType: IWeaponTypes) {
        this.store.dispatch(new CreateWeaponType(weaponType));
    }

    updateWeaponType(weaponType: IWeaponTypes, updatedWeaponType: IWeaponTypes) {
        updatedWeaponType.id = weaponType.id;
        this.store.dispatch(new UpdateWeaponType(updatedWeaponType));
    }

    deleteWeaponType(weaponTypeId: string) {
        this.store.dispatch(new DeleteWeaponType(weaponTypeId));
    }

}
