import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromWorkspace from '@workspace/reducers/reducer';

export const selectWorkspaces = createFeatureSelector<fromWorkspace.State>('workspaces');

export const selectWorkspaceData = createSelector(
    selectWorkspaces,
    (state: fromWorkspace.State) => state.workspaces
);

export const selectCurrentWorkspace = createSelector(
    selectWorkspaces,
    (state: fromWorkspace.State) => state.currentWorkspace
);
