import {Component, OnInit, ViewContainerRef} from '@angular/core';
import {IDivision} from '@models/interfaces/division';
import {Observable, Subscription} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {TranslateService} from '@ngx-translate/core';
import {Location} from '@angular/common';
import {tap} from 'rxjs/operators';

import {selectDivisionNames, selectDivisionsData} from '@divisions/selectors/selectors';
import {LoadPersons} from '@persons/actions/actions';
import {IDialogActions, IPerson, IStatus, IUser} from '@models/interfaces/human';
import {selectPersonData} from '@persons/selectors/selectors';
import {LoadDivisionNames, LoadDivisions} from '@divisions/actions/actions';
import {PageTypes} from '@models/constants/page-types';
import {Actions} from '@models/constants/actions';
import {selectStatuses, selectUser} from '@shared/selectors/selectors';
import {LoadStatuses, LoadUserById} from '@shared/actions/actions';
import {CombatListService} from '@combat-list/services/combat-list.service';
import {ClearCombatList, CreateCheckin} from '@combat-list/actions/actions';
import {selectCombatListData} from '@combat-list/selectors/selectors';
import {SharedService} from '@shared/services/shared.service';
import {ICombatNote} from '@models/interfaces/checkin';
import {UnsubscribeOndestroyAdapter} from '../../../adstracts/unsubscribe-ondestroy-adapter';
import {ModalRootComponent} from '@shared/components/modal-root/modal-root.component';
import {ModalService} from '@modal/services/modal.service';
import {SubSink} from 'subsink';

@Component({
    selector: 'app-combat-list',
    templateUrl: './combat-list.component.html',
    styleUrls: ['./combat-list.component.scss']
})
export class CombatListComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public persons: IPerson[] = [];
    public personsByDivision: IPerson[] = [];
    public pageType = PageTypes;
    public user: IUser = null;
    public divisionNames: string[] = [];
    public mobileActions: string[];
    public statuses: string[];
    public translatedStatuses: string[];
    public selectedItem: string;
    public selectedStatus: string;
    public dialogValue: string;
    public combatList: ICombatNote;

    public user$: Observable<IUser>;
    public divisions$: Observable<IDivision[]>;
    public divisionNames$: Observable<string[]>;

    private dialogValueSubscription: Subscription;
    private modalSubs: SubSink = new SubSink();

    constructor(
        private store: Store<any>,
        private translateService: TranslateService,
        private combatListService: CombatListService,
        private sharedService: SharedService,
        private location: Location,
        private vcRef: ViewContainerRef,
        private modalService: ModalService
    ) {
        super();
    }

    ngOnInit() {
        this.mobileActions = [Actions.STATUS];
        this.getUser();
        this.getDivisions();
        this.getPersons();
        this.getStatuses();
        this.getCombatList();
    }

    getCombatList() {
        this.subscriptions.sink = this.store
            .pipe(select(selectCombatListData))
            .subscribe(combatList => {
                if (combatList) {
                    const options = this.getModalOptions(combatList);
                    this.initModal(options);
                }
            });
    }

    initModal(options) {
        this.modalSubs.sink = this.modalService
            .initModal(ModalRootComponent, options)
            .subscribe((response) => {
                if (response) {
                    this.onCancel();
                    this.modalSubs.unsubscribe();
                }
            });
    }

    getUser() {
        this.user$ = this.store.pipe(
            select(selectUser),
            tap((user: IUser) => {
                if (!user) {
                    return this.fetchUser();
                }
                this.user = user;
            }));
    }

    fetchUser() {
        const userId = localStorage.getItem('userId');
        this.store.dispatch(new LoadUserById(userId));
    }

    getDivisions() {
        this.divisions$ = this.store.pipe(
            select(selectDivisionsData),
            tap((divisions: IDivision[]) => {
                if (!divisions) {
                    return this.fetchDivisions();
                }
                this.getDivisionNames(divisions);
            })
        );
    }

    fetchDivisions() {
        this.store.dispatch(new LoadDivisions());
    }

    getPersons() {
        this.store.dispatch(new LoadPersons());
        this.subscriptions.sink = this.store
            .pipe(select(selectPersonData))
            .subscribe((persons: IPerson[]) => (this.persons = persons));
    }

    getStatuses() {
        this.store.dispatch(new LoadStatuses());
        this.subscriptions.sink = this.store
            .pipe(select(selectStatuses))
            .subscribe((statuses: IStatus[]) => {
                this.statuses = this.filterStatusesByName(statuses);
                this.translatedStatuses = this.translateStatuses(this.statuses);
            });
    }

    getDivisionNames(divisions: IDivision[]) {
        this.divisionNames$ = this.store.pipe(
            select(selectDivisionNames),
            tap((divisionNames: string[]) => {
                if (!divisionNames) {
                    return this.fetchDivisionNames(divisions);
                }
                this.divisionNames = divisionNames;
            })
        );
    }

    fetchDivisionNames(divisions: IDivision[]) {
        this.store.dispatch(new LoadDivisionNames(divisions));
    }

    getPersonsByDivision(divisionName: string) {
        this.personsByDivision = this.findPersonsByDivisions(
            this.persons,
            divisionName
        );
    }

    findPersonsByDivisions(
        persons: IPerson[],
        divisionName: string
    ): IPerson[] {
        return persons.filter((person: IPerson) => {
            if (person.division && person.division.name === divisionName) {
                return person;
            }
        });
    }

    setPersonStatus(personId: string, status: string) {
        return this.personsByDivision.map((person: IPerson) => {
            const index = this.translatedStatuses.findIndex(
                (item: string) => item === status
            );
            if (person.id === personId) {
                person['translatedStatus'] = status;
                person['status'] = this.statuses[index];
            }
            return person;
        });
    }

    filterStatusesByName(statuses: IStatus[]): string[] {
        return statuses.map((status: IStatus) => status.state);
    }

    translateStatuses(statuses: string[]): string[] {
        return this.sharedService.translateArray(statuses);
    }

    handleAction(event: { action: string; item: any }) {
        if (event.action === Actions.STATUS) {
            this.displayActionDialog(
                this.getDialogOptions('', this.translatedStatuses),
                event.item.id
            );
        }
    }

    displayActionDialog(options: IDialogActions, personId?: string) {
        this.combatListService.getDialogAction(options);
        this.dialogValueSubscription = this.combatListService.dialogValue.subscribe(
            result => {
                this.dialogValue = result;
                if (this.dialogValue && this.dialogValue !== '') {
                    if (this.divisionNames.includes(this.dialogValue)) {
                        this.getDivisionDialogValue(this.dialogValue);
                        return;
                    }
                    this.getPersonDialogValue(personId, this.dialogValue);
                }
            }
        );
    }

    getDivisionDialogValue(dialogValue: string) {
        this.selectedItem = dialogValue;
        this.getPersonsByDivision(this.selectedItem);
        this.dialogValueSubscription.unsubscribe();
    }

    getPersonDialogValue(personId: string, dialogValue: string) {
        this.selectedStatus = dialogValue;
        this.personsByDivision = this.setPersonStatus(
            personId,
            this.selectedStatus
        );
        this.dialogValueSubscription.unsubscribe();
    }

    getDialogOptions(title: string, actions: string[]): IDialogActions {
        return this.combatListService.getDialogOptions(title, actions);
    }

    getModalOptions(combatList: ICombatNote) {
        return this.modalService.getModalDialogOptions({
            vcRef: this.vcRef,
            context: {
                path: 'combat-list-modal-view',
                combatList,
                divisionLength: this.personsByDivision.length
            },
            fullscreen: false,
        });
    }

    // Don't remove this method.
    // It needs for mobile.
    saveCheckin() {
        this.store.dispatch(new CreateCheckin(this.personsByDivision));
    }

    // Don't remove this method.
    // It needs for mobile.
    onCancel() {
        this.location.back();
    }
}
