import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromTournaments from '@tournaments/reducers/reducer';

export const selectTournaments = createFeatureSelector<fromTournaments.State>('tournaments');

export const selectTournamentsData = createSelector(
    selectTournaments,
    (state: fromTournaments.State) => state.tournaments
);

export const selectTournamentsErrors = createSelector(
    selectTournaments,
    (state: fromTournaments.State) => state.errors
);

export const selectTournamentById = createSelector(
    selectTournaments,
    (state: fromTournaments.State) => state.tournament
);
