import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Location} from '@angular/common';

import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {IWeapon, IWeaponTypes} from '@models/interfaces/weapons';
import {IDivision} from '@models/interfaces/division';
import {IPerson} from '@models/interfaces/human';
import {selectWeaponList, selectWeaponTypesData} from '@weapon/selectors/selectors';
import {selectDivisionNames, selectDivisionsData} from '@divisions/selectors/selectors';
import {selectPersonData, selectPersonsByDivision} from '@persons/selectors/selectors';
import {Animations} from '@models/constants/animations';
import {WeaponMocks} from '@models/mocks/weapon.mocks';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {Actions} from '@models/constants/actions';
import {PageTypes} from '@models/constants/page-types';
import {ModalService} from '@modal/services/modal.service';
import {
    CreateWeaponListItem,
    DeleteWeaponListItem,
    LoadPersonsWeaponList,
    LoadWeaponList,
    LoadWeaponTypesForList,
    UnlinkWeapon
} from '@weapon/actions/actions';
import {LoadPersons, LoadPersonsByDivision} from '@persons/actions/actions';
import {LoadDivisionNames, LoadDivisions} from '@divisions/actions/actions';
import {DialogsService} from '@shared/services/dialogs.service';
import {SubSink} from 'subsink/dist/subsink';

@Component({
    selector: 'app-weapon-list',
    templateUrl: './weapon-list.component.html',
    styleUrls: ['./weapon-list.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class WeaponListComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public selectedDivision: IDivision;
    public selectedPerson: string;
    public pageTypes = PageTypes;
    public actions: string[];
    public divisionNames: string[];
    public divisions: IDivision[];
    public personsNames: string[];

    public weaponTypes$: Observable<IWeaponTypes[]>;
    public divisions$: Observable<IDivision[]>;
    public weapons$: Observable<IWeapon[]>;
    public persons$: Observable<IPerson[]>;
    public divisionNames$: Observable<string[]>;
    public personsByDivision$: Observable<IPerson[]>;

    private dialogSubs: SubSink = new SubSink();

    constructor(private router: Router,
                private store: Store<any>,
                private modalService: ModalService,
                private location: Location,
                private dialogService: DialogsService) {
        super();
    }

    ngOnInit() {
        this.actions = [
            Actions.CHANGE,
            Actions.REMOVE,
            Actions.ADD_SHOOTS,
            Actions.TIE,
            Actions.UNTIE
        ];
        this.getWeaponList();
        this.getWeaponTypes();
        this.getDivisions();
        this.getPersons();
    }

    getWeaponList() {
        this.weapons$ = this.store.select(selectWeaponList);
    }

    getWeaponTypes() {
        this.weaponTypes$ = this.store.select(selectWeaponTypesData)
            .pipe(tap((weaponTypes: IWeaponTypes[]) => !weaponTypes && this.fetchWeaponTypes()));
    }

    fetchWeaponTypes() {
        this.store.dispatch(new LoadWeaponTypesForList());
    }

    getDivisions() {
        this.fetchDivisions();
        this.divisions$ = this.store.select(selectDivisionsData).pipe(
            tap((divisions: IDivision[]) => {
                if (divisions) {
                    this.getDivisionNames(divisions);
                    this.divisions = divisions;
                }
            })
        );
    }

    fetchDivisions() {
        this.store.dispatch(new LoadDivisions());
    }

    getDivisionNames(divisions: IDivision[]) {
        this.store.dispatch(new LoadDivisionNames(divisions));
        this.divisionNames$ = this.store.pipe(
            select(selectDivisionNames),
            tap((divisionNames: string[]) => this.divisionNames = divisionNames)
        );
    }

    getPersons() {
        this.persons$ = this.store.select(selectPersonData)
            .pipe(tap((persons: IPerson[]) => !persons && this.fetchPersons()));
    }

    fetchPersons() {
        this.store.dispatch(new LoadPersons());
    }

    handleAction(event: { action: string, item: IWeapon }) {
        switch (event.action) {
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.CHANGE:
                return this.openWeaponModal(event.item, true);
            case Actions.UNTIE:
                return this.unlinkWeapon(event.item.id);
        }
    }

    add() {
        this.openWeaponModal(WeaponMocks.emptyWeapon, false);
    }

    openWeaponModal(weapon: IWeapon, edit: boolean) {
        const objectType = this.pageTypes.WEAPON;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...weapon}, edit, objectType},
            (res) => {
                if (res) {
                    if (edit) {
                        const body = {...weapon, ...res};
                        this.createWeaponListItem(body);
                    } else {
                        this.createWeaponListItem(res);
                    }
                }
            });
    }

    openDialogModal(message: string, item: IWeapon) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.weaponName.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteWeaponListItem(item.id);
                }
            });
    }

    createWeaponListItem(weapon: IWeapon) {
        this.store.dispatch(new CreateWeaponListItem(weapon));
    }

    deleteWeaponListItem(weaponId: string) {
        this.store.dispatch(new DeleteWeaponListItem(weaponId));
    }

    unlinkWeapon(weaponId: string) {
        this.store.dispatch(new UnlinkWeapon(weaponId));
    }

    onSelectItem(value: any) {
        this.selectedDivision = value;
        this.store.dispatch(new LoadWeaponList(this.selectedDivision.id));
        this.store.dispatch(new LoadPersonsByDivision(this.selectedDivision.id));
    }

    initDialog(actions: string[], type: 'division' | 'persons') {
        const options = this.dialogService.getDialogOptions('', actions);
        this.dialogSubs.sink = this.dialogService.getDialogAction(options)
            .subscribe((result) => {
                if (result && result !== '') {
                    this.dialogType(type, result);
                }
            });
    }

    dialogType(type: 'division' | 'persons', result: string) {
        console.log(result);
        switch (type) {
            case 'division':
                const index = this.findDivisionIndex(this.divisionNames, result);
                this.selectedDivision = this.divisions[index];
                return this.getPersonsByDivision(this.selectedDivision);
            case 'persons':
                this.selectedPerson = result;
                this.store.dispatch(new LoadPersonsWeaponList({
                    personName: result,
                    divisionId: this.selectedDivision.id
                }));
                return;

        }
    }

    findDivisionIndex(divisionNames: string[], selectedDivision: string) {
        return divisionNames.findIndex((division: string) => division === selectedDivision);
    }

    getPersonsByDivision(division: IDivision) {
        this.store.dispatch(new LoadPersonsByDivision(division.id));
        this.personsByDivision$ = this.store.pipe(
            select(selectPersonsByDivision),
            tap((personsByDivision: IPerson[]) => {
                if (personsByDivision) {
                    this.personsNames = this.getPersonsNames(personsByDivision);
                }
            })
        );
    }

    getPersonsNames(persons: IPerson[]): string[] {
        return persons.map((person: IPerson) => person.userName);
    }

    goToWeaponTypes() {
        this.router.navigate(['weapon-list/weapon-types']);
    }

    goBack() {
        this.location.back();
    }

}
