import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {environment} from '@environments/environment';
import {IStatus, IUser} from '@models/interfaces/human';
import {IRank} from '@models/interfaces/ranks';
import {TranslateService} from '@ngx-translate/core';

@Injectable({
    providedIn: 'root'
})
export class SharedService {

    private validationBaseUrl: string = environment.BASE_URL + '/api/validation/' + environment.version;
    private disqualificationUrl: string = environment.BASE_URL + '/api/competitions/' + environment.version;
    private personBaseUrl: string = environment.BASE_URL + '/api/persons/' + environment.version;
    private checkInBaseUrl: string = environment.BASE_URL + '/api/checkin/' + environment.version;
    private userBaseUrl: string = environment.BASE_URL + '/api/users/' + environment.version;
    private rankBaseUrl: string = environment.BASE_URL + '/api/rank/' + environment.version;
    public imageBaseUrl: string = environment.BASE_URL + '/api/images/' + environment.version;


    constructor(private http: HttpClient,
                private translateService: TranslateService) {
    }

    getValidations(): Observable<any> {
        return this.http.get(this.validationBaseUrl + '/');
    }

    getDisqualificationTypes(): Observable<string[]> {
        return this.http.get(this.disqualificationUrl + '/enum/disqualification')
            .pipe(map((response: string[]) => response));
    }

    uploadImage(file: File, id: string): Observable<any> {
        const formData = new FormData();
        formData.append('file', file, file.name);

        const n = id.indexOf('.');
        id = id.substring(0, n !== -1 ? n : id.length);

        return this.http.post(this.imageBaseUrl + `/${id}`, formData)
            .pipe(map((response: any) => response));
    }

    deleteImage(imagePath: string): Observable<any> {
        return this.http.delete(this.imageBaseUrl + `/${imagePath}`)
            .pipe(map((response) => response));
    }

    getPersonStatuses(): Observable<IStatus[]> {
        return this.http.get(this.personBaseUrl + '/enum/present')
            .pipe(map((response: IStatus[]) => response));
    }

    getTimeIntervals(): Observable<string[]> {
        return this.http.get(this.checkInBaseUrl + '/enum/interval')
            .pipe(map((response: string[]) => response));
    }

    getUserById(userId: string): Observable<IUser> {
        return this.http.get(this.userBaseUrl + `/${userId}`)
            .pipe(map((response: IUser) => response));
    }

    getRanks(): Observable<IRank[]> {
        return this.http.get(this.rankBaseUrl + '/all')
            .pipe(map((response: IRank[]) => response));
    }

    get baseUrl() {
        return this.imageBaseUrl;
    }

    translateArray(array: string[]): string[] {
        return array.map((item: string) => {
            this.translateService.get(item)
                .subscribe((response: string) => item = response);
            return item;
        });
    }

    getMenu(): any[] {
        return [
            {name: 'Dashboard', path: '/dashboard', children: []},
            {name: 'judges', path: '/judges', children: []},
            {name: 'persons', path: '/persons', children: []},
            {name: 'divisions', path: '/divisions', children: []},
            {name: 'specialities', path: '/specialities', children: []},
            {name: 'users', path: '/users', children: []},
            {name: 'courses', path: '/courses', children: []},
            {name: 'weaponList', path: '/weapon-list', children: [
                    {
                        name: 'weaponType',
                        path: '/weapon-types'
                    },
                ]},
            {
                name: 'divisionList', path: '/checkin', children: [
                    {
                        name: 'checkinDetails',
                        path: '/checkin-details',
                        children: []
                    },
                ]
            },
            {name: 'login', path: '/login', children: []},
            {name: 'answers', path: '/answers', children: []},
            {
                name: 'tournaments',
                path: '/tournaments',
                children: [
                    {
                        name: 'stages',
                        path: '/stages'
                    },
                    {
                        name: 'competitors',
                        path: '/competitors'
                    },
                    {
                        name: 'rating',
                        path: '/rating'
                    }
                ]
            },
            {
                name: 'subjects',
                path: '/subjects',
                children: [
                    {
                        name: 'tests',
                        path: '/tests',
                        children: [
                            {
                                name: 'Questions',
                                path: '/questions'
                            },
                        ]
                    },
                    {
                        name: 'standards',
                        path: '/standards',
                        children: [
                            {
                                name: 'categories',
                                path: '/categories'
                            },
                            {
                                name: 'conditions',
                                path: '/conditions',
                                children: [
                                    {
                                        name: 'units',
                                        path: '/units',
                                    }
                                ]
                            },
                        ]
                    }
                ]
            },
        ];
    }
}
