import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {PageTypes} from '@models/constants/page-types';
import {ModalService} from '@modal/services/modal.service';
import {StandardsMocks} from '@models/mocks/specialities';
import {CreateCategory, DeleteCategory, LoadCategories, UpdateCategory} from '@quiz/actions/actions';
import {IStandardCategory} from '@models/interfaces/standard';
import {selectCategories} from '@quiz/selectors/selectors';
import {Actions} from '@models/constants/actions';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-categories',
    templateUrl: './categories.component.html',
    styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public objectType = PageTypes;
    public actions: string[];

    public categories$: Observable<IStandardCategory[]>;

    constructor(private modalService: ModalService,
                private store: Store<any>) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getCategories();
    }

    getCategories() {
        this.categories$ = this.store.select(selectCategories)
            .pipe(tap((categories: IStandardCategory[]) => !categories && this.fetchCategories()));
    }

    fetchCategories() {
        this.store.dispatch(new LoadCategories());
    }

    add() {
        this.openCategoryModal(StandardsMocks.emptyCategory, false);
    }

    openCategoryModal(category: any, edit: boolean) {
        const objectType = this.objectType.CATEGORY;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...category}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateCategory(res, category.id) : this.createCategory(res);
                }
            });
    }

    openDialogModal(message: string, item: IStandardCategory) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.nameCategoryRus, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteCategory(item.id);
                }
            });
    }

    handleAction(event: { action: string, item: IStandardCategory }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openCategoryModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
        }
    }

    createCategory(category: IStandardCategory) {
        this.store.dispatch(new CreateCategory(category));
    }

    updateCategory(category: IStandardCategory, categoryId: string) {
        category.id = categoryId;
        this.store.dispatch(new UpdateCategory(category));
    }

    deleteCategory(categoryId: string) {
        this.store.dispatch(new DeleteCategory(categoryId));
    }
}
