import {Action} from '@ngrx/store';
import {IDivision} from '@models/interfaces/division';

export enum ActionTypes {
    LoadDivisions = '[Divisions Page] Load Divisions',
    LoadedDivisions = '[Divisions Page] Loaded Divisions Success',
    ErrorsDivisions = '[Divisions Page] Divisions Errors',
    ErrorsRootDivisions = '[Divisions Page] Root Divisions Errors',
    LoadRootDivision = '[Divisions Page] Load Root Division',
    LoadedRootDivision = '[Divisions Page] Loaded Root Division Success',
    CreateDivision = '[Division Page] Create Division',
    RemoveDivision = '[Division Page] Remove Division',
    UpdateDivision = '[Division Page] Update Division',
    LoadDivisionNames = '[Division Page] Load Division Names',
    LoadedDivisionNames = '[Division Page] Loaded Division Names'
}

interface IPayload {
    division: IDivision;
    id: string | number;
}

export class LoadDivisions implements Action {
    readonly type = ActionTypes.LoadDivisions;
}

export class LoadedDivisions implements Action {
    readonly type = ActionTypes.LoadedDivisions;

    constructor(public payload: IDivision[]) {
    }
}

export class LoadRootDivision implements Action {
    readonly type = ActionTypes.LoadRootDivision;
}

export class LoadedRootDivision implements Action {
    readonly type = ActionTypes.LoadedRootDivision;

    constructor(public payload: IDivision) {
    }
}

export class ErrorsDivisions implements Action {
    readonly type = ActionTypes.ErrorsDivisions;

    constructor(public payload: any) {
    }
}

export class ErrorsRootDivisions implements Action {
    readonly type = ActionTypes.ErrorsRootDivisions;

    constructor(public payload: any) {
    }
}

export class CreateDivision implements Action {
    readonly type = ActionTypes.CreateDivision;

    constructor(public payload: IDivision) {
    }
}

export class UpdateDivision implements Action {
    readonly type = ActionTypes.UpdateDivision;

    constructor(public payload: IPayload) {
    }
}

export class RemoveDivision implements Action {
    readonly type = ActionTypes.RemoveDivision;

    constructor(public payload: IPayload) {
    }
}

export class LoadDivisionNames implements Action {
    readonly type = ActionTypes.LoadDivisionNames;

    constructor(public payload: IDivision[]) {
    }
}

export class LoadedDivisionNames implements Action {
    readonly type = ActionTypes.LoadedDivisionNames;

    constructor(public payload: string[]) {
    }
}

export type DivisionActions = LoadDivisions &
    LoadedDivisions & ErrorsDivisions & ErrorsRootDivisions & LoadRootDivision &
    LoadedRootDivision & CreateDivision & UpdateDivision &
    RemoveDivision & LoadDivisionNames & LoadedDivisionNames;
