import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs';

import {environment} from '@environments/environment';
import {ICourse} from '@models/interfaces/courses';

@Injectable({
    providedIn: 'root'
})
export class CoursesService {
    private coursesBaseUrl: string = environment.BASE_URL + '/api/course/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getCourses(): Observable<ICourse[]> {
        return this.http.get(this.coursesBaseUrl + '/get/all/course')
            .pipe(map((response: ICourse[]) => response));
    }

    createCourse(course: ICourse): Observable<ICourse> {
        return this.http.post(this.coursesBaseUrl + '/post/course/', course)
            .pipe(map((response: ICourse) => response));
    }

    updateCourse(course: ICourse): Observable<ICourse> {
        return this.http.put(this.coursesBaseUrl + `/put/course/${course.id}`, course)
            .pipe(map((response: ICourse) => response));
    }

    deleteCourse(courseId: string): Observable<any> {
        return this.http.delete(this.coursesBaseUrl + `/api/course/delete/${courseId}`)
            .pipe(map((response) => response));
    }
}
