import {Component, OnInit} from '@angular/core';
import {AbstractForm} from '@forms/abstract-form';
import {FormsService} from '@forms/services/forms.service';
import {FormArray, FormBuilder, Validators} from '@angular/forms';
import {QuizMocks} from '@models/mocks/quiz.mocks';
import {Icons} from '@models/constants/icons';
import {Store} from '@ngrx/store';
import {UploadImage} from '@shared/actions/actions';
import {TranslateService} from '@ngx-translate/core';

@Component({
    selector: 'app-questions-form',
    templateUrl: './questions-form.component.html',
    styleUrls: ['./questions-form.component.scss']
})
export class QuestionsFormComponent extends AbstractForm implements OnInit {
    public questionRadio: any[] = [];
    public answers: FormArray;
    public icons = Icons;

    constructor(protected formsService: FormsService,
                private fb: FormBuilder,
                private store: Store<any>,
                protected translateService: TranslateService) {
        super(formsService, translateService);
    }

    ngOnInit() {
        this.createQuestionsForm();
        this.getRadioButtons();
    }

    createQuestionsForm() {
        if (this.object) {
            this.form = this.fb.group({
                question: this.fb.group({
                    kk: [this.object.kk, [Validators.required]],
                    ru: [this.object.ru, [Validators.required]],
                    imagePath: [this.object.questionImagePath || '']
                }),
                right: [this.object.right, [Validators.required]],
                random: [this.object.random || false],
                active: [this.object.active || false],
                answers: this.fb.array(this.getAnswersArray()),
            });
            this.answers = this.form.get('answers') as FormArray;
        }
    }

    addAnswerField(index: number) {
        this.questionRadio.push({value: index});
        this.answers.push(this.createAnswers());
    }

    removeAnswerField(index: number) {
        this.questionRadio.splice(-1, 1);
        this.answers.removeAt(index);
    }

    getAnswersArray() {
        if (this.object.hasOwnProperty('answers') && this.object.answers.length) {
            return this.object.answers.map((answer) => {
                return this.fb.group({
                    kk: [answer.kk, [Validators.required]],
                    ru: [answer.ru, [Validators.required]],
                    imagePath: [answer.imagePath || '']
                });
            });
        }

        return [this.createAnswers(), this.createAnswers()];
    }

    createAnswers() {
        return this.fb.group({
            kk: ['', [Validators.required]],
            ru: ['', [Validators.required]],
            imagePath: ['']
        });
    }

    uploadImage(file: File, group: string, id?: number) {
        const n = file.name.indexOf('.');
        const imagePath = file.name.substring(0, n !== -1 ? n : file.name.length);
        if (group === 'question') {
            this.formControls.question['controls'].imagePath.setValue(imagePath);
        } else {
            const control = this.answers.at(id);
            control['controls'].imagePath.setValue(imagePath);
        }
        this.store.dispatch(new UploadImage(file));
    }

    deleteImage(imagePath: string, group: string, id?: number) {
        if (group === 'question') {
            this.formControls.question['controls'].imagePath.setValue('');
        } else {
            const control = this.answers.at(id);
            control['controls'].imagePath.setValue('');
        }
    }


    getRadioButtons() {
        if (this.object.right > 2) {
            for (let i = 1; i <= this.object.right; i++) {
                this.questionRadio.push({value: i});
            }
            return;
        }
        this.questionRadio = QuizMocks.questionRadio.slice(0);
    }

    getFieldLabel(index: number): string {
        switch (index) {
            case 1:
                return 'secondAnswer';
            case 2:
                return 'thirdAnswer';
            case 3:
                return 'fourthAnswer';
            default:
                return 'firstAnswer';
        }
    }

    trackByFn(index: any) {
        return index;
    }

    send(object) {
        this.submitted = true;
        if (this.form.invalid) {
            return;
        }
        this.save.next(object);
    }

}
