import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';
import {Store} from '@ngrx/store';

import {QuizService} from '@quiz/services/quiz.service';
import {IQuestion, ITest, IAnswers} from '@models/interfaces/quiz';
import {MessageService} from '@services/message.service';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {
    selectCategories,
    selectConditions,
    selectStandards,
    selectSubjectTests,
    selectTestQuestions,
    selectUnits
} from '@quiz/selectors/selectors';
import {questionMapperFromServer, quizMapperFromServer, answerMapperFromServer} from '@mappers/quiz.mapper';
import {ICondition, IConditionUnits, IStandard, IStandardCategory} from '@models/interfaces/standard';
import * as fromQuiz from '@quiz/actions/actions';

@Injectable()
export class QuizEffects {
    constructor(private actions$: Actions,
                private quizService: QuizService,
                private messageService: MessageService,
                private store: Store<any>) {
    }

    @Effect()
    loadSubjects$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadSubjects),
            mergeMap(() => this.quizService.getSubjects()
                .pipe(
                    map((subjects: any[]) => new fromQuiz.LoadedSubjects(subjects)),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadAll$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadAll),
            mergeMap(() => this.quizService.getSubjects()
                .pipe(
                    map((subjects: any[]) => new fromQuiz.LoadedSubjects(subjects)),
                    mergeMap((action: any) => {
                        return this.quizService.getTestsBySubject(action.payload[0].id).pipe(
                            map(tests => new fromQuiz.RefreshTests(tests)),
                            mergeMap((a: any) => {
                                return this.quizService.getQuestionList(a.payload[0].id).pipe(
                                    map(questions =>
                                        new fromQuiz.RefreshQuestions(questions))
                                );
                            })
                        );
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadTestsBySubject$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadTestsBySubject),
            mergeMap((action: any) => this.quizService.getTestsBySubject(action.payload)
                .pipe(
                    map((tests: ITest[]) => new fromQuiz.LoadedTestsBySubject(tests)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshTestsBySubject = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.RefreshTests),
            map((action: any) => new fromQuiz.LoadedTestsBySubject(action.payload))
        );


    @Effect()
    createTestForSubject$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.CreateTestForSubject),
            withLatestFrom(this.store.select(selectSubjectTests)),
            switchMap(([action, tests]: Array<any>) => {
                    return this.quizService.createTest(action.payload)
                        .pipe(
                            map((response) => {
                                const message = {user: ToastrUsersType.TEST, type: ToastrMessageType.CREATE};
                                if (response) {
                                    const mappedResponse = quizMapperFromServer(response);
                                    tests.push(mappedResponse);
                                    this.messageService.showToastrSuccess(message);
                                }

                                return new fromQuiz.RefreshTests(tests);
                            }),
                            catchError((err) => {
                                this.messageService.showPushNotification(err);
                                this.messageService.showToastrError(err);
                                return of(new fromQuiz.QuizErrors(err));
                            })
                        );
                }
            )
        );

    @Effect()
    updateTestById$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.UpdateTest),
            withLatestFrom(this.store.select(selectSubjectTests)),
            switchMap(([action, tests]: any[]) => {
                const {testId, test} = action.payload;
                return this.quizService.updateTest(testId, test)
                    .pipe(
                        map((item: any) => {
                            const message = {user: ToastrUsersType.TEST, type: ToastrMessageType.UPDATE};
                            if (item) {
                                tests = tests.map((el: ITest) => el.id === item.id ? item : el);
                                this.messageService.showToastrSuccess(message);
                            }

                            return new fromQuiz.RefreshTests(tests);
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromQuiz.QuizErrors(err));
                        })
                    );
            })
        );

    @Effect()
    deleteTestFromSubject$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.DeleteTestFromSubject),
            withLatestFrom(this.store.select(selectSubjectTests)),
            switchMap(([action, tests]: Array<any>) => {
                    const {testId} = action.payload;
                    return this.quizService.deleteTest(testId)
                        .pipe(
                            map(() => {
                                tests = tests.filter((test: ITest) => test.id !== testId);
                                const message = {user: ToastrUsersType.TEST, type: ToastrMessageType.REMOVE};
                                this.messageService.showToastrSuccess(message);
                                return new fromQuiz.RefreshTests(tests);
                            }),
                            catchError((err) => {
                                this.messageService.showPushNotification(err);
                                this.messageService.showToastrError(err);
                                return of(new fromQuiz.QuizErrors(err));
                            })
                        );
                }
            )
        );

    @Effect()
    getQuestionsByTest$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.GetQuestionsByTest),
            mergeMap((action: any) => this.quizService.getQuestionList(action.payload)
                .pipe(
                    map((questions: IQuestion[]) => new fromQuiz.SetQuestionsByTest(questions)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )),
        );

    @Effect()
    refreshQuestions$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.RefreshQuestions),
            map((action: any) => new fromQuiz.SetQuestionsByTest(action.payload))
        );

    @Effect()
    loadQuestionById$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadQuestionById),
            mergeMap((action: any) => {
                const {testId, questionId} = action.payload;
                return this.quizService.getQuestionById(testId, questionId)
                    .pipe(
                        map((question: object) => new fromQuiz.LoadedQuestionById(question)),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromQuiz.QuizErrors(err));
                        })
                    );
            })
        );

    @Effect()
    createQuestionForTest$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.CreateQuestionForTest),
            withLatestFrom(this.store.select(selectTestQuestions)),
            switchMap(([action, questions]: any[]) => {
                const {testId, question} = action.payload;
                return this.quizService.createQuestion(question, testId)
                    .pipe(
                        map((item: IQuestion) => {
                            const message = {user: ToastrUsersType.QUESTION, type: ToastrMessageType.CREATE};
                            if (item) {
                                const answers = item.answers.map((answer: IAnswers) => answerMapperFromServer(answer));
                                const mappedItem = questionMapperFromServer(item, answers);
                                questions.push(mappedItem);
                                this.messageService.showToastrSuccess(message);
                                return new fromQuiz.RefreshQuestions(questions);
                            }
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromQuiz.QuizErrors(err));
                        })
                    );
            })
        );

    @Effect()
    deleteQuestionFromTest$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.DeleteQuestionFromTest),
            withLatestFrom(this.store.select(selectTestQuestions)),
            switchMap(([action, questions]: any[]) => {
                const {testId, questionId} = action.payload;
                return this.quizService.deleteQuestion(questionId, testId)
                    .pipe(
                        map(() => {
                            const message = {user: ToastrUsersType.QUESTION, type: ToastrMessageType.REMOVE};
                            questions = questions.filter((question: IQuestion) => question.id !== questionId);
                            this.messageService.showToastrSuccess(message);
                            return new fromQuiz.RefreshQuestions(questions);
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromQuiz.QuizErrors(err));
                        })
                    );
            })
        );

    @Effect()
    updateQuestionById = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.UpdateQuestion),
            withLatestFrom(this.store.select(selectTestQuestions)),
            switchMap(([action, questions]: any[]) => {
                const {testId, questionId, question} = action.payload;
                return this.quizService.updateQuestion(testId, questionId, question)
                    .pipe(
                        map((item: IQuestion) => {
                            const message = {user: ToastrUsersType.QUESTION, type: ToastrMessageType.UPDATE};
                            this.messageService.showToastrSuccess(message);
                            questions = questions.map((el: IQuestion) => el.id === questionId ? item : el);
                            return new fromQuiz.RefreshQuestions(questions);
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromQuiz.QuizErrors(err));
                        })
                    );
            })
        );

    @Effect()
    loadStandardsCategories$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadCategories),
            mergeMap(() => this.quizService.getStandardsCategories()
                .pipe(
                    map((categories: IStandardCategory[]) => new fromQuiz.LoadedCategories(categories)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshStandardsCategories$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.RefreshCategories),
            map((action: any) => new fromQuiz.LoadedCategories(action.payload)),
            catchError((err) => of(new fromQuiz.QuizErrors(err)))
        );

    @Effect()
    createCategory$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.CreateCategory),
            withLatestFrom(this.store.select(selectCategories)),
            switchMap(([action, categories]: any[]) => this.quizService.createStandardsCategory(action.payload)
                .pipe(
                    map((category: IStandardCategory) => {
                        const message = {user: ToastrUsersType.CATEGORY, type: ToastrMessageType.CREATE};
                        if (category) {
                            this.messageService.showToastrSuccess(message);
                            categories.push(category);
                        }

                        return new fromQuiz.RefreshCategories(categories);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    updateCategory$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.UpdateCategory),
            withLatestFrom(this.store.select(selectCategories)),
            switchMap(([action, categories]: any[]) => this.quizService.updateCategory(action.payload)
                .pipe(
                    map((category: IStandardCategory) => {
                        const message = {user: ToastrUsersType.CATEGORY, type: ToastrMessageType.UPDATE};
                        if (category) {
                            this.messageService.showToastrSuccess(message);
                            categories = categories.map((item: IStandardCategory) =>
                                item.id === category.id ? category : item);
                        }

                        return new fromQuiz.RefreshCategories(categories);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteCategory$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.DeleteCategory),
            withLatestFrom(this.store.select(selectCategories)),
            switchMap(([action, categories]: any[]) => this.quizService.deleteCategory(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.CATEGORY, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        categories = categories.filter((item: IStandardCategory) => item.id !== action.payload);
                        return new fromQuiz.RefreshCategories(categories);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadConditions$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadConditions),
            mergeMap(() => this.quizService.getConditions()
                .pipe(
                    map((conditions: ICondition[]) => new fromQuiz.LoadedConditions(conditions)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshConditions$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.RefreshConditions),
            map((action: any) => new fromQuiz.LoadedConditions(action.payload)),
            catchError((err) => of(new fromQuiz.QuizErrors(err)))
        );

    @Effect()
    createConditions$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.CreateCondition),
            withLatestFrom(this.store.select(selectConditions)),
            switchMap(([action, conditions]: any) => this.quizService.createCondition(action.payload)
                .pipe(
                    map((condition) => {
                        const message = {user: ToastrUsersType.CONDITION, type: ToastrMessageType.CREATE};
                        if (condition) {
                            this.messageService.showToastrSuccess(message);
                            conditions.push(condition);
                        }

                        return new fromQuiz.RefreshConditions(conditions);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    updateConditions$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.UpdateCondition),
            withLatestFrom(this.store.select(selectConditions)),
            switchMap(([action, conditions]: any) => this.quizService.updateCondition(action.payload)
                .pipe(
                    map((condition) => {
                        const message = {user: ToastrUsersType.CONDITION, type: ToastrMessageType.UPDATE};
                        if (condition) {
                            this.messageService.showToastrSuccess(message);
                            conditions = conditions.map((item: ICondition) => item.id === condition.id ? condition : item);
                        }

                        return new fromQuiz.RefreshConditions(conditions);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteConditions$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.DeleteCondition),
            withLatestFrom(this.store.select(selectConditions)),
            switchMap(([action, conditions]: any) => this.quizService.deleteCondition(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.CONDITION, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        conditions = conditions.filter((item: ICondition) => item.id !== action.payload);

                        return new fromQuiz.RefreshConditions(conditions);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadUnits$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadUnits),
            mergeMap(() => this.quizService.getUnits()
                .pipe(
                    map((units: IConditionUnits[]) => new fromQuiz.LoadedUnits(units)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshUnits$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.RefreshUnits),
            map((action: any) => new fromQuiz.LoadedUnits(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromQuiz.QuizErrors(err));
            })
        );

    @Effect()
    createUnit$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.CreateUnit),
            withLatestFrom(this.store.select(selectUnits)),
            switchMap(([action, units]: any) => this.quizService.createUnit(action.payload)
                .pipe(
                    map((unit: IConditionUnits) => {
                        const message = {user: ToastrUsersType.UNIT, type: ToastrMessageType.CREATE};
                        if (unit) {
                            this.messageService.showToastrSuccess(message);
                            units.push(unit);
                        }

                        return new fromQuiz.RefreshConditions(units);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteUnit$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.DeleteUnit),
            withLatestFrom(this.store.select(selectUnits)),
            switchMap(([action, units]: any) => this.quizService.deleteUnit(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.UNIT, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        units = units.filter((item: IConditionUnits) => item.id !== action.payload);

                        return new fromQuiz.RefreshUnits(units);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadStandardsBySubject$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.LoadStandards),
            mergeMap((action: any) => this.quizService.getStandardsBySubject(action.payload)
                .pipe(
                    map((standards: IStandard[]) => new fromQuiz.LoadedStandards(standards)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshSubjects$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.RefreshStandards),
            map((action: any) => new fromQuiz.LoadedStandards(action.payload)),
            catchError((err) => of(new fromQuiz.QuizErrors(err)))
        );

    @Effect()
    createStandard$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.CreateStandards),
            withLatestFrom(this.store.select(selectStandards)),
            switchMap(([action, standards]: any[]) => this.quizService.createStandard(action.payload)
                .pipe(
                    map((standard: IStandard) => {
                        const message = {user: ToastrUsersType.STANDARD, type: ToastrMessageType.CREATE};
                        if (standard) {
                            this.messageService.showToastrSuccess(message);
                            standards.push(standard);
                        }

                        return new fromQuiz.RefreshStandards(standards);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    updateStandard$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.UpdateStandards),
            withLatestFrom(this.store.select(selectStandards)),
            switchMap(([action, standards]: any[]) => this.quizService.updateStandard(action.payload)
                .pipe(
                    map((standard: IStandard) => {
                        const message = {user: ToastrUsersType.STANDARD, type: ToastrMessageType.UPDATE};
                        if (standard) {
                            this.messageService.showToastrSuccess(message);
                            standards = standards.map((item: IStandard) => item.id === standard.id ? standard : item);
                        }

                        return new fromQuiz.RefreshStandards(standards);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteStandard$ = this.actions$
        .pipe(
            ofType(fromQuiz.ActionTypes.DeleteStandards),
            withLatestFrom(this.store.select(selectStandards)),
            switchMap(([action, standards]: any[]) => this.quizService.deleteStandard(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.STANDARD, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        standards = standards.filter((item: IStandard) => item.id !== action.payload);

                        return new fromQuiz.RefreshStandards(standards);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromQuiz.QuizErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadCurrentStandard$ = this.actions$.pipe(
        ofType(fromQuiz.ActionTypes.LoadCurrentStandard),
        map((action: any) => new fromQuiz.LoadedCurrentStandard(action.payload))
    );
}
