import {Injectable} from '@angular/core';
import {ToastrMessageType, ToastrTitle} from '@models/constants/toastr';
import {ToastrService} from 'ngx-toastr';
import {HttpErrorResponse} from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})
export class MessageService {
    public message: string;

    constructor(private toastrService: ToastrService) {
    }

    getSuccessMessage(userType: string, messageType: string): string {
        switch (messageType) {
            case ToastrMessageType.CREATE:
                this.message = `${userType} was created successfully`;
                return this.message;
            case ToastrMessageType.UPDATE:
                this.message = `${userType} was updated successfully`;
                return this.message;
            case ToastrMessageType.REMOVE:
                this.message = `${userType} was removed successfully`;
                return this.message;
            default:
                return;
        }
    }

    showToastrSuccess(message: { user: string, type: string }) {
        this.toastrService.success(
            this.getToastrMessage(message.user, message.type),
            ToastrTitle.SUCCESS
        );
    }

    showToastrError(err: HttpErrorResponse) {
        const {error} = err.error;
        for (const key in error) {
            if (error.hasOwnProperty(key)) {
                return this.toastrService.error(this.getToastrErrorMessage(error[key]), ToastrTitle.ERROR);
            }
        }
    }

    getToastrMessage(userType: string, messageType: string): string {
        return this.getSuccessMessage(userType, messageType);
    }

    getToastrErrorMessage(text: string): string {
        return text;
    }

    showPushNotification(text: string) {
    }
}
