import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';

const NO_CONNECTION = 'No Connection!';

@Injectable({
    providedIn: 'root'
})
export class ConnectionService {

    public readonly NO_CONNECTION = NO_CONNECTION;
    public status: BehaviorSubject<string>;
    private readonly connectionType: string;

    constructor() {
        this.connectionType = 'Unknown';
        this.status = new BehaviorSubject(this.connectionType);
    }
}
