import {LOCALE_ID, NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {HTTP_INTERCEPTORS, HttpClient, HttpClientModule} from '@angular/common/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {StoreModule} from '@ngrx/store';
import {EffectsModule} from '@ngrx/effects';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {ToastrModule} from 'ngx-toastr';
import {NgHttpLoaderModule} from 'ng-http-loader';
import {StoreDevtoolsModule} from '@ngrx/store-devtools';
import {registerLocaleData} from '@angular/common';
import localeRus from '@angular/common/locales/ru';
import {IMqttServiceOptions, MqttModule} from 'ngx-mqtt';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {LoginGuard} from '@guards/login.guard';
import {AuthGuard} from '@guards/auth.guard';
import {AuthInterceptor} from '@interceptors/auth.interceptor';
import {SubjectService} from '@services/subject.service';
import {LoginModule} from '@login/login.module';
import {DashboardModule} from '@dashboard/dashboard.module';
import {ExerciseModule} from '@exercises/exercise.module';
import {TournamentsModule} from '@tournaments/tournaments.module';
import {JudgesModule} from '@judges/judges.module';
import {CompetitorsModule} from '@competitors/competitors.module';
import {SharedModule} from '@shared/shared.module';
import {PersonsModule} from '@persons/persons.module';
import {StagesModule} from '@stages/stages.module';
import {CompetitorsRatingModule} from './common/modules/competitors-rating/competitors-rating.module';
import {DivisionsModule} from '@divisions/divisions.module';
import {metaReducers, reducers} from './reducers';
import {PersonEffects} from '@persons/effects/effects';
import {environment} from '@environments/environment';
import {StagesEffects} from '@stages/effects/effects';
import {TournamentsEffects} from '@tournaments/effects/effects';
import {CompetitorsEffects} from '@competitors/effects/effects';
import {DivisionsEffects} from '@divisions/effects/effects';
import {JudgesEffects} from '@judges/effects/judges-effects.service';
import {SharedEffects} from '@shared/effects/effects';
import {QuizModule} from '@quiz/quiz.module';
import {QuizEffects} from '@quiz/effects/effects';
import {CheckinModule} from '@checkin/checkin.module';
import {CheckinEffects} from '@checkin/effects/effects';
import {AnswersModule} from './common/modules/answers/answers.module';
import {CombatListModule} from '@combat-list/combat-list.module';
import {CombatListService} from '@combat-list/services/combat-list.service';
import {CombatListEffects} from '@combat-list/effects/effects';
import {SpecialitiesModule} from '@specialities/specialities.module';
import {SpecialitiesEffects} from '@specialities/effects/effects';
import {UserModule} from '@user/user.module';
import {UserEffects} from '@user/effects/effects';
import {WeaponModule} from '@weapon/weapon.module';
import {WeaponsEffects} from '@weapon/effects/effects';
import {CourseModule} from '@course/course.module';
import {CourseEffects} from '@course/effects/effects';
import {WorkspaceEffects} from '@workspace/effects/effects';

registerLocaleData(localeRus, 'ru');

export const MQTT_SERVICE_OPTIONS: IMqttServiceOptions = {
    hostname: environment.mqttHost,
    port: environment.mqttPort,
    path: '/mqtt',
    username: environment.mqttUserName,
    password: environment.mqttPassword
};
@NgModule({
    declarations: [
        AppComponent,
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        AppRoutingModule,
        HttpClientModule,
        LoginModule,
        DashboardModule,
        ExerciseModule,
        TournamentsModule,
        JudgesModule,
        SharedModule,
        PersonsModule,
        StagesModule,
        CompetitorsModule,
        CompetitorsRatingModule,
        DivisionsModule,
        QuizModule,
        CheckinModule,
        AnswersModule,
        CombatListModule,
        SpecialitiesModule,
        UserModule,
        WeaponModule,
        CourseModule,
        ToastrModule.forRoot({
            timeOut: 10000,
            positionClass: 'toast-top-center',
            preventDuplicates: true,
        }),
        NgHttpLoaderModule.forRoot(),
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: HttpLoaderFactory,
                deps: [HttpClient]
            }
        }),
        StoreModule.forRoot(reducers, {metaReducers}),
        EffectsModule.forRoot([
            PersonEffects,
            StagesEffects,
            TournamentsEffects,
            CompetitorsEffects,
            DivisionsEffects,
            JudgesEffects,
            SharedEffects,
            QuizEffects,
            CheckinEffects,
            CombatListEffects,
            SpecialitiesEffects,
            UserEffects,
            WeaponsEffects,
            CourseEffects,
            WorkspaceEffects
        ]),
        StoreDevtoolsModule.instrument({
            maxAge: 25,
            logOnly: environment.production,
        }),
        MqttModule.forRoot(MQTT_SERVICE_OPTIONS)
    ],
    providers: [
        {
            provide: HTTP_INTERCEPTORS,
            useClass: AuthInterceptor,
            multi: true
        },
        {
            provide: LOCALE_ID,
            useValue: 'ru-KZ'
        },
        LoginGuard,
        AuthGuard,
        SubjectService,
        CombatListService
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}

// required for AOT compilation
export function HttpLoaderFactory(http: HttpClient) {
    return new TranslateHttpLoader(http, '/assets/i18n/', '.json');
}
