import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {LoginService} from '../../services/login.service';
import {Router} from '@angular/router';
import {TokenService} from '@services/token.service';
import {ICreadentials} from '@models/interfaces/credentials';
import {FormsService} from '@forms/services/forms.service';
import {MessageService} from '@services/message.service';
import {Animations} from '@models/constants/animations';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {Store} from '@ngrx/store';
import {selectValidations} from '@shared/selectors/selectors';
import {LoadValidations} from '@shared/actions/actions';
import {DialogsService} from '@shared/services/dialogs.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class LoginComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public credentials: ICreadentials = {};
    public submitted = false;
    public message: string;
    public controls: string[] = [];
    public form: FormGroup;
    public validations: any;
    public log: any;

    constructor(private loginService: LoginService,
                private tokenService: TokenService,
                private router: Router,
                private formsService: FormsService,
                private messageService: MessageService,
                private store: Store<any>,
                private fb: FormBuilder,
                private dialogService: DialogsService) {
        super();
    }

    ngOnInit() {
        this.getValidation();
    }

    getValidation() {
        this.subscriptions.sink = this.store.select(selectValidations)
            .subscribe((validations) => {
                if (validations) {
                    this.validations = validations;
                    this.createForm();
                    this.detectFormChanges();
                } else {
                    this.fetchValidations();
                }
            });
    }

    fetchValidations() {
        this.store.dispatch(new LoadValidations());
    }

    createForm() {
        this.form = this.fb.group({
            login: ['', this.setValidation('login')],
            password: ['', this.setValidation('password')]
        });
        this.controls = this.createArrayOfControls(this.form);
    }

    setValidation(control: string) {
        return this.formsService.generateValidationListByBean(this.validations.UserLoginBean, control);
    }

    detectFormChanges() {
        this.subscriptions.sink = this.form.valueChanges.subscribe(object => this.credentials = object);
    }

    createArrayOfControls(group: FormGroup): string[] {
        return this.formsService.createArrayOfControls(group);
    }

    login() {
        this.submitted = true;
        if (this.form.invalid) {
            return;
        }
        this.loginService.login(this.credentials).subscribe(res => {
            this.tokenService.setToken(res.token);
            this.goToDashboard();
        }, err => {
            this.goToDashboard();
            this.messageService.showToastrError(err);
            this.messageService.showPushNotification(err);
        });
    }

    loginAsGuest() {
        this.loginService.login({login: 'guest', password: 'guest'}).subscribe(res => {
            this.dialogService.hideLoader();
            this.tokenService.setToken(res.token);
            this.goToDashboard();
        }, err => {
            this.dialogService.hideLoader();
            this.goToDashboard();
            this.messageService.showToastrError(err);
            this.messageService.showPushNotification(err);
        });
    }

    get formControls() {
        return this.form.controls;
    }

    goToDashboard() {
        this.router.navigate(['/dashboard']);
    }

    clearMessage() {
        setTimeout(() => this.message = '', 5000);
    }
}
