import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {of} from 'rxjs';
import {catchError, map, mergeMap} from 'rxjs/operators';
import {Location} from '@angular/common';

import * as fromCompetitors from '@competitors/actions/actions';
import * as fromTournaments from '@tournaments/actions/actions';
import {CompetitorsService} from '@competitors/services/competitors.service';
import {MessageService} from '@services/message.service';
import {ICompetitor} from '@models/interfaces/human';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {LocalStorageService} from '@services/local-storage.service';
import {IExercise} from '@models/interfaces/exercise';
import {DialogsService} from '@shared/services/dialogs.service';

@Injectable()
export class CompetitorsEffects {

    @Effect()
    loadCompetitors$ = this.actions$
        .pipe(
            ofType(fromCompetitors.ActionTypes.LoadCompetitors),
            mergeMap((action: any) => this.competitorsService.getCompetitors(action.payload)
                .pipe(
                    map((competitors: ICompetitor[]) => new fromCompetitors.LoadedCompetitors(competitors)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCompetitors.ErrorsCompetitors(err));
                    })
                ))
        );

    @Effect()
    createListOfCompetitors = this.actions$
        .pipe(
            ofType(fromCompetitors.ActionTypes.CreateListOfCompetitors),
            mergeMap((action: any) => {
                const {competitorsIds, tournament} = action.payload;
                return this.competitorsService.createListOfCompetitors(competitorsIds, tournament.id)
                    .pipe(
                        map((competitors: ICompetitor[]) => {
                            const message = {user: ToastrUsersType.COMPETITORS, type: ToastrMessageType.CREATE};
                            if (competitors) {
                                this.messageService.showToastrSuccess(message);
                            }
                            tournament.humanAmount = competitors.length;
                            tournament.competitors = competitors;
                            this.location.back();
                            return new fromTournaments.ForceUpdateTournament(tournament);
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromCompetitors.ErrorsCompetitors(err));
                        })
                    );
            })
        );

    @Effect()
    updateCompetitorByJudge$ = this.actions$
        .pipe(
            ofType(fromCompetitors.ActionTypes.UpdateCompetitorByJudge),
            mergeMap((action: any) => {
                const {tournament, competitorId, competitorUpdates, fromStorage} = action.payload;
                return this.competitorsService[fromStorage ? 'updateCompetitorWithBothFields' : 'updateCompetitorByJudge']
                (tournament.id, competitorId, competitorUpdates)
                    .pipe(
                        map((competitor: ICompetitor) => {
                            if (fromStorage) {
                                this.localStorageService.removeCompetitor(tournament.id, competitorId);
                            }
                            return new fromTournaments.ForceUpdateTournamentCompetitors({tournament, competitor: competitorUpdates});
                        }), catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromCompetitors.ErrorsCompetitors(err));
                        })
                    );
            })
        );

    @Effect()
    getCompetitorsRatingByStage$ = this.actions$
        .pipe(
            ofType(fromCompetitors.ActionTypes.LoadCompetitorsRatingByStage),
            mergeMap((action: any) => {
                const {tournamentId, stageId} = action.payload;
                return this.competitorsService.getCompetitorsRatingByStage(tournamentId, stageId)
                    .pipe(
                        map((rating: IExercise[]) => new fromCompetitors.LoadedCompetitorsRatingByStage(rating)),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromCompetitors.ErrorsCompetitors(err));
                        })
                    );
            })
        );

    @Effect()
    getCompetitorsRating$ = this.actions$
        .pipe(
            ofType(fromCompetitors.ActionTypes.LoadCompetitorsRating),
            mergeMap((action: any) => this.competitorsService.getCompetitorsRating(action.payload)
                .pipe(
                    map((rating: IExercise[]) => new fromCompetitors.LoadedCompetitorsRatingByStage(rating)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCompetitors.ErrorsCompetitors(err));
                    })
                )
            )
        );

    constructor(private actions$: Actions,
                private competitorsService: CompetitorsService,
                private messageService: MessageService,
                private localStorageService: LocalStorageService,
                private location: Location) {
    }
}
