import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

import {IPerson} from '@models/interfaces/human';
import {compare, SortEvent} from '@models/interfaces/table';
import {Actions} from '@models/constants/actions';
import {Animations} from '@models/constants/animations';
import {FieldsMocks} from '@models/mocks/fields.mocks';
import {TableAbstract} from '../../../../adstracts/table.abstract';
import {TranslateService} from '@ngx-translate/core';

@Component({
    selector: 'app-base-table',
    templateUrl: './base-table.component.html',
    styleUrls: ['./base-table.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class BaseTableComponent extends TableAbstract implements OnInit {
    public existsActions = Actions;
    public buttons = Actions;


    @Input() defaultPageSize: number;
    @Input() page = 1;
    @Input() objects: any[];
    @Input() actions: string[];
    @Input() paginationLength: number;
    @Input() isCompetitors;
    @Input() searchValue: string;
    @Input() divisionValue: string;
    @Input() statusName: string;

    @Output() action = new EventEmitter<{ action: string, item: any }>();
    @Output() pages: EventEmitter<{}> = new EventEmitter<{ pageNumber: number, pageSize: number }>();
    @Output() competitorValue: EventEmitter<IPerson> = new EventEmitter();

    public keysLength: number;
    public columnSizes: string;
    public mappedObject: any;

    constructor(protected translateService: TranslateService) {
        super(translateService);
    }

    ngOnInit() {
        this.detectLanguage();
        setTimeout(() => {
            if (this.objects && this.objects.length) {
                this.mappedObject = this.getMappedObject(this.objects[0]);
                this.keysLength = this.getKeyListLength(this.mappedObject);
                this.columnSizes = this.getColumnSizes();
            }
        }, 1000);
    }

    get searchableObjectByName() {
        if (this.isCompetitors) {
            if (this.searchValue) {
                return this.objects.filter(item => item.userName.toLowerCase().includes(this.searchValue));
            } else if (this.divisionValue) {
                return this.objects.filter(item => item.division && item.division.name === this.divisionValue);
            } else {
                return this.objects;
            }
        }
    }

    getMappedObject(input): any {
        const object = {};
        for (const key in input) {
            if (input.hasOwnProperty(key)) {
                const k: any = key;
                if (this.displayedCells.includes(k)) {
                    object[key] = input[key];
                }
            }
        }
        return object;
    }

    getColumnSizes(): string {
        const defaultSize = 120;
        const sizeArray: any[] = [];
        for (let i = 0; i < this.keysLength; i++) {
            sizeArray.push('*');
        }
        if (this.actions && this.actions.length) {
            for (let i = 0; i < this.actions.length; i++) {
                sizeArray.push(defaultSize);
            }
        }
        return sizeArray.join(',');
    }

    getKeyListLength(object): number {
        return Object.keys(object).length;
    }

    onClickAction(action: string, item: any) {
        this.action.next({action, item});
    }

    loadPage() {
        this.pages.emit({pageNumber: this.page, pageSize: this.defaultPageSize});
    }


    onSort({column, direction}: SortEvent) {
        this.headers.forEach(header => {
            if (header.sortable !== column) {
                header.direction = '';
            }
        });
        if (direction === '') {
            return;
        } else {
            this.objects = [...this.objects].sort((a, b) => {
                const res = compare(a[column], b[column]);
                this.direction = direction;
                return direction === 'asc' ? res : -res;
            });
        }
    }

    onSwitcherValueChange(person: IPerson) {
        person.member = !person.member;
        this.competitorValue.next(person);
    }

    switcherCase(field) {
        return FieldsMocks.switcherFields.includes(field);
    }

    getIcons(action: string) {
        switch (action) {
            case Actions.CHANGE:
                return this.icons.edit;
            case Actions.REMOVE:
                return this.icons.delete;
            case Actions.COMPETITORS:
                return this.icons.competitors;
            case Actions.RATING:
                return this.icons.trophy;
            case Actions.STAGES:
                return this.icons.stages;
            case Actions.CHANGE_PASSWORD:
                return this.icons.changePassword;
            case Actions.TESTS:
                return this.icons.tests;
            case Actions.QUESTIONS:
                return this.icons.questions;
            case Actions.DETAILS:
                return this.icons.details;
            case Actions.STANDARDS:
                return this.icons.standards;
            case Actions.CATEGORIES:
                return this.icons.categories;
            case Actions.ERRORS:
                return this.icons.errors;
            case Actions.CONDITIONS:
                return this.icons.conditions;
            case Actions.UNITS:
                return this.icons.units;
            case Actions.ADD_SHOOTS:
                return this.icons.add;
            case Actions.TIE:
                return this.icons.tie;
            case Actions.UNTIE:
                return this.icons.untie;
        }
    }

    // Don't remove this method.
    // It needs for mobile.
    notGeneralCells(key) {
        switch (key) {
            case this.fields.ACTIVE:
            case this.fields.BIRTH_DATE:
            case this.fields.SAFE_TECHNIQUE:
            case this.fields.RANK:
            case this.fields.DATE:
            case this.fields.GROUPS:
            case this.fields.NOT_COUNTED:
            case this.fields.WEAPON_NAME:
                return false;
            default:
                return true;
        }
    }

    getClass(action: string) {
        switch (action) {
            case Actions.REMOVE:
                return 'btn-danger';
            case Actions.CHANGE_PASSWORD:
                return 'btn-warning';
            default:
                return 'btn-primary';
        }
    }
}
