import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap} from 'rxjs/operators';
import {of} from 'rxjs';

import * as fromDivisions from '@divisions/actions/actions';
import {DivisionsService} from '@divisions/services/divisions.service';
import {MessageService} from '@services/message.service';
import {IDivision} from '@models/interfaces/division';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';

@Injectable()
export class DivisionsEffects {

    @Effect()
    loadListOfDivisions = this.actions$
        .pipe(
            ofType(fromDivisions.ActionTypes.LoadDivisions),
            mergeMap(() => this.divisionsService.getDivisions()
                .pipe(
                    switchMap((divisions: IDivision[]) => [
                        new fromDivisions.LoadedDivisions(divisions),
                        new fromDivisions.LoadDivisionNames(divisions)
                    ]),
                    catchError((err: any) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromDivisions.ErrorsDivisions(err));
                    })
                ))
        );

    @Effect()
    loadDivisionNames$ = this.actions$
        .pipe(
            ofType(fromDivisions.ActionTypes.LoadDivisionNames),
            map((action: any) => {
                const divisionNames = action.payload.map((division: IDivision) => division.name);
                return new fromDivisions.LoadedDivisionNames(divisionNames);
            })
        );

    @Effect()
    loadRootDivision$ = this.actions$
        .pipe(
            ofType(fromDivisions.ActionTypes.LoadRootDivision),
            mergeMap(() => this.divisionsService.getRoot()
                .pipe(
                    switchMap((root: IDivision) => [
                        new fromDivisions.LoadedRootDivision(root),
                        new fromDivisions.LoadDivisions()
                    ]),
                    catchError((err: any) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromDivisions.ErrorsRootDivisions(err));
                    })
                ))
        );

    @Effect()
    createDivision$ = this.actions$
        .pipe(
            ofType(fromDivisions.ActionTypes.CreateDivision),
            mergeMap((action: any) => this.divisionsService.create(action.payload)
                .pipe(
                    map((division: IDivision) => {
                        const message = {user: ToastrUsersType.DIVISION, type: ToastrMessageType.CREATE};
                        if (division) {
                            this.messageService.showToastrSuccess(message);
                        }
                        return new fromDivisions.LoadRootDivision();
                    }),
                    catchError((err: any) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromDivisions.ErrorsRootDivisions(err));
                    })
                )
            )
        );

    @Effect()
    removeDivision$ = this.actions$
        .pipe(
            ofType(fromDivisions.ActionTypes.RemoveDivision),
            mergeMap((action: any) => this.divisionsService.remove(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.DIVISION, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        return new fromDivisions.LoadRootDivision();
                    }),
                    catchError((err: any) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromDivisions.ErrorsRootDivisions(err));
                    })
                )
            )
        );

    @Effect()
    updateDivision$ = this.actions$
        .pipe(
            ofType(fromDivisions.ActionTypes.UpdateDivision),
            mergeMap((action: any) => {
                const {division, id} = action.payload;
                return this.divisionsService.update(division, id)
                    .pipe(
                        map((data: IDivision) => {
                            const message = {user: ToastrUsersType.DIVISION, type: ToastrMessageType.UPDATE};
                            if (data) {
                                this.messageService.showToastrSuccess(message);
                            }
                            return new fromDivisions.LoadRootDivision();
                        }),
                        catchError((err: any) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromDivisions.ErrorsRootDivisions(err));
                        })
                    );
            })
        );

    constructor(private actions$: Actions,
                private divisionsService: DivisionsService,
                private messageService: MessageService) {
    }
}

