import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromCombatList from '@combat-list/reducers/reducer';

export const selectCombatList = createFeatureSelector<fromCombatList.State>('combatList');

export const selectCombatListData = createSelector(
    selectCombatList,
    (state: fromCombatList.State) => state.combatList
);

export const selectCombatListErrors = createSelector(
    selectCombatList,
    (state: fromCombatList.State) => state.errors
);
