import {Component, Inject, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {DOCUMENT} from '@angular/common';
import {IMqttMessage, MqttService} from 'ngx-mqtt';

import {LoginService} from '@login/services/login.service';
import {Animations} from '@models/constants/animations';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss'],
    animations: [Animations.enterLeaveOpacity],
})
export class DashboardComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public role: string;
    public elem: any;
    constructor(private router: Router,
                private loginService: LoginService,
                private _mqttService: MqttService,
                @Inject(DOCUMENT) private document: any) {
        super();
    }

    ngOnInit() {
        this.role = localStorage.getItem('role');

    }

    goTo(pageName: string) {
        if (pageName === 'persons') {
            this.router.navigate([`/${pageName}`], {queryParams: {page: 1, size: 10}});
            return;
        }
        this.router.navigate([`/${pageName}`]);
    }

    logOut() {
        this.loginService.logOut();
    }

}
