import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {Store} from '@ngrx/store';
import {ActivatedRoute} from '@angular/router';
import {Observable} from 'rxjs';
import * as moment from 'moment';

import {AbstractForm} from '@forms/abstract-form';
import {FormsService} from '@forms/services/forms.service';
import {ICondition, IStandardCategory, IStandardCategoryWithTime, IStandardFails} from '@models/interfaces/standard';
import {selectCategories, selectConditions} from '@quiz/selectors/selectors';
import {Icons} from '@models/constants/icons';
import {Animations} from '@models/constants/animations';
import {Actions} from '@models/constants/actions';
import {PageTypes} from '@models/constants/page-types';
import {FormTypes} from '@models/constants/form-types';
import {ISubject} from '@models/interfaces/quiz';

@Component({
    selector: 'app-standards-form',
    templateUrl: './standards-form.component.html',
    styleUrls: ['./standards-form.component.scss'],
    animations: [Animations.showHide]
})
export class StandardsFormComponent extends AbstractForm implements OnInit {

    public categoriesWithTime: IStandardCategoryWithTime[] = [];
    public selectedConditions: ICondition[] = [];
    public failsList: IStandardFails[] = [];
    public subject: ISubject;
    public showCategoryForm = false;
    public showConditionsForm = false;
    public showFailsForm = false;
    public isCategoryCreated = false;
    public conditionsCreated = false;
    public isFailsCreated = false;
    public isUniqueCategory = false;
    public isUniqueCondition = false;
    public isUniqueFails = false;
    public isInvalid: boolean;
    public icons = Icons;
    public categoryForm: FormGroup;
    public conditionsForm: FormGroup;
    public failsForm: FormGroup;
    public actions: string[];
    public pageTypes = PageTypes;

    private categories$: Observable<IStandardCategory[]>;
    private conditions$: Observable<ICondition[]>;
    private subjectId: string;

    constructor(protected formsService: FormsService,
                protected translateService: TranslateService,
                private fb: FormBuilder,
                private store: Store<any>,
                private route: ActivatedRoute) {
        super(formsService, translateService);
    }

    ngOnInit() {
        this.actions = [Actions.REMOVE];
        this.getRouteParams();
        this.getCategories();
        this.getConditions();
        this.createForm();
        this.categoriesWithTime = this.object.categoriesList || [];
        this.selectedConditions = this.object.conditionsList || [];
        this.failsList = this.object.failsList || [];

    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams
            .subscribe((params) => this.subjectId = params['subject']);
    }

    getCategories() {
        this.categories$ = this.store.select(selectCategories);
    }

    getConditions() {
        this.conditions$ = this.store.select(selectConditions);
    }

    createForm() {
        if (this.object) {
            this.form = this.fb.group({
                info: this.fb.group({
                    namedKz: [this.object.namedKz, [Validators.required]],
                    namedRus: [this.object.namedKz, [Validators.required]],
                    descriptionKz: [this.object.descriptionKz, [Validators.required]],
                    descriptionRus: [this.object.descriptionRus, [Validators.required]],
                }),
                groups: [this.object.groups, [Validators.required]],
                active: [this.object.active, [Validators.required]],
            });
        }
    }

    createCategoryForm() {
        this.categoryForm = this.fb.group({
            category: [null, [Validators.required]],
            time: ['00:00:00', [Validators.required]]
        });
    }

    createConditionsForm() {
        this.conditionsForm = this.fb.group({
            conditions: [null, [Validators.required]]
        });
    }

    createFailsForm() {
        this.failsForm = this.fb.group({
            nameFailsRus: ['', [Validators.required]],
            nameFailsKz: ['', [Validators.required]],
            minusPoint: [0, [Validators.required]],
        });
    }

    setFormArrays(formType: string) {
        let formValue;
        switch (formType) {
            case FormTypes.CATEGORY:
                this.isCategoryCreated = true;
                if (this.categoryForm.invalid) {
                    return;
                }

                formValue = this.categoryForm.value;
                this.isUniqueCategory = this.checkUniqueState(this.categoriesWithTime, formValue.category, FormTypes.CATEGORY);
                if (this.isUniqueCategory) {
                    this.resetForm(this.categoryForm, FormTypes.CATEGORY);
                    return;
                }

                this.categoriesWithTime.push(formValue);
                this.resetForm(this.categoryForm, FormTypes.CATEGORY);
                return;
            case FormTypes.CONDITIONS:
                this.conditionsCreated = true;
                if (this.conditionsForm.invalid) {
                    return;
                }
                formValue = this.conditionsForm.value;
                this.isUniqueCondition = this.checkUniqueState(this.selectedConditions, formValue.conditions, FormTypes.CONDITIONS);
                if (this.isUniqueCondition) {
                    this.resetForm(this.conditionsForm, FormTypes.CONDITIONS);
                    return;
                }

                this.selectedConditions.push(formValue.conditions);
                this.resetForm(this.conditionsForm, FormTypes.CONDITIONS);
                return;
            case FormTypes.FAILS:
                this.isFailsCreated = true;
                if (this.failsForm.invalid) {
                    return;
                }
                formValue = this.failsForm.value;
                this.isUniqueFails = this.checkUniqueState(this.failsList, formValue, FormTypes.FAILS);
                if (this.isUniqueFails) {
                    this.resetForm(this.failsForm, FormTypes.FAILS);
                    return;
                }
                this.failsList.push(formValue);
                this.resetForm(this.failsForm, FormTypes.FAILS);
                return;
        }
    }

    checkUniqueState(array: any[], value: any, formType: string) {
        return array.some((item: any) => {
            switch (formType) {
                case FormTypes.CATEGORY:
                    return item.category.id === value.id;
                case FormTypes.CONDITIONS:
                    return item.id === value.id;
                default:
                    return item.nameFailsRus === value.nameFailsRus;
            }
        });
    }

    resetForm(form: FormGroup, formType: string) {
        form.reset();
        switch (formType) {
            case FormTypes.CATEGORY:
                this.isCategoryCreated = !this.isCategoryCreated;
                return;
            case FormTypes.CONDITIONS:
                this.conditionsCreated = !this.conditionsCreated;
                return;
            case FormTypes.FAILS:
                this.isFailsCreated = !this.isFailsCreated;
                return;
        }
    }

    resetAlert(formType: string) {
        switch (formType) {
            case FormTypes.CATEGORY:
                this.isUniqueCategory = !this.isUniqueCategory;
                return;
            case FormTypes.CONDITIONS:
                this.isUniqueCondition = !this.isUniqueCondition;
                return;
            default:
                this.isUniqueFails = !this.isUniqueFails;
                return;
        }
    }

    onSelectedItem(value: any, control: string, form: FormGroup) {
        form.controls[control].setValue(value);
    }

    alertMessage(formType: string) {
        switch (formType) {
            case FormTypes.CATEGORY:
                return 'categoryAlreadyAdded';
            case FormTypes.CONDITIONS:
                return 'conditionAlreadyAdded';
            default:
                return 'failAlreadyAdded';
        }
    }

    onShowForm(formType: string) {
        switch (formType) {
            case FormTypes.CATEGORY:
                this.showCategoryForm = !this.showCategoryForm;
                if (this.showCategoryForm) {
                    this.createCategoryForm();
                }
                return;
            case FormTypes.CONDITIONS:
                this.showConditionsForm = !this.showConditionsForm;
                if (this.showConditionsForm) {
                    this.createConditionsForm();
                }
                return;
            case FormTypes.FAILS:
                this.showFailsForm = !this.showFailsForm;
                if (this.showFailsForm) {
                    this.createFailsForm();
                }
                return;
        }
    }

    mapStandardsForm() {
        return {
            active: this.form.controls['active'].value,
            groups: this.form.controls['groups'].value,
            namedRus: this.form.controls['info']['controls'].namedRus.value,
            namedKz: this.form.controls['info']['controls'].namedKz.value,
            descriptionRus: this.form.controls['info']['controls'].descriptionRus.value,
            descriptionKz: this.form.controls['info']['controls'].descriptionKz.value,
            categoriesList: this.mapCategories(),
            conditionsList: this.mapConditions(),
            failsList: this.failsList,
            subject: this.subjectId
        };
    }

    mapCategories() {
        return this.categoriesWithTime.map((item: IStandardCategoryWithTime) => this.getCategoryId(item));
    }

    getCategoryId(category: IStandardCategoryWithTime) {
        return {
            category: category.category.id,
            time: moment(category.time, 'HH:mm:ss: A').diff(moment().startOf('day'), 'seconds')
        };
    }

    mapConditions() {
        return this.selectedConditions.map((condition: ICondition) => {
            return {
                conditionsRus: condition.conditionsRus,
                conditionsKz: condition.conditionsKz,
                coefficient: condition.coefficient,
                units: condition.units.id
            };
        });
    }

    handleAction(event: { action: string, item: any }, formType: string) {
        switch (formType) {
            case FormTypes.CATEGORY:
                if (event.action === Actions.REMOVE) {
                    this.categoriesWithTime = this.filterSettings(this.categoriesWithTime, event.item);
                }
                return;
            case FormTypes.CONDITIONS:
                if (event.action === Actions.REMOVE) {
                    this.selectedConditions = this.filterSettings(this.selectedConditions, event.item);
                }
                return;

            case FormTypes.FAILS:
                if (event.action === Actions.REMOVE) {
                    this.failsList = this.filterSettings(this.failsList, event.item);
                }
                return;
        }
    }

    filterSettings(array: any, item: any) {
        return array.filter((el) => el !== item);
    }

    send() {
        this.submitted = true;
        if (this.form.invalid || !this.categoriesWithTime.length) {
            this.isInvalid = true;
            return;
        }
        this.isInvalid = false;
        const standard = this.mapStandardsForm();
        this.save.next(standard);
    }

}
