import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {TranslateService} from '@ngx-translate/core';

import {AbstractForm} from '@forms/abstract-form';
import {FormsService} from '@forms/services/forms.service';
import {IJudge, IPerson} from '@models/interfaces/human';
import {IStage} from '@models/interfaces/stage';
import {IDivision} from '@models/interfaces/division';
import {selectClassifiers, selectLevels, selectRanks, selectValidations, selectWeaponTypes} from '@shared/selectors/selectors';
import {selectDivisionsData} from '@divisions/selectors/selectors';
import {selectJudgesData} from '@judges/selectors/judjes.selector';
import {IRank} from '@models/interfaces/ranks';
import {IConditionUnits} from '@models/interfaces/standard';
import {selectUnits} from '@quiz/selectors/selectors';
import {selectPersonsByDivision} from '@persons/selectors/selectors';
import {UploadImage} from '@shared/actions/actions';
import {selectWeaponTypesData} from '@weapon/selectors/selectors';
import {IWeaponTypes} from '@models/interfaces/weapons';
import {DialogsService} from '@shared/services/dialogs.service';
import {SubSink} from 'subsink';
import {PageTypes} from '@models/constants/page-types';
import {SharedService} from '@shared/services/shared.service';


@Component({
    selector: 'app-base-form',
    templateUrl: './base-form.component.html',
    styleUrls: ['./base-form.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class BaseFormComponent extends AbstractForm implements OnInit {

    public weaponTypes = [];
    public divisions: IDivision[] = [];
    public judges: IJudge[];
    public classifier: IStage[];
    public levels: any;
    public levelNames: string[];
    public ranks: IRank[] = [];
    public units: IConditionUnits[] = [];
    public pageTypes = PageTypes;

    private dialogSubs: SubSink = new SubSink();

    constructor(protected formsService: FormsService,
                protected translateService: TranslateService,
                private store: Store<any>,
                private dialogsService: DialogsService,
                private sharedService: SharedService
    ) {
        super(formsService, translateService);
    }

    ngOnInit() {
        this.getValidations();
        this.createForm();
        this.createArrayOfSubscriptions();
        this.loadJudgesData();
        this.loadClassifiers();
        this.loadWeaponTypes();
        this.getDivisionsList();
        this.loadLevels();
        this.getGridRowsByControls();
        this.getRanks();
        this.getUnits();
        this.getPersonsByDivision();
        this.getWeaponTypesForList();
    }

    loadJudgesData() {
        const fields: string[] = [this.fields.MAIN_JUDGE, this.fields.STATISTICS_JUDGE, this.fields.DIRECTOR];
        this.controls.forEach((control) => {
            if (fields.includes(control)) {
                this.subscriptions.sink = this.store.pipe(select(selectJudgesData))
                    .subscribe((judges: IJudge[]) => {
                        this.judges = judges;
                    });
            }
        });
    }

    loadClassifiers() {
        const field = this.fields.ISC_CLASSIFIER;
        this.controls.forEach((control: string) => {
            if (control === field) {
                this.subscriptions.sink = this.store.pipe(select(selectClassifiers))
                    .subscribe((data: IStage[]) => this.classifier = data);
            }
        });
    }

    loadLevels() {
        this.controls.forEach((control: string) => {
            if (control === this.fields.LEVEL) {
                this.subscriptions.sink = this.store.pipe(select(selectLevels))
                    .subscribe((levels: any) => {
                        this.levels = levels;
                        this.levelNames = this.getLevelNames(this.levels);
                    });
            }
        });
    }

    loadWeaponTypes() {
        this.controls.forEach((control: string) => {
            if (control === this.fields.WEAPON_TYPE) {
                this.subscriptions.sink = this.store.pipe(select(selectWeaponTypes))
                    .subscribe((types: any) => {
                        this.weaponTypes = types;
                        this.translatedWeaponTypes = this.sharedService.translateArray(types);
                    });
            }
        });
    }

    getDivisionsList() {
        this.controls.forEach((control: string) => {
            if (control === this.fields.DIVISION) {
                this.subscriptions.sink = this.store.pipe(select(selectDivisionsData))
                    .subscribe((divisions: IDivision[]) => this.divisions = divisions);
            }
        });
    }

    getRanks() {
        this.controls.forEach((control: string) => {
            if (control === this.fields.RANK) {
                this.subscriptions.sink = this.store.pipe(select(selectRanks))
                    .subscribe((ranks: IRank[]) => this.ranks = ranks);
            }
        });
    }

    getUnits() {
        this.subscriptions.sink = this.store.select(selectUnits)
            .subscribe((units: IConditionUnits[]) => this.units = units);
    }

    getValidations() {
        this.subscriptions.sink = this.store.pipe(select(selectValidations))
            .subscribe((validations: any) => this.validations = validations);
    }

    getWeaponTypesForList() {
        this.subscriptions.sink = this.store.select(selectWeaponTypesData)
            .subscribe((weaponTypes: IWeaponTypes[]) => this.weaponTypesWeaponList = weaponTypes);
    }

    getPersonsByDivision() {
        this.subscriptions.sink = this.store.select(selectPersonsByDivision)
            .subscribe((persons: IPerson[]) => this.personsByDivision = persons);
    }

    createArrayOfSubscriptions() {
        this.subscriptions.sink = this.detectFormValueChanges();
    }

    initDialog(control) {
        const options = this.getDialogOptions();
        this.dialogSubs.sink = this.dialogsService.getDialogAction(options).subscribe((result) => {
            if (result !== '') {
                this.selectedItem = result;
                this.formControls[control].setValue(this.selectedItem);
                this.dialogSubs.unsubscribe();
            }
        });
    }

    getDialogOptions() {
        return this.dialogsService.getDialogOptions('  ', this.mobileSelectArray);
    }

    onLoadImage(file: File) {
        const n = file.name.indexOf('.');
        const imagePath = file.name.substring(0, n !== -1 ? n : file.name.length);
        this.formControls['imagePath'].setValue(imagePath);
        this.store.dispatch(new UploadImage(file));
    }

    onDeleteImage() {
        this.formControls['imagePath'].setValue('');
    }
}
