import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromCompetitors from '@competitors/reducers/reducer';

export const selectCompetitors = createFeatureSelector<fromCompetitors.State>('competitors');

export const selectCompetitorsData = createSelector(
    selectCompetitors,
    (state: fromCompetitors.State) => state.competitors
);

export const selectCompetitorsRating = createSelector(
    selectCompetitors,
    (state: fromCompetitors.State) => state.rating
);

export const selectCompetitorsErrors = createSelector(
    selectCompetitors,
    (state: fromCompetitors.State) => state.errors
);
