import {Action} from '@ngrx/store';
import {ISpecialities} from '@models/interfaces/specialities';

export enum ActionTypes {
    LoadSpecialities = '[Specialities Page] Load Specialities',
    LoadedSpecialities = '[Specialities Page] Loaded Specialities',
    SpecialitiesErrors = '[Specialities Page] Specialities Errors',
    RefreshSpecialities = '[Specialities Page] Refresh Specialities',
    CreateSpeciality = '[Specialities Page] Create Speciality',
    UpdateSpeciality = '[Specialities Page] Update Speciality',
    RemoveSpeciality = '[Specialities Page] Remove Speciality'
}

export class LoadSpecialities implements Action {
    readonly type = ActionTypes.LoadSpecialities;
}

export class LoadedSpecialities implements Action {
    readonly type = ActionTypes.LoadedSpecialities;

    constructor(public payload: ISpecialities[]) {
    }
}

export class SpecialitiesErrors implements Action {
    readonly type = ActionTypes.SpecialitiesErrors;

    constructor(public payload: any) {
    }
}

export class RefreshSpecialities implements Action {
    readonly type = ActionTypes.RefreshSpecialities;

    constructor(public payload: ISpecialities[]) {
    }
}

export class CreateSpeciality implements Action {
    readonly type = ActionTypes.CreateSpeciality;

    constructor(public payload: ISpecialities) {
    }
}

export class UpdateSpeciality implements Action {
    readonly type = ActionTypes.UpdateSpeciality;

    constructor(public payload: ISpecialities) {
    }
}

export class RemoveSpeciality implements Action {
    readonly type = ActionTypes.RemoveSpeciality;

    constructor(public payload: string) {
    }
}


export type SpecialitiesActions = LoadSpecialities &
    LoadedSpecialities & SpecialitiesErrors & CreateSpeciality &
    RefreshSpecialities & UpdateSpeciality & RemoveSpeciality;
