import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs';

import {environment} from '@environments/environment';
import {ISpecialities} from '@models/interfaces/specialities';

@Injectable({
    providedIn: 'root'
})
export class SpecialitiesService {
    private specialityBaseUrl: string = environment.BASE_URL + '/api/speciality/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getSpecialities(): Observable<ISpecialities[]> {
        return this.http.get(this.specialityBaseUrl + '/all')
            .pipe(map((response: ISpecialities[]) => response));
    }

    createSpeciality(speciality: ISpecialities): Observable<ISpecialities> {
        return this.http.post(this.specialityBaseUrl + '/speciality', speciality)
            .pipe(map((response: ISpecialities) => response));
    }

    updateSpeciality(speciality: ISpecialities): Observable<ISpecialities> {
        return this.http.put(this.specialityBaseUrl + `/speciality/${speciality.id}`, speciality)
            .pipe(map((response: ISpecialities) => response));
    }

    removeSpeciality(specialityId: string): Observable<any> {
        return this.http.delete(this.specialityBaseUrl + `/speciality/${specialityId}`)
            .pipe(map((response: ISpecialities) => response));
    }
}
