import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {environment} from '@environments/environment';
import {ICheckin, ICheckinParams} from '@models/interfaces/checkin';
import {IPerson} from '@models/interfaces/human';
import {personMapperFromServer} from '@mappers/person.mapper';
import {checkinDetailsMapperFromServer} from '@mappers/checkin.mapper';

@Injectable({
    providedIn: 'root'
})
export class CheckinService {
    private checkinBaseUrl: string = environment.BASE_URL + '/api/checkin/' + environment.version;

    constructor(private http: HttpClient) {
    }

    searchCheckin(params: ICheckinParams): Observable<ICheckin[]> {
        return this.http.get(this.checkinBaseUrl + `/search/${params.division}/${params.status}/${params.interval}/${params.date}`)
            .pipe(map((response: ICheckin[]) => response.map((item) => checkinDetailsMapperFromServer(item))));
    }

    getDivisionCheckinByInterval(params: ICheckinParams): Observable<any> {
        return this.http.get(this.checkinBaseUrl + `/combatenote/${params.division}/${params.interval}/${params.date}`)
            .pipe(map((response: any[]) => response));
    }

    // Don't remove this method.
    // It needs for mobile
    getPersonsCheckinByDivision(divisionId: string): Observable<IPerson[]> {
        return this.http.get(this.checkinBaseUrl + `/check/division/${divisionId}`)
            .pipe(map((response: IPerson[]) => response.map((item: any) => personMapperFromServer(item))));
    }
}
