import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';
import {Store} from '@ngrx/store';

import * as fromPerson from '@persons/actions/actions';
import {LoadedPersonsCount} from '@persons/actions/actions';
import {PersonService} from '@persons/service/person.service';
import {IPerson} from '@models/interfaces/human';
import {MessageService} from '@services/message.service';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {selectPersonData} from '@persons/selectors/selectors';

@Injectable()
export class PersonEffects {

    constructor(
        private actions$: Actions,
        private personService: PersonService,
        private messageService: MessageService,
        private store: Store<any>
    ) {
    }

    @Effect()
    loadPersons$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.LoadPersons),
            mergeMap(() => this.personService.getPersons()
                .pipe(
                    map((persons: IPerson[]) => new fromPerson.LoadedPersons(persons)),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromPerson.ErrorLoadedPersons(err));
                    })
                ))
        );

    @Effect()
    loadPersonsPerPage$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.LoadPersonsPerPage),
            mergeMap((action: any) => {
                const {pageNumber, pageSize} = action.payload;
                this.personService.personNavigate({pageNumber, pageSize});
                return this.personService.getPersonsPerPage(pageNumber, pageSize)
                    .pipe(
                        switchMap((persons: IPerson[]) => [
                            new fromPerson.LoadedPersonsPerPage(persons),
                        ]),
                        catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromPerson.ErrorLoadedPersons(err));
                        })
                    );
            })
        );

    @Effect()
    loadPersonsPerPageByDivision$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.LoadPersonsByDivisionPerPage),
            mergeMap((action: any) => {
                const {pageNumber, pageSize} = action.payload.page;
                const {divisionId} = action.payload;
                this.personService.personNavigate({pageNumber, pageSize});
                return this.personService.getPersonsPerPageByDivision(pageNumber, pageSize, divisionId)
                    .pipe(
                        switchMap((persons: IPerson[]) => [
                            new fromPerson.LoadedPersonsPerPage(persons),
                        ]),
                        catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromPerson.ErrorLoadedPersons(err));
                        })
                    );
            })
        );

    @Effect()
    createPerson$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.CreatePerson),
            mergeMap((action: any) => {
                const {person} = action.payload;
                return this.personService.createPerson(person)
                    .pipe(
                        switchMap((createdPerson: IPerson) => {
                            const message = {user: ToastrUsersType.PERSON, type: ToastrMessageType.CREATE};
                            if (createdPerson) {
                                this.messageService.showToastrSuccess(message);
                            }
                            return [new fromPerson.LoadPersonsCount()];
                        }),
                        catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromPerson.ErrorLoadedPersons(err));
                        }));
            })
        );

    @Effect()
    deletePerson$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.DeletePerson),
            mergeMap((action: any) => {
                const {page, person} = action.payload;
                return this.personService.deletePerson(person.id)
                    .pipe(
                        switchMap((data: IPerson) => {
                            const message = {user: ToastrUsersType.PERSON, type: ToastrMessageType.REMOVE};
                            if (data) {
                                this.messageService.showToastrSuccess(message);
                            }
                            return [new fromPerson.LoadPersonsPerPage(page), new fromPerson.LoadPersonsCount()];
                        }),
                        catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromPerson.ErrorLoadedPersons(err));
                        }));
            })
        );

    @Effect()
    updatePerson$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.UpdatePerson),
            mergeMap((action: any) => {
                const {person} = action.payload;
                return this.personService.updatePerson(person, person.id)
                    .pipe(
                        switchMap((data: IPerson) => {
                            const message = {user: ToastrUsersType.PERSON, type: ToastrMessageType.UPDATE};
                            if (data) {
                                this.messageService.showToastrSuccess(message);
                            }
                            return [new fromPerson.LoadPersonsCount()];
                        }),
                        catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromPerson.ErrorLoadedPersons(err));
                        }));
            }),
        );

    @Effect()
    getPersonsCount$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.LoadPersonsCount),
            mergeMap(() => this.personService.getPersonsCount()
                .pipe(
                    map((personsCount: number) => new LoadedPersonsCount(Number(personsCount))),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromPerson.ErrorLoadedPersons(err));
                    })
                ))
        );

    @Effect()
    loadPersonsByDivision$ = this.actions$
        .pipe(
            ofType(fromPerson.ActionTypes.LoadPersonsByDivision),
            withLatestFrom(this.store.select(selectPersonData)),
            map(([action, persons]: any[]) => {
                const personsByDivision = persons.filter((person: IPerson) => {
                    if (person.division && person.division.id === action.payload) {
                        return person;
                    }
                });
                return new fromPerson.LoadedPersonsByDivision(personsByDivision);
            }),
            catchError((err) => {
                this.messageService.showToastrError(err);
                this.messageService.showPushNotification(err);
                return of(new fromPerson.ErrorLoadedPersons(err));
            })
        );
}
