import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromDivisions from '@divisions/reducers/reducer';

export const selectDivisions = createFeatureSelector<fromDivisions.State>('divisions');

export const selectDivisionsData = createSelector(
    selectDivisions,
    (state: fromDivisions.State) => state.divisions
);

export const selectRootDvisionData = createSelector(
    selectDivisions,
    (state: fromDivisions.State) => state.rootDivision
);

export const selectDivisionsErrors = createSelector(
    selectDivisions,
    (state: fromDivisions.State) => state.divisionsErrors
);

export const selectRootDivisionsErrors = createSelector(
    selectDivisions,
    (state: fromDivisions.State) => state.rootDivisionsErrors
);

export const selectDivisionNames = createSelector(
    selectDivisions,
    (state: fromDivisions.State) => state.divisionNames
);
