import {ActionTypes, CombatListActions} from '@combat-list/actions/actions';

export interface State {
    combatList: any;
    errors?: any;
}

export const initialState: State = {
    combatList: null,
    errors: null
};

export function reducer(state: State = initialState, action: CombatListActions) {
    switch (action.type) {
        case ActionTypes.LoadedCurrentCombatList:
            return {...state, combatList: action.payload};
        case ActionTypes.CombatListErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
