import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {environment} from '@environments/environment';
import {IQuestion, ISubject, ITest, IAnswers} from '@models/interfaces/quiz';
import {
    answerMapperFromServer, answerMapperToServer, conditionsMapper,
    questionMapperFromServer,
    questionMapperToServer,
    quizMapperFromServer,
    quizMapperToServer, standardMapperFromServer, standardMapperToServer,
    subjectMapperFromServer
} from '@mappers/quiz.mapper';
import {ICondition, IConditionUnits, IStandard, IStandardCategory} from '@models/interfaces/standard';

@Injectable({
    providedIn: 'root'
})
export class QuizService {
    private quizBaseUrl: string = environment.BASE_URL + '/api/quiz/' + environment.version;
    private categoryBaseUrl: string = environment.BASE_URL + '/api/category/' + environment.version;
    private conditionsBaseUrl: string = environment.BASE_URL + '/api/common/condition/' + environment.version;
    private unitsBaseUrl: string = environment.BASE_URL + '/api/name/' + environment.version;
    private standardsBaseUrl: string = environment.BASE_URL + '/api/standard/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getSubjects(): Observable<ISubject[]> {
        return this.http.get(this.quizBaseUrl + '/enum/subjects')
            .pipe(map((response: ISubject[]) =>
                response.map((item: ISubject) => subjectMapperFromServer(item))));
    }

    getTestsBySubject(subject: string): Observable<ITest[]> {
        return this.http.get(this.quizBaseUrl + `/quiz/subject/${subject}`)
            .pipe(map((response: any[]) => response.map((item) => quizMapperFromServer(item))));
    }

    createTest(test: ITest): Observable<ITest> {
        const mappedData = quizMapperToServer(test);
        return this.http.post(this.quizBaseUrl + '/quiz/create', mappedData)
            .pipe(map((response: any) => response));
    }

    updateTest(testId: string, test: ITest): Observable<ITest> {
        const mappedData = quizMapperToServer(test);
        return this.http.put(this.quizBaseUrl + `/quiz/${testId}`, mappedData)
            .pipe(map((response: any) => quizMapperFromServer(response)));
    }

    deleteTest(testId: string): Observable<any> {
        return this.http.delete(this.quizBaseUrl + `/quiz/${testId}`)
            .pipe(map((response: any) => response));
    }

    getQuestionList(testId: string): Observable<IQuestion[]> {
        return this.http.get(this.quizBaseUrl + `/quiz/${testId}/list`)
            .pipe(map((response: any) => {
                return response.map((item: any) => {
                    const answers = item.answers.map((answer: IAnswers) => answerMapperFromServer(answer));
                    return questionMapperFromServer(item, answers);
                });
            }));
    }

    getQuestionById(testId: string, questionId: string): Observable<IQuestion> {
        return this.http.get(this.quizBaseUrl + `/quiz/${testId}/question/${questionId}`)
            .pipe(map((response: any) => {
                const answers = response.answers.map((answer: IAnswers) => answerMapperFromServer(answer));
                return questionMapperFromServer(response, answers);
            }));
    }

    createQuestion(question: IQuestion, testId: string): Observable<IQuestion> {
        const answers = question.answers.map((answer: IAnswers) => answerMapperToServer(answer));
        const mappedData = questionMapperToServer(question, answers);
        return this.http.post(this.quizBaseUrl + `/quiz/${testId}/question`, mappedData)
            .pipe(map((response: IQuestion) => response));
    }

    updateQuestion(testId: string, questionId: string, question: any): Observable<any> {
        const mappedAnswers = question.answers.map((answer: IAnswers) => answerMapperToServer(answer));
        const mappedData = questionMapperToServer(question, mappedAnswers);
        return this.http.put(this.quizBaseUrl + `/quiz/${testId}/question/${questionId}`, mappedData)
            .pipe(map((response: any) => {
                const answers = response.answers.map((answer: IAnswers) => answerMapperFromServer(answer));
                return questionMapperFromServer(response, answers);
            }));
    }

    deleteQuestion(questionId: string, testId: string): Observable<any> {
        return this.http.delete(this.quizBaseUrl + `/quiz/${testId}/question/${questionId}`)
            .pipe(map((response: any) => response));
    }

    getStandardsCategories(): Observable<IStandardCategory[]> {
        return this.http.get(this.categoryBaseUrl + '/get/all/categories')
            .pipe(map((response: IStandardCategory[]) => response));
    }

    createStandardsCategory(category: IStandardCategory): Observable<IStandardCategory> {
        return this.http.post(this.categoryBaseUrl + '/post/category', category)
            .pipe(map((response: IStandardCategory) => response));
    }

    updateCategory(category: IStandardCategory): Observable<IStandardCategory> {
        return this.http.put(this.categoryBaseUrl + `/put/category/${category.id}`, category)
            .pipe(map((response: IStandardCategory) => response));
    }

    deleteCategory(categoryId: string): Observable<any> {
        return this.http.delete(this.categoryBaseUrl + `/delete/category/${categoryId}`)
            .pipe(map((response) => response));
    }

    getConditions(): Observable<ICondition[]> {
        return this.http.get(this.conditionsBaseUrl + '/get/all')
            .pipe(map((response: ICondition[]) => response));
    }

    createCondition(condition: ICondition): Observable<ICondition> {
        const body = conditionsMapper(condition);
        return this.http.post(this.conditionsBaseUrl + '/post/common/condition', body)
            .pipe(map((response: ICondition) => response));
    }

    updateCondition(condition: ICondition): Observable<ICondition> {
        const body = conditionsMapper(condition);
        return this.http.put(this.conditionsBaseUrl + `/put/common/condition/${condition.id}`, body)
            .pipe(map((response: ICondition) => response));
    }

    deleteCondition(conditionId: string): Observable<any> {
        return this.http.delete(this.conditionsBaseUrl + `/delete/${conditionId}`)
            .pipe(map((response) => response));
    }

    getUnits(): Observable<IConditionUnits[]> {
        return this.http.get(this.unitsBaseUrl + '/get/name')
            .pipe(map((response: IConditionUnits[]) => response));
    }

    createUnit(unit: IConditionUnits): Observable<IConditionUnits> {
        return this.http.post(this.unitsBaseUrl + '/post/unit', unit)
            .pipe(map((response: IConditionUnits) => response));
    }

    deleteUnit(unitId: string): Observable<any> {
        return this.http.delete(this.unitsBaseUrl + `/delete/unit/${unitId}`)
            .pipe(map((response) => response));
    }

    getStandardsBySubject(subjectId: string): Observable<IStandard[]> {
        return this.http.get(this.standardsBaseUrl + `/get/standards/${subjectId}`)
            .pipe(map((response: IStandard[]) => response.map((item) => standardMapperFromServer(item))));
    }

    createStandard(standard: IStandard): Observable<IStandard> {
        const mappedData = standardMapperToServer(standard);
        return this.http.post(this.standardsBaseUrl + '/post/standard', mappedData)
            .pipe(map((response: IStandard) => standardMapperFromServer(response)));
    }

    updateStandard(standard: IStandard): Observable<IStandard> {
        const mappedData = standardMapperToServer(standard);
        return this.http.put(this.standardsBaseUrl + `/put/standard/${standard.id}`, mappedData)
            .pipe(map((response: IStandard) => standardMapperFromServer(response)));
    }

    deleteStandard(standardId): Observable<any> {
        return this.http.delete(this.standardsBaseUrl + `/delete/standard/${standardId}`)
            .pipe(map((response) => response));
    }
}
