import {Component, OnDestroy, OnInit} from '@angular/core';
import {IQuestion, ISubject, ITest} from '@models/interfaces/quiz';
import {Subscription} from 'rxjs';
import {TranslateService} from '@ngx-translate/core';
import {QuizService} from '@quiz/services/quiz.service';
import {ModalService} from '@modal/services/modal.service';

@Component({
    selector: 'app-answers',
    templateUrl: './answers.component.html',
    styleUrls: ['./answers.component.scss']
})
export class AnswersComponent implements OnInit, OnDestroy {
    public subject: ISubject;
    public test: ITest;
    public question: IQuestion;

    private subscriptions: Subscription[] = [];
    private currentLang: string;

    constructor(private translateService: TranslateService,
                private quizService: QuizService,
                private modalService: ModalService) {
    }

    ngOnInit() {
        this.currentLang = localStorage.getItem('lang');
        this.subscriptions.push(this.translateService.onLangChange.subscribe((res) => {
            this.currentLang = res.lang;
        }));
        //
        this.quizService.getSubjects().subscribe(subjects => {
            this.subject = subjects[0];
            this.quizService.getTestsBySubject(this.subject.id).subscribe(tests => {
                this.test = tests[0];
                this.quizService.getQuestionList(this.test.id).subscribe(questions => {
                    this.question = questions[0];
                });
            });
        });
        // this.modalService.openModal()
    }

    ngOnDestroy(): void {
        this.subscriptions.forEach((s: Subscription) => s.unsubscribe());
    }

}
