import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';
import {Store} from '@ngrx/store';

import * as fromUser from '@user/actions/actions';
import {LoadedUserCount} from '@user/actions/actions';
import {IUser} from '@models/interfaces/human';
import {MessageService} from '@services/message.service';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {UserService} from '@user/services/user.service';
import {PersonService} from '@persons/service/person.service';
import {JudgesService} from '@judges/service/judges.service';
import {selectUsersData} from '@user/selectors/selectors';

@Injectable()
export class UserEffects {

    constructor(
        private actions$: Actions,
        private userService: UserService,
        private messageService: MessageService,
        private personService: PersonService,
        private judgeService: JudgesService,
        private store: Store<any>
    ) {
    }

    @Effect()
    loadUsers$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.LoadUsers),
            mergeMap(() => this.userService.getUsers()
                .pipe(
                    map((users: IUser[]) => new fromUser.LoadedUsers(users)),
                    catchError((err) => of(new fromUser.UserErrors(err)))
                ))
        );

    @Effect()
    refreshUsers$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.RefreshUsers),
            map((action: any) => new fromUser.LoadedUsers(action.payload)),
            catchError((err) => of(new fromUser.UserErrors(err)))
        );

    @Effect()
    loadUsersPerPage$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.LoadUsersPerPage),
            mergeMap((action: any) => {
                const {pageNumber, pageSize} = action.payload;
                this.personService.personNavigate({pageNumber, pageSize});
                return this.userService.getUsersPerPage(pageNumber, pageSize)
                    .pipe(
                        switchMap((users: IUser[]) => [
                            new fromUser.LoadedUsersPerPage(users),
                        ]),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromUser.UserErrors(err));
                        })
                    );
            })
        );

    @Effect()
    createUser$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.CreateUser),
            withLatestFrom(this.store.select(selectUsersData)),
            switchMap(([action, users]: any[]) => this.userService.createUser(action.payload)
                .pipe(
                    map((createdUser: IUser) => {
                        const message = {user: ToastrUsersType.USER, type: ToastrMessageType.CREATE};
                        if (createdUser) {
                            this.messageService.showToastrSuccess(message);
                            users.push(createdUser);
                        }
                        return new fromUser.RefreshUsers(users);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromUser.UserErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteUser$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.DeleteUser),
            withLatestFrom(this.store.select(selectUsersData)),
            switchMap(([action, users]: any[]) => this.judgeService.deleteJudge(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.USER, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        users = users.filter((user: IUser) => user.id !== action.payload);
                        return new fromUser.RefreshUsers(users);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromUser.UserErrors(err));
                    })
                )),
        );

    @Effect()
    updateUser$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.UpdateUser),
            withLatestFrom(this.store.select(selectUsersData)),
            switchMap(([action, users]: any[]) => {
                return this.judgeService.updateJudge(action.payload, action.payload.id)
                    .pipe(
                        map((data: IUser) => {
                            const message = {user: ToastrUsersType.USER, type: ToastrMessageType.UPDATE};
                            if (data) {
                                this.messageService.showToastrSuccess(message);
                                users = users.map((user: IUser) =>
                                    user.id === action.payload.id ? action.payload : user);
                            }
                            return new fromUser.RefreshUsers(users);
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromUser.UserErrors(err));
                        })
                    );
            }),
        );

    @Effect()
    loadUsersCount$ = this.actions$
        .pipe(
            ofType(fromUser.ActionTypes.LoadUserCount),
            mergeMap(() => this.userService.getUsersCount()
                .pipe(
                    map((count: number) => new LoadedUserCount(Number(count))),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromUser.UserErrors(err));
                    })
                ))
        );
}
