import {Action} from '@ngrx/store';
import {ICompetitor} from '@models/interfaces/human';
import {ITournament} from '@models/interfaces/tournament';
import {IExercise} from '@models/interfaces/exercise';

export enum ActionTypes {
    LoadCompetitors = '[Competitors Page] Load Competitors',
    LoadedCompetitors = '[Competitors Page] Loaded Competitors Success',
    ErrorsCompetitors = '[Competitors Page] Competitors Errors',
    CreateListOfCompetitors = '[Competitors Page] Create List Of Competitors',
    UpdateCompetitorByJudge = '[Competitors Page] Update Competitor by judge',
    LoadCompetitorsRatingByStage = '[Competitors Rating Page] Load Competitors Rating By Stage',
    LoadedCompetitorsRatingByStage = '[Competitors Rating Page] Loaded Competitors Rating By Stage',
    LoadCompetitorsRating = '[Competitors Rating Page] Load Competitors Rating'
}

interface IPayload {
    competitorsIds?: string[];
    tournament?: ITournament;
}

export class LoadCompetitors implements Action {
    readonly type = ActionTypes.LoadCompetitors;

    constructor(public payload: string) {
    }
}

export class LoadedCompetitors implements Action {
    readonly type = ActionTypes.LoadedCompetitors;

    constructor(public payload: ICompetitor[]) {
    }
}

export class ErrorsCompetitors implements Action {
    readonly type = ActionTypes.ErrorsCompetitors;

    constructor(public payload: any) {
    }
}

export class CreateListOfCompetitors implements Action {
    readonly type = ActionTypes.CreateListOfCompetitors;

    constructor(public payload: IPayload) {
    }
}

export class UpdateCompetitorByJudge implements Action {
    readonly type = ActionTypes.UpdateCompetitorByJudge;

    constructor(public payload: any) {
    }
}

export class LoadCompetitorsRatingByStage implements Action {
    readonly type = ActionTypes.LoadCompetitorsRatingByStage;

    constructor(public payload: { tournamentId: string, stageId: string }) {
    }
}

export class LoadedCompetitorsRatingByStage implements Action {
    readonly type = ActionTypes.LoadedCompetitorsRatingByStage;

    constructor(public payload: IExercise[]) {
    }
}

export class LoadCompetitorsRating implements Action {
    readonly type = ActionTypes.LoadCompetitorsRating;

    constructor(public payload: string) {
    }
}

export type CompetitorActions = LoadCompetitors &
    LoadedCompetitors & ErrorsCompetitors & CreateListOfCompetitors &
    UpdateCompetitorByJudge & LoadCompetitorsRatingByStage &
    LoadedCompetitorsRatingByStage & LoadCompetitorsRating;
