import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {select, Store} from '@ngrx/store';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';

import {MessageService} from '@services/message.service';
import {SpecialitiesService} from '@specialities/services/specialities.service';
import {ISpecialities} from '@models/interfaces/specialities';
import {selectSpecialitiesData} from '@specialities/selectors/selectors';
import * as fromSpecialities from '@specialities/actions/actions';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';

@Injectable()
export class SpecialitiesEffects {

    constructor(private actions$: Actions,
                private specialityService: SpecialitiesService,
                private messageService: MessageService,
                private store: Store<any>) {
    }

    @Effect()
    loadSpecialities$ = this.actions$
        .pipe(
            ofType(fromSpecialities.ActionTypes.LoadSpecialities),
            mergeMap(() => this.specialityService.getSpecialities()
                .pipe(
                    map((specialities: ISpecialities[]) => new fromSpecialities.LoadedSpecialities(specialities)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromSpecialities.SpecialitiesErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshSpecialities$ = this.actions$
        .pipe(
            ofType(fromSpecialities.ActionTypes.RefreshSpecialities),
            map((action: any) => new fromSpecialities.LoadedSpecialities(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromSpecialities.SpecialitiesErrors(err));
            })
        );

    @Effect()
    createSpeciality$ = this.actions$
        .pipe(
            ofType(fromSpecialities.ActionTypes.CreateSpeciality),
            withLatestFrom(this.store.pipe(select(selectSpecialitiesData))),
            switchMap(([action, specialities]: any[]) => this.specialityService.createSpeciality(action.payload)
                .pipe(
                    map((speciality) => {
                        const message = {user: ToastrUsersType.SPECIALITY, type: ToastrMessageType.CREATE};
                        if (speciality) {
                            this.messageService.showToastrSuccess(message);
                        }
                        specialities.push(speciality);
                        return new fromSpecialities.RefreshSpecialities(specialities);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromSpecialities.SpecialitiesErrors(err));
                    })
                ))
        );

    @Effect()
    updateSpeciality$ = this.actions$
        .pipe(
            ofType(fromSpecialities.ActionTypes.UpdateSpeciality),
            withLatestFrom(this.store.pipe(select(selectSpecialitiesData))),
            switchMap(([action, specialities]: any) => this.specialityService.updateSpeciality(action.payload)
                .pipe(
                    map((speciality: ISpecialities) => {
                        const message = {user: ToastrUsersType.SPECIALITY, type: ToastrMessageType.UPDATE};
                        if (speciality) {
                            this.messageService.showToastrSuccess(message);
                        }
                        specialities = specialities.map((item: ISpecialities) =>
                            item.id === speciality.id ? speciality : item);
                        return new fromSpecialities.RefreshSpecialities(specialities);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromSpecialities.SpecialitiesErrors(err));
                    })
                )
            )
        );

    @Effect()
    removeSpeciality$ = this.actions$
        .pipe(
            ofType(fromSpecialities.ActionTypes.RemoveSpeciality),
            withLatestFrom(this.store.pipe(select(selectSpecialitiesData))),
            switchMap(([action, specialities]: any[]) => this.specialityService.removeSpeciality(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.SPECIALITY, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);

                        specialities = specialities.filter((item: ISpecialities) => item.id !== action.payload);
                        return new fromSpecialities.RefreshSpecialities(specialities);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromSpecialities.SpecialitiesErrors(err));
                    })
                )
            )
        );
}
