import {Component, OnDestroy, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {select, Store} from '@ngrx/store';
import {TranslateService} from '@ngx-translate/core';
import {Subscription} from 'rxjs';
import {Location} from '@angular/common';

import {IDivision} from '@models/interfaces/division';
import {selectDivisionsData} from '@divisions/selectors/selectors';
import {selectIntervals} from '@shared/selectors/selectors';
import {SharedService} from '@shared/services/shared.service';
import {PageTypes} from '@models/constants/page-types';
import {CombatListService} from '@combat-list/services/combat-list.service';
import {IDialogActions, IPerson} from '@models/interfaces/human';
import {LoadDivisionCheckinByInterval, LoadPersonsCheckinByDivision} from '@checkin/actions/actions';
import {selectCheckinByInterval, selectPersonCheckinByDivision} from '@checkin/selectors/selectors';
import {ICombatNote} from '@models/interfaces/checkin';
import {Actions} from '@models/constants/actions';
import {LoadTimeIntervals} from '@shared/actions/actions';
import {LoadDivisions} from '@divisions/actions/actions';
import {ModalService} from '@modal/services/modal.service';
import {IDatePickerOptions} from '@models/interfaces/shared';
import {SubSink} from 'subsink';

@Component({
    selector: 'app-commander-division-list',
    templateUrl: './checkin.component.html',
    styleUrls: ['./checkin.component.scss']
})
export class CheckinComponent implements OnInit, OnDestroy {
    public form: FormGroup;
    public submitted = false;
    public intervals: string[] = [];
    public translatedIntervals: string[] = [];
    public divisions: IDivision[] = [];
    public divisionNames: string[] = [];
    public selectedDivision: string;
    public selectedDate: string;
    public selectedInterval: string;
    public isoDate: string;
    public openMobileModal = false;
    public pageType = PageTypes;
    public isInvalid = false;
    public personsByDivision: IPerson[];
    public checkinByInterval: ICombatNote[];
    public mobileActions: string[];

    private subscriptions: Subscription[] = [];
    private dialogValueSubscription: Subscription;
    private datePickerSubs: SubSink = new SubSink();

    constructor(private store: Store<any>,
                private translateService: TranslateService,
                private fb: FormBuilder,
                private sharedService: SharedService,
                private combatListService: CombatListService,
                private router: Router,
                private location: Location,
                private modalService: ModalService) {
    }

    ngOnInit() {
        this.mobileActions = [Actions.DETAILS];
        this.getDivisionsList();
        this.getTimeIntervals();
    }

    getDivisionsList() {
        this.subscriptions.push(
            this.store.pipe(select(selectDivisionsData))
                .subscribe((divisions: IDivision[]) => {
                    if (divisions) {
                        this.divisions = divisions;
                        this.divisionNames = this.getDivisionNames(this.divisions);
                    }

                    if (!this.divisions.length) {
                        this.store.dispatch(new LoadDivisions());
                    }
                })
        );
    }

    getDivisionNames(divisions: IDivision[]) {
        return divisions.map((division: IDivision) => division.name);
    }

    getTimeIntervals() {
        this.subscriptions.push(
            this.store.pipe(select(selectIntervals))
                .subscribe((intervals: string[]) => {
                    this.translatedIntervals = this.translateIntervals(intervals);
                    this.intervals = intervals;
                    if (!this.translatedIntervals.length || !this.intervals.length) {
                        this.store.dispatch(new LoadTimeIntervals());
                    }
                    this.createForm();
                })
        );
    }

    createForm() {
        this.form = this.fb.group({
            division: [null, Validators.required],
            date: ['', Validators.required],
            interval: [null, Validators.required]
        });
    }

    translateIntervals(intervals: string[]): string[] {
        return this.sharedService.translateArray(intervals);
    }

    handleAction(event: { action: string, item: any }) {
        if (event.action === Actions.DETAILS) {
            this.goToCheckinDetails();
        }
    }

    // Don't remove this method.
    // It needs for mobile.
    dialogAction(options: IDialogActions) {
        this.combatListService.getDialogAction(options);
        this.dialogValueSubscription = this.combatListService.dialogValue
            .subscribe((result) => {
                if (result && result !== '') {
                    if (this.divisionNames.includes(result)) {
                        this.getDivisionDialogValue(result);
                        this.setDivisionValue();
                        return;
                    } else if (this.translatedIntervals.includes(result)) {
                        this.getIntervalsDialogValue(result);
                        this.setIntervalValue();
                        return;
                    }
                }
            });
    }

    // Don't remove this method.
    // It needs for mobile.
    getDialogOptions(title: string, actions: string[]): IDialogActions {
        return this.combatListService.getDialogOptions(title, actions);
    }

    getDivisionCheckinByInterval() {
        this.subscriptions.push(
            this.store.pipe(select(selectCheckinByInterval))
                .subscribe((checkinByInterval: ICombatNote[]) =>
                    this.checkinByInterval = checkinByInterval)
        );
    }

    setDivisionValue() {
        const currentDivision = this.findCurrentDivision(this.selectedDivision);
        this.form.controls['division'].setValue(currentDivision.id);
        this.getPersonsByDivision(currentDivision.id);
    }

    getPersonsByDivision(divisionId: string | number) {
        this.store.dispatch(new LoadPersonsCheckinByDivision(divisionId));
        this.subscriptions.push(
            this.store.pipe(select(selectPersonCheckinByDivision))
                .subscribe((persons: IPerson[]) => this.personsByDivision = persons)
        );

    }

    getDivisionDialogValue(dialogValue: string) {
        this.selectedDivision = dialogValue;
        this.dialogValueSubscription.unsubscribe();
    }

    findCurrentDivision(divisionName: string): IDivision {
        return this.divisions.find((division: IDivision) => division.name === divisionName);
    }

    setIntervalValue() {
        const currentInterval = this.getCurrentInterval(this.selectedInterval);
        this.form.controls['interval'].setValue(currentInterval);
    }

    getCurrentInterval(item: string): string {
        const index = this.findSelectedIntervalIndex(item);
        return this.intervals[index];
    }

    findSelectedIntervalIndex(item: string): number {
        return this.translatedIntervals.findIndex((value: string) => value === item);
    }


    getIntervalsDialogValue(dialogValue: string) {
        this.selectedInterval = dialogValue;
        this.dialogValueSubscription.unsubscribe();
    }

    onItemSelected(item: any, type: string) {
        switch (type) {
            case 'division':
                this.selectedDivision = item.name;
                return this.form.controls[type].setValue(item.id);
            case 'interval':
                this.selectedInterval = this.getCurrentInterval(item);
                return this.form.controls[type].setValue(this.selectedInterval);
        }
    }

    // Don't remove this method.
    // It needs for mobile.
    onSelectDate(event) {
        const options: IDatePickerOptions = this.getDatePickerOptions(event);
        this.initDatePicker(options);
    }

    initDatePicker(options: IDatePickerOptions) {
        this.datePickerSubs.sink = this.modalService.initDateModal(options)
            .subscribe((date: Date) => {
                if (date) {
                    this.selectedDate = date.toDateString();
                    this.isoDate = date.toISOString();
                    this.form.controls['date'].setValue(this.isoDate);
                    this.datePickerSubs.unsubscribe();
                }
            });
    }

    getDatePickerOptions(event): IDatePickerOptions {
        return this.modalService.getDateModalOptions({
            date: new Date(),
            dateFormat: 'dd MMMM yyyy',
            okButtonText: '',
            cancelButtonText: '',
            title: '',
            locale: 'ru'
        }, event);
    }

    loadCheckin() {
        this.submitted = true;
        if (this.form.invalid) {
            this.isInvalid = true;
            return;
        }
        this.isInvalid = false;
        this.store.dispatch(new LoadDivisionCheckinByInterval(this.form.value));
        this.getDivisionCheckinByInterval();
    }

    goToCheckinDetails() {
        const currentDivision = this.findCurrentDivision(this.selectedDivision);
        const currentInterval = this.getCurrentInterval(this.selectedInterval);
        this.router.navigate(['checkin/checkin-details'], {
            queryParams: {
                divisionId: currentDivision.id,
                date: this.isoDate,
                interval: currentInterval || this.selectedInterval
            }
        });
    }

    getDatePart(date: string) {
        const currentHours = new Date(date);
        return currentHours.getUTCHours() < 12;
    }

    goBack() {
        this.location.back();
    }

    ngOnDestroy() {
        this.subscriptions.forEach((s: Subscription) => s.unsubscribe());
    }
}
