import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {switchMap} from 'rxjs/internal/operators/switchMap';

import * as fromCheckin from '@checkin/actions/actions';
import {MessageService} from '@services/message.service';
import {CheckinService} from '@checkin/services/checkin.service';
import {ICheckin, ICombatNote} from '@models/interfaces/checkin';
import {IPerson} from '@models/interfaces/human';
import {checkinMapperFromServer} from '@mappers/checkin.mapper';
import {selectPersonCheckinByDivision} from '@checkin/selectors/selectors';

@Injectable()
export class CheckinEffects {

    constructor(private actions$: Actions,
                private messageService: MessageService,
                private checkinService: CheckinService,
                private store: Store<any>) {
    }

    @Effect()
    loadCheckinByParams$ = this.actions$
        .pipe(
            ofType(fromCheckin.ActionTypes.LoadCheckin),
            mergeMap((action: any) => this.checkinService.searchCheckin(action.payload)
                .pipe(
                    map((checkin: ICheckin[]) => new fromCheckin.LoadedCheckin(checkin)),
                    catchError((error) => {
                        this.messageService.showPushNotification(error);
                        this.messageService.showToastrError(error);
                        return of(new fromCheckin.CheckinErrors(error));
                    })
                )
            )
        );

    @Effect()
    loadDivisionCheckinByInterval$ = this.actions$
        .pipe(
            ofType(fromCheckin.ActionTypes.LoadDivisionCheckinByInterval),
            withLatestFrom(this.store.pipe(select(selectPersonCheckinByDivision))),
            switchMap(([action, persons]: any) => {
                    return this.checkinService.getDivisionCheckinByInterval(action.payload)
                        .pipe(
                            map((combatLists: ICombatNote[]) => {
                                const mappedData = combatLists.map((item) => checkinMapperFromServer(item, persons.length));
                                return new fromCheckin.LoadedDivisionCheckinByInterval(mappedData);
                            }),
                            catchError((error) => {
                                this.messageService.showPushNotification(error);
                                this.messageService.showToastrError(error);
                                return of(new fromCheckin.CheckinErrors(error));
                            })
                        );
                }
            )
        );

    @Effect()
    loadPersonsCheckinByDivision$ = this.actions$
        .pipe(
            ofType(fromCheckin.ActionTypes.LoadPersonsCheckinByDivision),
            mergeMap((action: any) => this.checkinService.getPersonsCheckinByDivision(action.payload)
                .pipe(
                    map((persons: IPerson[]) => new fromCheckin.LoadedPersonsCheckinByDivision(persons)),
                    catchError((error) => {
                        this.messageService.showPushNotification(error);
                        this.messageService.showToastrError(error);
                        return of(new fromCheckin.CheckinErrors(error));
                    })
                )
            )
        );

    @Effect()
    clearCheckin$ = this.actions$
        .pipe(
            ofType(fromCheckin.ActionTypes.ClearCheckin),
            map((action: any) => new fromCheckin.LoadedCheckin(action.payload)),
            catchError((error) => {
                this.messageService.showPushNotification(error);
                this.messageService.showToastrError(error);
                return of(new fromCheckin.CheckinErrors(error));
            })
        );
}
