import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {select, Store} from '@ngrx/store';
import {map, tap} from 'rxjs/operators';
import {combineLatest, Observable} from 'rxjs';
import {Location} from '@angular/common';
import {SubSink} from 'subsink';

import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {IStandard} from '@models/interfaces/standard';
import {selectStandards} from '@quiz/selectors/selectors';
import {LoadCurrentStandard, LoadStandards} from '@quiz/actions/actions';
import {IDialogActions, IPerson} from '@models/interfaces/human';
import {DialogsService} from '@shared/services/dialogs.service';
import {LoadDivisions} from '@divisions/actions/actions';
import {selectDivisionNames, selectDivisionsData} from '@divisions/selectors/selectors';
import {IDivision} from '@models/interfaces/division';
import {LoadPersons, LoadPersonsByDivision} from '@persons/actions/actions';
import {selectPersonData, selectPersonsByDivision} from '@persons/selectors/selectors';
import {DivisionsService} from '@divisions/services/divisions.service';
import {PageTypes} from '@models/constants/page-types';
import {Actions} from '@models/constants/actions';
import {ModalService} from '@modal/services/modal.service';

@Component({
    selector: 'app-standard-details',
    templateUrl: './standard-details.component.html',
    styleUrls: ['./standard-details.component.scss']
})
export class StandardDetailsComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public standard$: Observable<void | IStandard>;
    public divisions$: Observable<IDivision[]>;
    public personsByDivision$: Observable<IPerson[]>;
    public divisions: IDivision[];
    public standard: IStandard;
    public personsByDivision: IPerson[];
    public divisionNames: string[];
    public currentDivisionName: string;
    public pageTypes = PageTypes;
    public mobileActions: string[];
    public standardMembers: IPerson[];
    public persons: IPerson[];

    private subjectId: string;
    private standardId: string;
    private dialogSubs: SubSink = new SubSink();

    constructor(private route: ActivatedRoute,
                private store: Store<any>,
                private location: Location,
                private dialogsService: DialogsService,
                private divisionService: DivisionsService,
                private modalService: ModalService,
                private router: Router,
    ) {
        super();
    }

    ngOnInit() {
        this.mobileActions = [Actions.MEMBER];
        this.getRouteParams();
        this.getDivisions();
        this.fetchPersons();
    }

    getRouteParams() {
        this.subscriptions.sink = combineLatest(this.route.paramMap, this.route.queryParams)
            .pipe(
                map(((result) => ({standardId: result[0]['params'].id, subjectId: result[1].subjectId})))
            ).subscribe((result) => {
                this.subjectId = result.subjectId;
                this.standardId = result.standardId;
                this.getStandard(this.subjectId, this.standardId);
            });
    }

    getStandard(subjectId: string, standardId: string) {
        this.standard$ = this.store.pipe(
            select(selectStandards),
            map((standards: IStandard[]) => {
                if (!standards) {
                    return this.fetchStandards(subjectId);
                }

                this.standard = this.findStandardById(standards, standardId);
                return this.standard;
            }));
    }

    fetchStandards(subjectId: string) {
        this.store.dispatch(new LoadStandards(subjectId));
    }

    findStandardById(standards: IStandard[], standardId: string): IStandard {
        return standards.find((standard: IStandard) => standard.id === standardId);
    }

    getDivisions() {
        this.divisions$ = this.store.pipe(
            select(selectDivisionsData),
            tap((divisions: IDivision[]) => {
                if (!divisions) {
                    return this.fetchDivisions();
                }
                this.divisions = divisions;
                this.getDivisionNames();
            }));
    }

    getPersons(divisions: IDivision[], divisionName: string) {
        this.subscriptions.sink = this.store.pipe(
            select(selectPersonData))
            .subscribe((persons: IPerson[]) => {
                if (persons) {
                    this.getPersonsByDivision(divisions, divisionName);
                }
            });
    }

    getPersonsByDivision(divisions: IDivision[], divisionName: string) {
        const division = this.findDivisionByName(divisions, divisionName);
        if (division) {
            this.store.dispatch(new LoadPersonsByDivision(division.id));
            this.personsByDivision$ = this.store.pipe(
                select(selectPersonsByDivision),
                tap((persons: IPerson[]) => {
                    if (persons) {
                        this.personsByDivision = persons.map((person: IPerson) => {
                            person['standardsMember'] = false;
                            return person;
                        });
                        this.dialogSubs.unsubscribe();
                    }
                })
            );
        }
    }

    fetchPersons() {
        this.store.dispatch(new LoadPersons());
    }

    findDivisionByName(divisions: IDivision[], divisionName: string) {
        return this.divisionService.findDivisonByName(divisions, divisionName);
    }

    getDivisionNames() {
        this.subscriptions.sink = this.store.pipe(select(selectDivisionNames))
            .subscribe((names: string[]) => this.divisionNames = names);
    }

    fetchDivisions() {
        this.store.dispatch(new LoadDivisions());
    }

    showDialogAction() {
        const options = this.setDialogOptions();
        this.dialogSubs.sink = this.dialogsService
            .getDialogAction(options)
            .subscribe(result => {
                if (result === '') {
                    this.dialogSubs.unsubscribe();
                    return;
                }
                this.currentDivisionName = result;
                this.getPersons(this.divisions, this.currentDivisionName);
            });
    }

    handleActions(event: { action: string, item: IPerson }) {
        this.setPersonsStatus(event.item.id);
    }

    setDialogOptions(): IDialogActions {
        return this.dialogsService.getDialogOptions('', this.divisionNames);
    }

    setPersonsStatus(personId: string) {
        this.personsByDivision = this.personsByDivision.map((person: IPerson) => {
            if (person.id === personId) {
                person.standardsMember = !person.standardsMember;
                return person;
            }
            return person;
        });
    }

    onSelectAll(type: string) {
        this.personsByDivision = this.personsByDivision.map((person: IPerson) => {
            if (type === 'select') {
                person.standardsMember = true;
                return person;
            }

            person.standardsMember = false;
            return person;
        });
    }

    onStart() {
        const standardMembers: IPerson[] = this.getStandardMembers();
        const percentOfMembers: number = this.getPercentOfMembers(standardMembers);
        this.store.dispatch(new LoadCurrentStandard({
            standard: this.standard,
            standardMembers,
            percentOfMembers
        }));
        this.router.navigate(['/', 'subjects', 'standards', this.standard.id, 'exam']);
    }

    getStandardMembers() {
        return this.personsByDivision
            .filter((person: IPerson) => person.standardsMember)
            .map((member: IPerson) => {
                member['timer'] = '00:00:00';
                member['started'] = false;
                return member;
            });
    }

    getPercentOfMembers(standardMembers: IPerson[]) {
        return standardMembers.length * 100 / this.personsByDivision.length;
    }

    isStandardMember(): boolean {
        return this.personsByDivision.some((person: IPerson) => person.standardsMember);
    }

    getModalDialogOptions(options) {
        return this.modalService.getModalDialogOptions(options);
    }

    goBack() {
        this.location.back();
    }

}
