import {Component, OnInit, ViewChild} from '@angular/core';
import {Observable} from 'rxjs';
import {ActivatedRoute, Router} from '@angular/router';
import {map} from 'rxjs/operators';
import {select, Store} from '@ngrx/store';
import {tap} from 'rxjs/internal/operators/tap';

import {IPerson} from '@models/interfaces/human';
import {Actions} from '@models/constants/actions';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {Mocks} from '@models/mocks/persons.mock';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {PersonService} from '@persons/service/person.service';
import {
    CreatePerson,
    DeletePerson,
    LoadPersonsByDivisionPerPage,
    LoadPersonsCount,
    LoadPersonsPerPage,
    UpdatePerson
} from '@persons/actions/actions';
import {selectPersonDataPerPage, selectPersonsCount} from '@persons/selectors/selectors';
import {PageTypes} from '@models/constants/page-types';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {LoadDivisions} from '@divisions/actions/actions';
import {IDivision} from '@models/interfaces/division';
import {selectDivisionsData} from '@divisions/selectors/selectors';
import {NgSelectComponent} from '@ng-select/ng-select';

@Component({
    selector: 'app-persons',
    templateUrl: './persons.component.html',
    styleUrls: ['./persons.component.scss']
})
export class PersonsComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public actions: string[] = [];
    public page: number;
    public size: number;
    public personsCount$: Observable<number>;
    public persons$: Observable<IPerson[]>;
    public pageTypes = PageTypes;
    public divisions$: Observable<IDivision[]>;
    public divisions: IDivision[];
    public selectedItem: IDivision;

    @ViewChild(NgSelectComponent, {static: false}) ngSelect: NgSelectComponent;

    constructor(private modalService: ModalService,
                private personService: PersonService,
                private router: Router,
                private route: ActivatedRoute,
                private store: Store<any>
    ) {
        super();
    }

    ngOnInit() {
        this.getRouteParams();
        this.getPersonsCount();
        this.getDivisions();
        this.actions = [Actions.CHANGE, Actions.REMOVE];
    }

    handleAction(event: { action: string, item: any }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openPersonModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            default:
                return;
        }
    }

    add() {
        this.openPersonModal(Mocks.emptyPerson(this.divisions[0]), false);
    }

    openPersonModal(person: IPerson, edit: boolean) {
        const objectType = 'Person';
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...person}, edit, objectType},
            (res) => {
                if (res) {
                    if (edit) {
                        this.updateList(person, res);
                    } else {
                        this.addPerson(res);
                    }
                    const item = this.ngSelect.itemsList.findByLabel(res.division.name);
                    this.ngSelect.select(item);
                }
            });
    }

    openDialogModal(message: string, item: IPerson) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.userName, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeFromList(item);
                }
            });
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParamMap
            .pipe(map((params) => {
                this.page = Number(params.get('page'));
                this.size = Number(params.get('size'));
            })).subscribe(() =>
                this.getPersonsPerPage({pageNumber: this.page || 1, pageSize: this.size || 10}));
    }

    getPersonsPerPage(data: any) {
        this.fetchPersonsPerPage(data);
        this.persons$ = this.store.pipe(select(selectPersonDataPerPage));
    }

    fetchPersonsPerPage(data) {
        this.store.dispatch(new LoadPersonsPerPage(data));
    }

    getPersonsCount() {
        this.personsCount$ = this.store.select(selectPersonsCount)
            .pipe(tap((count: number) => !count && this.fetchPersonsCount()));
    }

    fetchPersonsCount() {
        this.store.dispatch(new LoadPersonsCount());
    }

    getDivisions() {
        this.divisions$ = this.store.pipe(
            select(selectDivisionsData),
            tap((divisions: IDivision[]) => {
                if (!divisions) {
                    return  this.fetchDivisions();
                }
                this.divisions = divisions;
            })
        );
    }

    fetchDivisions() {
        this.store.dispatch(new LoadDivisions());
    }

    addPerson(person: IPerson) {
        const page = {pageNumber: this.page, pageSize: this.size};
        this.store.dispatch(new CreatePerson({person, page}));

    }

    removeFromList(person: IPerson) {
        const page = {pageNumber: this.page, pageSize: this.size};
        this.store.dispatch(new DeletePerson({person, page}));
    }

    updateList(item: IPerson, updatedData: IPerson) {
        const page = {pageNumber: this.page, pageSize: this.size};
        updatedData.id = item.id;
        this.store.dispatch(new UpdatePerson({person: updatedData, page}));
    }

    onSelectItem(value) {
        const page = {pageNumber: this.page, pageSize: this.size};
        this.selectedItem = value;
        if (!value) {
           return this.store.dispatch(new LoadPersonsPerPage(page));
        }
        this.store.dispatch(new LoadPersonsByDivisionPerPage({page, divisionId: value.id}));
    }
}
