import {ICompetitor} from '@models/interfaces/human';
import {ActionTypes, CompetitorActions} from '@competitors/actions/actions';
import {IExercise} from '@models/interfaces/exercise';

export interface State {
    competitors: ICompetitor[];
    rating: IExercise[];
    errors?: any;
}

export const initialState: State = {
    competitors: [],
    rating: []
};

export function reducer (state: State = initialState, action: CompetitorActions) {
    switch (action.type) {
        case ActionTypes.LoadedCompetitors:
            return {...state, competitors: action.payload};
        case ActionTypes.LoadedCompetitorsRatingByStage:
            return {...state, rating: action.payload};
        case ActionTypes.ErrorsCompetitors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
