import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/internal/operators/tap';
import {SubSink} from 'subsink';

import {Icons} from '@models/constants/icons';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {PageTypes} from '@models/constants/page-types';
import {ModalService} from '@modal/services/modal.service';
import {StandardsMocks} from '@models/mocks/specialities';
import {
    CreateStandards,
    DeleteStandards,
    LoadCategories,
    LoadConditions,
    LoadStandards,
    LoadSubjects,
    UpdateStandards,
} from '@quiz/actions/actions';
import {Actions} from '@models/constants/actions';
import {ICondition, IStandard, IStandardCategory} from '@models/interfaces/standard';
import {selectCategories, selectConditions, selectStandards, selectSubjects} from '@quiz/selectors/selectors';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {DialogsService} from '@shared/services/dialogs.service';
import {IDialogActions} from '@models/interfaces/human';
import {ISubject} from '@models/interfaces/quiz';

@Component({
    selector: 'app-standards',
    templateUrl: './standards.component.html',
    styleUrls: ['./standards.component.scss'],
})
export class StandardsComponent extends UnsubscribeOndestroyAdapter
    implements OnInit, OnDestroy {
    public subjectName: string;
    public subjectId: string;
    public icons = Icons;
    public objectType = PageTypes;
    public actions: string[];
    public mobileActions: string[];
    public conditions$: Observable<ICondition[]>;
    public categories$: Observable<IStandardCategory[]>;
    public standards$: Observable<IStandard[]>;
    public subjects$: Observable<ISubject[]>;
    public subjectsNames: string[] = [];
    public currentSubjectName: string;
    public currentSubject: ISubject;

    private dialogSubs: SubSink = new SubSink();

    constructor(
        private route: ActivatedRoute,
        private location: Location,
        private modalService: ModalService,
        private router: Router,
        private store: Store<any>,
        private dialogsService: DialogsService,
    ) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.mobileActions = [Actions.START];
        this.getParams();
        this.getCategories();
        this.getConditions();
        this.getSubjects();
    }

    getParams() {
        this.subscriptions.sink = this.route.queryParams.subscribe(params => {
            this.subjectName = params['name'];
            this.subjectId = params['subject'];
            if (this.subjectName && this.subjectId) {
                this.getStandards(this.subjectId);
            }
        });
    }

    getSubjects() {
        this.subjects$ = this.store.pipe(
            select(selectSubjects),
            tap((subjects: ISubject[]) => {
                if (!subjects) {
                    return this.fetchSubjects();
                }

                this.subjectsNames = this.getSubjectsNames(subjects);
                this.currentSubject = this.findSubjectByName(subjects, this.currentSubjectName);
                if (this.currentSubject && this.currentSubjectName) {
                    this.getStandards(this.currentSubject.id);
                }
            }));
    }

    fetchSubjects() {
        this.store.dispatch(new LoadSubjects());
    }

    getSubjectsNames(subjects: ISubject[]): string[] {
        return subjects.map((subject: ISubject) =>
            localStorage.getItem('lang') === 'ru' ? subject.ru : subject.kk);
    }

    findSubjectByName(subjects: ISubject[], subjectName: string): ISubject {
        return subjects.find((subject: ISubject) =>
            subject.ru === subjectName || subject.kk === subjectName);
    }

    getStandards(subjectId: string) {
        this.fetchStandards(subjectId);
        this.standards$ = this.store.select(selectStandards);
    }

    fetchStandards(subjectId: string) {
        this.store.dispatch(new LoadStandards(subjectId));
    }

    getCategories() {
        this.categories$ = this.store
            .select(selectCategories)
            .pipe(
                tap(
                    (categories: IStandardCategory[]) =>
                        !categories && this.fetchCategories(),
                ),
            );
    }

    fetchCategories() {
        this.store.dispatch(new LoadCategories());
    }

    getConditions() {
        this.conditions$ = this.store
            .select(selectConditions)
            .pipe(
                tap(
                    (conditions: ICondition[]) =>
                        !conditions && this.fetchConditions(),
                ),
            );
    }

    fetchConditions() {
        this.store.dispatch(new LoadConditions());
    }

    handleActions(event: { action: string; item: IStandard }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openStandardsModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.START:
                return this.goToChild(`subjects/standards/${event.item.id}`, this.currentSubject.id);
        }
    }

    add() {
        this.openStandardsModal(StandardsMocks.emptyStandard, false);
    }

    openStandardsModal(standard: any, edit: boolean) {
        const objectType = this.objectType.STANDARDS;
        this.modalService.openModal(
            BaseModalComponent,
            {centered: true, size: 'lg'},
            {object: {...standard}, edit, objectType, maxWidth: '1200px'},
            res => {
                if (res) {
                    edit
                        ? this.updateStandard(res, standard.id)
                        : this.createStandard(res);
                }
            },
        );
    }

    openDialogModal(message: string, item: IStandard) {
        this.modalService.openModal(
            DialogModalComponent,
            {centered: true},
            {itemName: item.namedRus, message},
            res => {
                if (res && res === 'Yes') {
                    this.deleteStandard(item.id);
                }
            },
        );
    }

    createStandard(standard: IStandard) {
        this.store.dispatch(new CreateStandards(standard));
    }

    updateStandard(updatedStandard: IStandard, standardId: string) {
        updatedStandard.id = standardId;
        this.store.dispatch(new UpdateStandards(updatedStandard));
    }

    deleteStandard(standardId: string) {
        this.store.dispatch(new DeleteStandards(standardId));
    }

    // Don't delete this method.
    // It needs for mobile version.
    showDialogAction() {
        const options = this.setDialogOptions();
        this.dialogSubs.sink = this.dialogsService
            .getDialogAction(options)
            .subscribe(result => {
                if (result === '') {
                    this.dialogSubs.unsubscribe();
                    return;
                }
                this.currentSubjectName = result;
                this.getSubjects();
            });
    }

    setDialogOptions(): IDialogActions {
        return this.dialogsService.getDialogOptions('', this.subjectsNames);
    }

    goBack() {
        this.location.back();
    }

    goToChild(childPath: string, subjectId?: string) {
        this.router.navigate([childPath], {queryParams: {subjectId}});
    }
}
