import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {Subscription, timer} from 'rxjs';
import {TimerService} from '@exercises/timer.service';

@Component({
    selector: 'app-timer',
    templateUrl: './timer.component.html',
    styleUrls: ['./timer.component.scss']
})
export class TimerComponent implements OnInit {
    public ticks = 0;
    public timerLabel = 'Start';
    private timerSubscription: Subscription;
    private started: boolean;
    public defaultDisplayTime = '00 : 00 : 00';
    public displayTime: string;

    @Output() timerValue: EventEmitter<{ value: number; label: string }> = new EventEmitter();

    constructor(private timerService: TimerService) {
    }

    ngOnInit() {
        this.displayTime = this.defaultDisplayTime;
    }

    startTimer() {
        const $timer = timer(0, 1000);
        this.timerSubscription = $timer.subscribe(ticks => {
            this.ticks = ticks;
            this.displayTime = this.generateTimeString(ticks);
            this.getTimerValue();
        });
    }

    stopTimer() {
        this.timerSubscription.unsubscribe();
    }

    public generateTimeString(time: number) {
        return this.timerService.generateTimeString(time);
    }

    public clickTimer() {
        this.started ? this.stopTimer() : this.startTimer();
        this.started = !this.started;
        this.timerLabel = this.started ? 'stop' : 'start';
    }


    getTimerValue() {
        this.timerValue.emit({value: this.ticks, label: this.displayTime});
    }

}
