import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';

import {environment} from '@environments/environment';
import {Observable} from 'rxjs';
import {IWorkSpace} from '@models/interfaces/workspace';
import {map} from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class WorkspaceService {
    public baseUrl = `${environment.BASE_URL}/api/workspace/${environment.version}`;

    constructor(private http: HttpClient) {
    }

    getWorkSpaceForTest(): Observable<IWorkSpace[]> {
        return this.http.get<IWorkSpace[]>(`${this.baseUrl}/allfortest`)
            .pipe(map((response: IWorkSpace[]) => response));
    }

    getCurrentWorkspace(): Observable<IWorkSpace> {
        return this.http.get<IWorkSpace>(this.baseUrl)
            .pipe(map((response: IWorkSpace) => response));
    }

    sendCurrentWorkspace(workspace: IWorkSpace): Observable<IWorkSpace> {
        return this.http.post<IWorkSpace>(`${this.baseUrl}/useintest`, workspace)
            .pipe(map((response: IWorkSpace) => response));
    }
}
