import {ActionTypes, SharedAction} from '@shared/actions/actions';
import {IStatus, IUser} from '@models/interfaces/human';
import {IRank} from '@models/interfaces/ranks';

export interface State {
    classifiers: any[];
    weaponTypes: any[];
    levels: any[];
    validations: any;
    competitorMarks: string[];
    disqualificationTypes: string[];
    errors?: any;
    queries?: any;
    image?: any;
    statuses: IStatus[];
    intervals: string[];
    user: IUser;
    ranks: IRank[];
}

export const initialState: State = {
    classifiers: [],
    weaponTypes: [],
    levels: [],
    validations: undefined,
    competitorMarks: [],
    disqualificationTypes: [],
    statuses: [],
    intervals: [],
    user: null,
    ranks: []
};

export function reducer(state: State = initialState, action: SharedAction) {
    switch (action.type) {
        case ActionTypes.LoadedClassifiers:
            return {...state, classifiers: action.payload};
        case ActionTypes.LoadedWeaponTypes:
            return {...state, weaponTypes: action.payload};
        case ActionTypes.LoadedLevels:
            return {...state, levels: action.payload};
        case ActionTypes.LoadedValidations:
            return {...state, validations: action.payload};
        case ActionTypes.LoadedCompetitorMarks:
            return {...state, competitorMarks: action.payload};
        case ActionTypes.LoadedDisqualificationTypes:
            return {...state, disqualificationTypes: action.payload};
        case ActionTypes.SetQueries:
            return {...state, queries: action.payload};
        case ActionTypes.SharedErrors:
            return {...state, errors: action.payload};
        case ActionTypes.UploadedImage:
            return {...state, image: action.payload};
        case ActionTypes.LoadedStatuses:
            return {...state, statuses: action.payload};
        case ActionTypes.LoadedTimeIntervals:
            return {...state, intervals: action.payload};
        case ActionTypes.LoadedUserById:
            return {...state, user: action.payload};
        case ActionTypes.LoadedRanks:
            return {...state, ranks: action.payload};
        default:
            return state;
    }
}
