import {Injectable} from '@angular/core';
import {Observable, Subject} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';

import {environment} from '@environments/environment';
import {IDialogActions, IPerson} from '@models/interfaces/human';
import {checkinToServer} from '@mappers/person.mapper';

@Injectable({
    providedIn: 'root'
})
export class CombatListService {
    public dialogValue: Subject<string> = new Subject<string>();
    private checkinBaseUrl: string = environment.BASE_URL + '/api/checkin/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getDialogAction(data: any) {
        return;
    }

    createCheckinByDivision(data: IPerson[]): Observable<any> {
        const mappedData = data.map((person: IPerson) => checkinToServer(person));
        return this.http.post(this.checkinBaseUrl + '/check', mappedData)
            .pipe(map((response) => response));
    }

    createCombatListAfterCheckin(data: { combatId: string, date: string, divisionId: string }): Observable<any> {
        const body = {combatId: data.combatId, date: data.date};
        return this.http.post(this.checkinBaseUrl + `/check/division/${data.divisionId}/combatnote`, body)
            .pipe(map((response) => response));
    }

    getDialogOptions(title: string, actions: string[]): IDialogActions {
        return {
            title: ` ${title}`,
            cancelButtonText: '',
            actions: actions
        };
    }

    getDatePickerConfig() {
        return;
    }
}
