import {Action} from '@ngrx/store';
import {IWeapon, IWeaponTypes} from '@models/interfaces/weapons';

export enum ActionTypes {
    LoadWeaponTypesForList = '[WeaponTypes Page] Load WeaponTypes',
    LoadedWeaponTypesForList = '[WeaponTypes Page] Loaded WeaponTypes',
    CreateWeaponType = '[WeaponTypes Page] Create WeaponType',
    DeleteWeaponType = '[WeaponTypes Page] Delete WeaponType',
    UpdateWeaponType = '[WeaponTypes Page] Update WeaponType',
    RefreshWeaponTypes = '[WeaponTypes Page] Refresh WeaponTypes',
    LoadWeaponList = '[WeaponList Page] Load Weapon List',
    LoadedWeaponList = '[WeaponList Page] Loaded Weapon List',
    RefreshWeaponList = '[WeaponList Page] Refresh Weapon List',
    CreateWeaponListItem = '[WeaponList Page] Create Weapon List Item',
    UpdateWeaponListItem = '[WeaponList Page] Update Weapon List Item',
    DeleteWeaponListItem = '[WeaponList Page] Delete Weapon List Item',
    WeaponErrors = '[WeaponTypes Page] Weapons Errors',
    UnlinkWeapon = '[WeaponList Page] Unlink Weapon',
    LinkWeapon = '[WeaponList Page] Link Weapon',
    AddShootsCount = '[WeaponList Page] Add Shoots Count',
    LoadPersonsWeaponList = '[WeaponList Page] Load Persons Weapon List',
    LoadedPersonsWeaponList = '[WeaponList Page] Loaded Persons Weapon List',
}

export class LoadWeaponTypesForList implements Action {
    readonly type = ActionTypes.LoadWeaponTypesForList;
}

export class LoadedWeaponTypesForList implements Action {
    readonly type = ActionTypes.LoadedWeaponTypesForList;

    constructor(public payload: IWeaponTypes[]) {}
}

export class RefreshWeaponTypes implements Action {
    readonly type = ActionTypes.RefreshWeaponTypes;

    constructor(public payload: IWeaponTypes[]) {}
}

export class CreateWeaponType implements Action {
    readonly type = ActionTypes.CreateWeaponType;

    constructor(public payload: IWeaponTypes) {}
}

export class UpdateWeaponType implements Action {
    readonly type = ActionTypes.UpdateWeaponType;

    constructor(public payload: IWeaponTypes) {}
}

export class DeleteWeaponType implements Action {
    readonly type = ActionTypes.DeleteWeaponType;

    constructor(public payload: string) {}
}

export class WeaponErrors implements Action {
    readonly type = ActionTypes.WeaponErrors;

    constructor(public payload: any) {}
}

export class LoadWeaponList implements Action {
    readonly type = ActionTypes.LoadWeaponList;

    constructor(public payload: string | number) {}
}

export class LoadedWeaponList implements Action {
    readonly type = ActionTypes.LoadedWeaponList;

    constructor(public payload: IWeapon[]) {}
}

export class LoadPersonsWeaponList implements Action {
    readonly type = ActionTypes.LoadPersonsWeaponList;

    constructor(public payload: {personName: string, divisionId: string}) {}
}

export class RefreshWeaponList implements Action {
    readonly type = ActionTypes.RefreshWeaponList;

    constructor(public payload: IWeapon[]) {}
}

export class CreateWeaponListItem implements Action {
    readonly type = ActionTypes.CreateWeaponListItem;

    constructor(public payload: IWeapon) {}
}

export class UpdateWeaponListItem implements Action {
    readonly type = ActionTypes.UpdateWeaponListItem;

    constructor(public payload: IWeapon) {}
}

export class DeleteWeaponListItem implements Action {
    readonly type = ActionTypes.DeleteWeaponListItem;

    constructor(public payload: string) {}
}

export class UnlinkWeapon implements Action {
    readonly type = ActionTypes.UnlinkWeapon;

    constructor(public payload: string) {}
}

export class LinkWeapon implements Action {
    readonly type = ActionTypes.LinkWeapon;

    constructor(public payload: {weaponId: string, personId: string}) {}
}

export class AddShootsCount implements Action {
    readonly type = ActionTypes.AddShootsCount;

    constructor(public payload: {weaponId: string, fireCount: number}) {}
}



export type WeaponsActions = LoadWeaponTypesForList &
    LoadedWeaponTypesForList & CreateWeaponType & RefreshWeaponTypes &
    DeleteWeaponType & WeaponErrors & LoadWeaponList &
    LoadedWeaponList & RefreshWeaponList & CreateWeaponListItem &
    UpdateWeaponListItem & DeleteWeaponListItem & UnlinkWeapon &
    LinkWeapon & AddShootsCount & UpdateWeaponType & LoadPersonsWeaponList;
