import {Component, OnInit} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {TreeNode} from 'angular-tree-component';

import {IDivision} from '@models/interfaces/division';
import {Actions} from '@models/constants/actions';
import {DivisionsService} from '@divisions/services/divisions.service';
import {selectRootDvisionData} from '@divisions/selectors/selectors';
import {CreateDivision, LoadRootDivision, RemoveDivision, UpdateDivision} from '@divisions/actions/actions';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {Animations} from '@models/constants/animations';

@Component({
    selector: 'app-divisions',
    templateUrl: './divisions.component.html',
    styleUrls: ['./divisions.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class DivisionsComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public actions = [Actions.CHANGE, Actions.REMOVE];
    public nodes: IDivision[];

    constructor(private divisionsService: DivisionsService,
                private store: Store<any>,
                private modalService: ModalService) {
        super();
    }

    ngOnInit() {
        this.getRootDivision();
    }

    getRootDivision() {
        this.subscriptions.sink = this.store.pipe(select(selectRootDvisionData))
            .subscribe(root => {
                if (!root) {
                    this.fetchRootDivisions();
                }
                if (root !== null) {
                    this.nodes = [root];
                }
            });
    }

    fetchRootDivisions() {
        this.store.dispatch(new LoadRootDivision());
    }

    openDivisionModal(parentNode: TreeNode, edit: boolean) {
        const objectType = 'Division';
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: edit ? parentNode.data : this.mapParentNode(parentNode.data), edit, objectType},
            (division: IDivision) => {
                if (division) {
                    division.parent = parentNode.data.id;
                    if (edit) {
                        division.parent = parentNode.data.parent;
                        division.id = parentNode.data.id;
                        this.updateDivisionAndNode(division, division.id);
                    } else {
                        this.createDivisionAndNode(division);
                        parentNode.expand();
                    }
                }
            });
    }

    mapParentNode(data) {
        return {
            name: '',
            active: true,
            parent: data.parent
        };
    }

    openRemoveModal(node: TreeNode) {
        this.openDialogModal('dialogQuestion', node);
    }


    openDialogModal(message: string, node: TreeNode) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: node.data.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeDivisionAndNode(node);
                }
            });
    }

    createDivisionAndNode(newDivision: IDivision) {
        this.store.dispatch(new CreateDivision(newDivision));
    }

    updateDivisionAndNode(division: IDivision, id: string | number) {
        this.store.dispatch(new UpdateDivision({division, id}));
    }

    removeDivisionAndNode(removedNode: TreeNode) {
        this.store.dispatch(new RemoveDivision(removedNode.data.id));
    }
}
