import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromJudge from '@judges/reducers/judges.reducer';

export const selectJudges = createFeatureSelector<fromJudge.State>('judges');

export const selectJudgesData = createSelector(
    selectJudges,
    (state: fromJudge.State) => state.judges
);

export const selectJudgesErrors = createSelector(
    selectJudges,
    (state: fromJudge.State) => state.errors
);

