import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {Store} from '@ngrx/store';

import {Icons} from '@models/constants/icons';
import {SharedService} from '@shared/services/shared.service';

@Component({
    selector: 'app-image-loader',
    templateUrl: './image-loader.component.html',
    styleUrls: ['./image-loader.component.scss'],
})
export class ImageLoaderComponent implements OnInit {
    public icons = Icons;
    public isUnderBlock = false;

    @Input() imagePath: string;

    @Output() checkFileChanges: EventEmitter<File> = new EventEmitter();
    @Output() loadImage: EventEmitter<File> = new EventEmitter();
    @Output() deleteImage: EventEmitter<string> = new EventEmitter();

    @ViewChild('imageFile', { static: false }) imageFileValue: ElementRef;

    constructor(private store: Store<any>,
                private sharedService: SharedService) {
    }

    ngOnInit() {
    }

    checkFile(file) {
        this.checkFileChanges.emit(file);
    }

    uploadImage() {
        const file = this.imageFileValue.nativeElement.files[0];
        this.loadImage.emit(file);
    }

    resetImage() {
        this.imageFileValue.nativeElement.value = '';
    }

    onDeleteImage() {
        this.deleteImage.emit(this.imagePath);
    }

    enterLeaveMouse() {
        this.isUnderBlock = !this.isUnderBlock;
    }

    get imageUrl() {
        return this.sharedService.imageBaseUrl;
    }

}
