import {ActionTypes, ICurrentStandard, QuizActions} from '../actions/actions';
import {IQuestion, ISubject, ITest} from '@models/interfaces/quiz';
import {ICondition, IConditionUnits, IStandard, IStandardCategory} from '@models/interfaces/standard';

export interface State {
    subjects: ISubject[];
    tests: ITest[];
    questions: IQuestion[];
    currentQuestion: object;
    standardsCategories: IStandardCategory[];
    standardsConditions: ICondition[];
    units: IConditionUnits[];
    standards: IStandard[];
    currentStandard: ICurrentStandard;
    errors?: any;
}

export const initialState: State = {
    subjects: undefined,
    tests: null,
    questions: null,
    currentQuestion: {},
    standardsCategories: undefined,
    standardsConditions: undefined,
    units: undefined,
    standards: undefined,
    currentStandard: undefined
};

export function reducer(state: State = initialState, action: QuizActions) {
    switch (action.type) {
        case ActionTypes.LoadedSubjects:
            return {...state, subjects: action.payload};
        case ActionTypes.LoadedTestsBySubject:
            return {...state, tests: action.payload};
        case ActionTypes.SetQuestionsByTest:
            return {...state, questions: action.payload};
        case ActionTypes.QuizErrors:
            return {...state, errors: action.payload};
        case ActionTypes.LoadedQuestionById:
            return {...state, currentQuestion: action.payload};
        case ActionTypes.LoadedStandards:
            return {...state, standards: action.payload};
        case ActionTypes.LoadedCategories:
            return {...state, standardsCategories: action.payload};
        case ActionTypes.LoadedConditions:
            return {...state, standardsConditions: action.payload};
        case ActionTypes.LoadedUnits:
            return {...state, units: action.payload};
        case ActionTypes.LoadedCurrentStandard:
            return {...state, currentStandard: action.payload};
        default:
            return state;
    }
}
