import {IWorkSpace} from '@models/interfaces/workspace';
import {ActionTypes, WorkspaceActions} from '../actions/actions';

export interface State {
    workspaces: IWorkSpace[];
    currentWorkspace: IWorkSpace;
}

export const initialState: State = {
    workspaces: undefined,
    currentWorkspace: undefined,
};

export function reducer(state: State = initialState, action: WorkspaceActions) {
    switch (action.type) {
        case ActionTypes.LoadedWorkspaces:
            return {...state, workspaces: action.payload};
        case ActionTypes.LoadedCurrentWorkspace:
            return {...state, currentWorkspace: action.payload};
        default:
            return state;
    }
}
