import {Action} from '@ngrx/store';
import {IJudge, IUserRegistration} from '@models/interfaces/human';

export enum ActionTypes {
    LoadJudges = '[Judges Page] Load Judges',
    LoadedJudges = '[Judges Page] Loaded Judges',
    ErrorLoadedJudges = '[Judges Page] Error Loaded Judges',
    CreateJudge = '[Judges Page] Create Judges',
    DeleteJudge = '[Judges Page] Delete Judges',
    UpdateJudge = '[Judges Page] Update Judges',
    ChangePassword = '[Judges Page] Change Password',
    RefreshJudges = '[Judges Page] Refresh Judges'
}

export class LoadJudges implements Action {
    readonly type = ActionTypes.LoadJudges;

    constructor() {
    }
}

export class LoadedJudges implements Action {
    readonly type = ActionTypes.LoadedJudges;

    constructor(public payload: IJudge[]) {
    }
}

export class RefreshJudges implements Action {
    readonly type = ActionTypes.RefreshJudges;

    constructor(public payload: IJudge[]) {
    }
}

export class ErrorLoadedJudges implements Action {
    readonly type = ActionTypes.ErrorLoadedJudges;

    constructor(public payload: any) {
    }
}

export class ErrorChangedPassword implements Action {
    readonly type = ActionTypes.ErrorLoadedJudges;

    constructor(public payload: any) {
    }
}

export class CreateJudge implements Action {
    readonly type = ActionTypes.CreateJudge;

    constructor(public payload: IUserRegistration) {
    }
}

export class DeleteJudge implements Action {
    readonly type = ActionTypes.DeleteJudge;

    constructor(public payload: string) {
    }
}

export class UpdateJudge implements Action {
    readonly type = ActionTypes.UpdateJudge;

    constructor(public payload: any) {
    }
}

export class ChangePassword implements Action {
    readonly type = ActionTypes.ChangePassword;

    constructor(public payload: { data: any, newPassword: string }) {
    }
}

export type JudgesAction = LoadJudges | LoadedJudges | ErrorLoadedJudges |
    CreateJudge | DeleteJudge | UpdateJudge | ChangePassword | ErrorChangedPassword |
    RefreshJudges;
