import {Component, OnInit, ViewContainerRef, ViewEncapsulation} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Location} from '@angular/common';
import {ActivatedRoute} from '@angular/router';
import {SubSink} from 'subsink';

import {ModalService} from '@modal/services/modal.service';
import {PersonService} from '@persons/service/person.service';
import {ConnectionService} from '@services/connection.service';
import {LocalStorageService} from '@services/local-storage.service';
import {MarkService} from '@services/mark.service';
import {competitorMapperFromServer} from '@mappers/tournament.mapper';
import {CreateListOfCompetitors, UpdateCompetitorByJudge} from '@competitors/actions/actions';
import {ForceUpdateTournamentCompetitors, LoadTournaments} from '@tournaments/actions/actions';
import {LoadDivisions} from '@divisions/actions/actions';
import {LoadPersons} from '@persons/actions/actions';
import {selectPersonData} from '@persons/selectors/selectors';
import {selectDivisionsData} from '@divisions/selectors/selectors';
import {selectTournamentsData} from '@tournaments/selectors/selectors';
import {ICompetitor, IPerson} from '@models/interfaces/human';
import {IDivision} from '@models/interfaces/division';
import {ITournament} from '@models/interfaces/tournament';
import {Actions} from '@models/constants/actions';
import {PageTypes} from '@models/constants/page-types';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {ModalRootComponent} from '@shared/components/modal-root/modal-root.component';
import {TournamentsService} from '@tournaments/services/tournaments.service';
import {LoadCompetitorMarks} from '@shared/actions/actions';

@Component({
    selector: 'app-competitor',
    templateUrl: './competitors.component.html',
    styleUrls: ['./competitors.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CompetitorsComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public mobileActions: string[];
    public competitors: ICompetitor[];
    public persons: IPerson[];
    public divisions: IDivision[] = [];
    public searchValue = '';
    public tournamentId: string;
    public division: IDivision;
    public divisionValue: string;
    public pageSize = 10;
    public competitor: ICompetitor;
    public pageType = PageTypes;
    public mark: string;
    public competitorLength = 0;

    public tournamentName: string;

    private connectionStatus: string;
    public tournaments: ITournament[];
    private tournament: ITournament;
    private dialogSubs: SubSink = new SubSink();

    constructor(private modalService: ModalService,
                private personService: PersonService,
                private store: Store<any>,
                private location: Location,
                private route: ActivatedRoute,
                private storageService: LocalStorageService,
                private connectionService: ConnectionService,
                private markService: MarkService,
                private vcRef: ViewContainerRef,
                private tournamentsService: TournamentsService) {
        super();
    }

    ngOnInit() {
        this.getDivisions();
        this.getRouteParams();
        this.mobileActions = [Actions.CHANGE];
        this.fetchMarks();
        if (this.connectionService.status) {
            this.getConnectionStatus();
        }
    }

    fetchMarks() {
        this.store.dispatch(new LoadCompetitorMarks());
    }

    getConnectionStatus() {
        this.subscriptions.sink = this.connectionService.status
            .subscribe(status => this.connectionStatus = status);
    }

    getDivisions() {
        this.subscriptions.sink = this.store.pipe(select(selectDivisionsData))
            .subscribe((divisions: IDivision[]) => {
                if (!divisions) {
                    return this.store.dispatch(new LoadDivisions());
                }

                this.divisions = divisions;
            });
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams.subscribe((params) => {
            this.tournamentId = params['tournament'];
            this.tournamentName = params['name'];
            this.getPersons(this.tournamentId);
        });
    }

    getPersons(tournamentId: string) {
        this.fetchPersons();
        this.subscriptions.sink = this.store.pipe(select(selectPersonData))
            .subscribe((persons: IPerson[]) => {
                if (persons) {
                    this.persons = persons;
                    this.getCompetitors(tournamentId);
                }
            });
    }

    fetchPersons() {
        this.store.dispatch(new LoadPersons());
    }

    getCompetitors(tournamentId: string) {
        this.subscriptions.sink = this.store.pipe(select(selectTournamentsData))
            .subscribe((tournaments: ITournament[]) => {
                if (!tournaments) {
                    return this.store.dispatch(new LoadTournaments());
                }
                if (tournaments && tournaments.length && tournamentId) {
                    this.tournaments = tournaments;
                    this.tournament = this.findTournamentById(tournaments, tournamentId);
                    this.competitors = this.getCompetitorsFromTournament(tournaments, tournamentId);
                    this.findCompetitors();
                }
            });
    }

    findCompetitors() {
        this.competitors.forEach((competitor: ICompetitor) => {
            this.persons = this.setCompetitors(this.persons, competitor);
            this.filterPersonsByMember(this.persons);
        });
    }

    setCompetitors(persons: IPerson[], competitor: ICompetitor): IPerson[] {
        return persons.map((person: IPerson) => {
            if (person.id === competitor.person.id) {
                person.member = true;
            }
            return person;
        });
    }

    filterPersonsByMember(persons: IPerson[]) {
        this.competitorLength = persons.filter((person: IPerson) => person.member).length;
    }

    createListOfCompetitors() {
        const competitorsIds = this.personsIds;
        this.store.dispatch(new CreateListOfCompetitors({competitorsIds, tournament: this.tournament}));
    }

    get personsIds(): string[] {
        return this.persons
            .filter((person: IPerson) => person.member === true)
            .map((item: IPerson) => item.id);
    }

    handleAction(event: any) {
        const options = this.getModalOptions(event.item);
        this.competitor = event.item;
        this.initModal(options);
    }

    initModal(options) {
        this.dialogSubs.sink = this.modalService.initModal(ModalRootComponent, options)
            .subscribe((result) => {
                this.tournamentId = result.tournamentId;
                this.getCompetitors(this.tournamentId);
                if (result.value) {
                    this.updateCompetitor(result.value, this.tournamentId);
                    this.dialogSubs.unsubscribe();
                }
            });
    }

    getModalOptions(competitor: ICompetitor) {
        return this.modalService.getModalDialogOptions({
            vcRef: this.vcRef,
            context: {
                path: 'competitors-modal-view',
                competitor,
                tournamentId: this.tournamentId
            },
            fullscreen: false,
        });
    }

    updateCompetitor(competitorUpdates, tournamentId: string) {
        competitorUpdates.id = this.competitor.id;
        const tournament = this.findTournamentById(this.tournaments, tournamentId);
        if (this.connectionStatus && this.connectionStatus !== this.connectionService.NO_CONNECTION) {
            this.store.dispatch(new UpdateCompetitorByJudge({
                tournament: tournament,
                competitorId: this.competitor.id,
                competitorUpdates,
                fromStorage: false
            }));
            return;
        }
        this.store.dispatch(new ForceUpdateTournamentCompetitors({tournament, competitor: competitorUpdates}));
        this.storageService.addCompetitor(this.tournamentId, competitorUpdates);
    }

    getCompetitorsFromTournament(tournaments: ITournament[], tournamentId: string): ICompetitor[] {
        return this.findTournamentById(tournaments, tournamentId).competitors
            .map((competitor: ICompetitor, index: number) => competitorMapperFromServer(competitor, index));
    }

    findTournamentById(tournaments: ITournament[], tournamentId: string): ITournament {
        return this.tournamentsService.findTournamentById(tournaments, tournamentId);
    }

    selectAll() {
        this.persons.forEach(competitor => competitor.member = true);
    }

    unSelectAll() {
        this.persons.forEach(competitor => competitor.member = false);
    }

    cancel() {
        this.location.back();
    }

    getSearchValue(value) {
        this.searchValue = value;
    }

    onSelectItem(value: IDivision) {
        this.division = value;
        this.divisionValue = value.name;
    }
}
