import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {select, Store} from '@ngrx/store';

import {IUser} from '@models/interfaces/human';
import {FormsService} from '@forms/services/forms.service';
import {selectValidations} from '@shared/selectors/selectors';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {LoadValidations} from '@shared/actions/actions';

@Component({
    selector: 'app-change-password-form',
    templateUrl: './change-password-form.component.html',
    styleUrls: ['./change-password-form.component.scss']
})
export class ChangePasswordFormComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public form: FormGroup;

    public password: string;
    @Output() save = new EventEmitter<IUser>();
    @Output() cancel = new EventEmitter();
    public submitted: boolean;

    constructor(private fb: FormBuilder,
                private formsService: FormsService,
                private store: Store<any>) {
        super();
    }

    ngOnInit() {
        this.getValidations();
        this.detectValueChanges();
    }

    getValidations() {
        this.subscriptions.sink = this.store.pipe(select(selectValidations))
            .subscribe((validations: any) => {
                if (!validations) {
                    this.store.dispatch(new LoadValidations());
                }
                this.createForm(validations);
            });
    }

    createForm(validations: any) {
        this.form = this.fb.group(
            {
                'password': ['', this.getValidatorType(validations, 'newPassword')],
                'repassword': ['', this.getValidatorType(validations, 'newPassword')],
            }, {validator: this.checkPasswords});
    }

    getValidatorType(validations: any, control: string) {
        return this.formsService.generateValidationListByBean(validations.ChangePasswordBean, control);
    }

    detectValueChanges() {
        this.subscriptions.sink = this.form.valueChanges.subscribe(value => this.password = value.password);
    }

    checkPasswords(group: FormGroup) {
        const password = group.controls.password.value;
        const repassword = group.controls.repassword.value;

        return (!password || !repassword || password === repassword) ? null : {notEquals: true};
    }

    onSave(password) {
        this.submitted = true;
        if (this.form.invalid) {
            return;
        }
        this.save.next(password);
    }

    onCancel() {
        this.cancel.next();
    }
}
