import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '@environments/environment';
import {Router} from '@angular/router';
import {TokenService} from '@services/token.service';
import {ICreadentials} from '@models/interfaces/credentials';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class LoginService {

    private options: object = {withCredentials: true};

    constructor(private http: HttpClient,
                private tokenService: TokenService,
                private router: Router) {
    }

    public login(credentials: ICreadentials): Observable<any> {
        return this.http.post(`${environment.BASE_URL}/api/auth/${environment.version}/login`, credentials)
            .pipe(map((data) => data));
    }

    public logOut() {
        return this.http.post(`${environment.BASE_URL}/api/auth/${environment.version}/logout`, null)
            .subscribe(() => {
                this.tokenService.removeToken();
                localStorage.removeItem('role');
                this.router.navigate(['/login']);
            }, err => {
                console.log(err);
            });
    }
}
