import {Action} from '@ngrx/store';
import {IPerson} from '@models/interfaces/human';

export enum ActionTypes {
    CreateCheckin = '[Combat List Page] Create Checkin',
    CreateCombatList = '[Combat List Page] Create Combat List',
    LoadedCurrentCombatList = '[Combat List Page] Loaded Current Combat List',
    ClearCombatList = '[Combat List Page] Clear Combat List',
    CombatListErrors = '[Combat List Errors] Combat List Errors'
}

export class CreateCheckin implements Action {
    readonly type = ActionTypes.CreateCheckin;

    constructor(public payload: IPerson[]) {
    }
}

export class CreateCombatList implements Action {
    readonly type = ActionTypes.CreateCombatList;

    constructor(public payload: { combatId: string, date: string, divisionId: string }) {
    }
}

export class CombatListErrors implements Action {
    readonly type = ActionTypes.CombatListErrors;

    constructor(public payload: any) {
    }
}

export class LoadedCurrentCombatList implements Action {
    readonly type = ActionTypes.LoadedCurrentCombatList;

    constructor(public payload: any) {
    }
}

export class ClearCombatList implements Action {
    readonly type = ActionTypes.ClearCombatList;

    constructor(public payload: any) {
    }
}

export type CombatListActions = CreateCheckin | CombatListErrors |
    CreateCombatList | LoadedCurrentCombatList | ClearCombatList;
