import {Component, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {select, Store} from '@ngrx/store';

import {ICompetitor, IPerson} from '@models/interfaces/human';
import {LoadCompetitorsRating, LoadCompetitorsRatingByStage} from '@competitors/actions/actions';
import {Icons} from '@models/constants/icons';
import {selectTournamentsData} from '@tournaments/selectors/selectors';
import {ITournament} from '@models/interfaces/tournament';
import {IStage} from '@models/interfaces/stage';
import {selectCompetitorsRating} from '@competitors/selectors/selectors';
import {LoadPersons} from '@persons/actions/actions';
import {selectPersonData} from '@persons/selectors/selectors';
import {IExercise} from '@models/interfaces/exercise';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {TournamentsService} from '@tournaments/services/tournaments.service';
import {PageTypes} from '@models/constants/page-types';

@Component({
    selector: 'app-competitors-rating',
    templateUrl: './competitors-rating.component.html',
    styleUrls: ['./competitors-rating.component.scss']
})
export class CompetitorsRatingComponent extends UnsubscribeOndestroyAdapter implements OnInit, OnChanges {

    public competitors: ICompetitor[];
    public tournamentId: string;
    public stages: IStage[];
    public actions = [];
    public icons = Icons;
    public persons: IPerson[];
    public rating: any[];
    public pageTypes = PageTypes;


    constructor(private store: Store<any>,
                private route: ActivatedRoute,
                private tournamentsService: TournamentsService) {
        super();

    }

    ngOnChanges(changes: SimpleChanges): void {
        console.log(changes);
    }

    ngOnInit() {
        this.getRouteParams();
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParams
            .subscribe((params) => {
                this.tournamentId = params['tournament'];
                this.getTournamentsStages(this.tournamentId);
                this.getPersons();
            });
    }

    getTournamentsStages(tournamentId: string) {
        this.subscriptions.sink = this.store.pipe(select(selectTournamentsData))
            .subscribe((tournaments: ITournament[]) => {
                if (tournaments && tournaments.length) {
                    const currentTournament: ITournament = this.findTournamentById(tournaments, tournamentId);
                    this.stages = currentTournament.stages;
                }
            });
    }

    findTournamentById(tournaments: ITournament[], tournamentId: string): ITournament {
        return this.tournamentsService.findTournamentById(tournaments, tournamentId);
    }

    getPersons() {
        this.onItemSelected();
        this.store.dispatch(new LoadPersons());
        this.subscriptions.sink = this.store.select(selectPersonData)
            .subscribe((persons: IPerson[]) => {
                if (persons) {
                    this.persons = persons;
                    this.getCompetitors();
                }
            });

    }

    onItemSelected(value?: any) {
        value ? this.fetchCompetitorsRatingByStage(this.tournamentId, value.id)
            : this.fetchCompetitorsRating(this.tournamentId);
    }

    fetchCompetitorsRating(tournamentId: string) {
        this.store.dispatch(new LoadCompetitorsRating(tournamentId));
    }

    fetchCompetitorsRatingByStage(tournamentId: string, stageId: string) {
        this.store.dispatch(new LoadCompetitorsRatingByStage({tournamentId, stageId}));
    }

    getCompetitors() {
        this.fetchCompetitorsRating(this.tournamentId);
        this.subscriptions.sink = this.store.select(selectCompetitorsRating)
            .subscribe((rating) => {
                this.rating = rating.map((item: IExercise, i: number) => this.mapRatting(item, i));
            });
    }

    mapRatting(rating: IExercise, index: number) {
        const person = this.getPersonById(rating.personId);
        return {
            competitionPlace: index + 1,
            userName: person.userName,
            birthDate: person.birthDate,
            percentage: rating.percentage.toFixed(2),
            hitFactor: rating.hitFactor.toFixed(2),
            stages: rating.stages,
            score: rating.score || 0,
            timeOfExercise: rating.timeOfExercise || 0,
            disqualificationReason: rating.disqualification || 'no'
        };
    }

    getPersonById(personId: string) {
        return this.persons.find((person: IPerson) => person.id === personId);
    }
}
