import {Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs';
import {Store} from '@ngrx/store';
import {tap} from 'rxjs/internal/operators/tap';

import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {ISpecialities} from '@models/interfaces/specialities';
import {selectSpecialitiesData} from '@specialities/selectors/selectors';
import {CreateSpeciality, LoadSpecialities, RemoveSpeciality, UpdateSpeciality} from '@specialities/actions/actions';
import {Actions} from '@models/constants/actions';
import {PageTypes} from '@models/constants/page-types';
import {SpecialitiesMocks} from '@models/mocks/specialities';
import {UnsubscribeOndestroyAdapter} from '../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-specialities',
    templateUrl: './specialities.component.html',
    styleUrls: ['./specialities.component.scss']
})
export class SpecialitiesComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public actions: string[];
    public pages = PageTypes;

    public specialities$: Observable<ISpecialities[]>;

    constructor(private modalService: ModalService,
                private store: Store<any>) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getSpecialities();
    }

    handleActions(event: { action: string, item: ISpecialities }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openSpecialitiesModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
        }
    }

    addSpeciality() {
        this.openSpecialitiesModal(SpecialitiesMocks.emptySpeciality, false);
    }

    getSpecialities() {
        this.specialities$ = this.store.select(selectSpecialitiesData)
            .pipe(tap((specialities: ISpecialities[]) => !specialities && this.fetchSpecialities()));
    }

    fetchSpecialities() {
        this.store.dispatch(new LoadSpecialities());
    }

    openSpecialitiesModal(speciality: ISpecialities, edit: boolean) {
        const objectType = 'Specialities';
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...speciality}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateSpeciality(res, speciality.id) : this.createSpecialities(res);
                }
            });
    }

    openDialogModal(message: string, item: ISpecialities) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.specialityRus, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.removeSpeciality(item.id);
                }
            });
    }

    createSpecialities(speciality: ISpecialities) {
        this.store.dispatch(new CreateSpeciality(speciality));
    }

    updateSpeciality(updatedSpeciality: ISpecialities, specialityId: string) {
        updatedSpeciality.id = specialityId;
        this.store.dispatch(new UpdateSpeciality(updatedSpeciality));
    }

    removeSpeciality(specialityId: string) {
        this.store.dispatch(new RemoveSpeciality(specialityId));
    }
}
