import {IUser} from '@models/interfaces/human';
import {ActionTypes, UserActions} from '@user/actions/actions';

export interface State {
    users: IUser[];
    usersPerPage: IUser[];
    usersCount: number;
    errors?: any;
}

export const initialState: State = {
    users: undefined,
    usersCount: 0,
    usersPerPage: []
};

export function reducer(state: State = initialState, action: UserActions) {
    switch (action.type) {
        case ActionTypes.LoadedUsers:
            return {...state, users: action.payload};
        case ActionTypes.LoadedUsersPerPage:
            return {...state, usersPerPage: action.payload};
        case ActionTypes.LoadedUserCount:
            return {...state, usersCount: action.payload};
        case ActionTypes.UserErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}

