import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap} from 'rxjs/operators';
import {of} from 'rxjs';

import * as fromShared from '../actions/actions';
import {IStage} from '@models/interfaces/stage';
import {StagesService} from '@stages/service/stages.service';
import {SharedService} from '@shared/services/shared.service';
import {CompetitorsService} from '@competitors/services/competitors.service';
import {MessageService} from '@services/message.service';
import {IStatus, IUser} from '@models/interfaces/human';
import {IRank} from '@models/interfaces/ranks';

@Injectable()
export class SharedEffects {


    constructor(private actions$: Actions,
                private stagesService: StagesService,
                private sharedService: SharedService,
                private competitorsService: CompetitorsService,
                private messageService: MessageService) {
    }

    @Effect()
    loadClassifiers$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadClassifiers),
            mergeMap(() => this.stagesService.getClassifier()
                .pipe(
                    map((classifiers: IStage[]) => new fromShared.LoadedClassifiers(classifiers)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        return of(new fromShared.SharedErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadWeaponTypes$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadWeaponTypes),
            mergeMap(() => this.stagesService.getWeaponTypes()
                .pipe(
                    map((weapons: any) => new fromShared.LoadedWeaponTypes(weapons)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                            return of(new fromShared.SharedErrors(err));
                        }
                    )
                )
            )
        );

    @Effect()
    loadLevels$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadLevels),
            mergeMap(() => this.stagesService.getLevels()
                .pipe(
                    map((levels: any) => new fromShared.LoadedLevels(levels)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                            return of(new fromShared.SharedErrors(err));
                        }
                    )
                )
            )
        );

    @Effect()
    loadValidations$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadValidations),
            mergeMap(() => this.sharedService.getValidations()
                .pipe(
                    map((validations: any) => new fromShared.LoadedValidations(validations)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        return of(new fromShared.SharedErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadCompetitorMarks$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadCompetitorMarks),
            mergeMap(() => this.competitorsService.getCompetitorMarksList()
                .pipe(
                    map((marks: string[]) => new fromShared.LoadedCompetitorMarks(marks)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        return of(new fromShared.SharedErrors(err));
                    })
                )
            )
        );

    @Effect()
    loadDisqualificationTypes$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadDisqualificationTypes),
            mergeMap(() => this.sharedService.getDisqualificationTypes()
                .pipe(
                    map((types: string[]) => new fromShared.LoadedDisqualificationTypes(types)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        return of(new fromShared.SharedErrors(err));
                    })
                )
            )
        );

    @Effect()
    getQueries$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.GetQueries),
            map((action: any) => new fromShared.SetQueries(action.payload))
        );

    @Effect()
    uploadImage$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.UploadImage),
            switchMap((action: any) => this.sharedService.uploadImage(action.payload, action.payload.name)
                .pipe(
                    map((response) => new fromShared.UploadedImage(response)),
                    catchError((err) => of(new fromShared.SharedErrors(err)))
                )
            )
        );

    @Effect()
    deleteImage$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.DeleteImage),
            switchMap((action: any) => this.sharedService.deleteImage(action.payload)
                .pipe(
                    map(() => new fromShared.UploadedImage('')),
                    catchError((err) => of(new fromShared.SharedErrors(err)))
                ))
        );

    @Effect()
    loadStatuses$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadStatuses),
            mergeMap(() => this.sharedService.getPersonStatuses()
                .pipe(
                    map((statuses: IStatus[]) => new fromShared.LoadedStatuses(statuses)),
                    catchError((err) => of(new fromShared.SharedErrors(err)))
                )
            )
        );

    @Effect()
    loadTimesInterval$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadTimeIntervals),
            mergeMap(() => this.sharedService.getTimeIntervals()
                .pipe(
                    map((intervals: string[]) => new fromShared.LoadedTimeIntervals(intervals)),
                    catchError((err) => of(new fromShared.SharedErrors(err)))
                )
            )
        );

    @Effect()
    loadUserById$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadUserById),
            mergeMap((action: any) => this.sharedService.getUserById(action.payload)
                .pipe(
                    map((user: IUser) => new fromShared.LoadedUserById(user)),
                    catchError((err) => of(new fromShared.SharedErrors(err)))
                )
            )
        );

    @Effect()
    loadRanks$ = this.actions$
        .pipe(
            ofType(fromShared.ActionTypes.LoadRanks),
            mergeMap(() => this.sharedService.getRanks()
                .pipe(
                    map((ranks: IRank[]) => new fromShared.LoadedRanks(ranks)),
                    catchError((err) => of(new fromShared.SharedErrors(err)))
                )
            )
        );
}
