import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';

import {StagesService} from '@stages/service/stages.service';
import {MessageService} from '@services/message.service';
import * as fromStages from '@stages/actions/actions';
import {IStage} from '@models/interfaces/stage';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {Store} from '@ngrx/store';
import {selectStagesData} from '@stages/selectors/selectors';
import * as fromTournaments from '@tournaments/actions/actions';

@Injectable()
export class StagesEffects {

    constructor(private actions$: Actions,
                private stagesService: StagesService,
                private messageService: MessageService,
                private store: Store<any>) {
    }

    @Effect()
    loadStages$ = this.actions$
        .pipe(
            ofType(fromStages.ActionTypes.LoadStages),
            mergeMap((action: any) => this.stagesService.getStages(action.payload)
                .pipe(
                    map((stages: IStage[]) => (new fromStages.LoadedStages(stages))),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromStages.ErrorStages(err));
                    })
                )
            )
        );

    @Effect()
    refreshStages$ = this.actions$
        .pipe(
            ofType(fromStages.ActionTypes.RefreshStages),
            map((action: any) => new fromStages.LoadedStages(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromStages.ErrorStages(err));
            })
        );

    @Effect()
    createStage$ = this.actions$
        .pipe(
            ofType(fromStages.ActionTypes.CreateStage),
            switchMap((action: any) => {
                const {stage, tournament} = action.payload;
                return this.stagesService.createStage(stage, tournament.id)
                    .pipe(
                        map((createdStage: IStage) => {
                            const message = {user: ToastrUsersType.STAGE, type: ToastrMessageType.CREATE};
                            if (createdStage) {
                                this.messageService.showToastrSuccess(message);
                            }
                            tournament.stages.push(createdStage);
                            return new fromTournaments.ForceUpdateTournament({tournament});
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromStages.ErrorStages(err));
                        })
                    );
            })
        );

    @Effect()
    deleteStage$ = this.actions$
        .pipe(
            ofType(fromStages.ActionTypes.DeleteStage),
            switchMap((action: any) => {
                const {tournament, id} = action.payload;
                return this.stagesService.deleteStage(tournament.id, id)
                    .pipe(
                        map((res) => {
                            const message = {user: ToastrUsersType.STAGE, type: ToastrMessageType.REMOVE};
                            if (res) {
                                this.messageService.showToastrSuccess(message);
                            }
                            tournament.stages = tournament.stages.filter((stage: IStage) => stage.id !== id);
                            return new fromTournaments.ForceUpdateTournament({tournament});
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromStages.ErrorStages(err));
                        })
                    );
            })
        );

    @Effect()
    updateStage$ = this.actions$
        .pipe(
            ofType(fromStages.ActionTypes.UpdateStage),
            withLatestFrom(this.store.select(selectStagesData)),
            switchMap(([action, stages]: any[]) => {
                const {stage, tournament} = action.payload;
                return this.stagesService.updateStage(stage, tournament.id)
                    .pipe(
                        map((updatedStage: IStage) => {
                            const message = {user: ToastrUsersType.STAGE, type: ToastrMessageType.UPDATE};
                            if (updatedStage) {
                                stages = stages.map((item: IStage) => item.id === updatedStage.id ? updatedStage : item);
                                this.messageService.showToastrSuccess(message);
                            }
                            const tournamentStage = tournament.stages.find(st => st.id === updatedStage.id);
                            Object.keys(updatedStage).forEach(key => {
                                tournamentStage[key] = updatedStage[key];
                            });
                            tournament.stages = tournament.stages
                                .map(st => st.id === tournamentStage.id ? tournamentStage : st);
                            return new fromTournaments.ForceUpdateTournament({tournament});
                        }),
                        catchError((err) => {
                            this.messageService.showPushNotification(err);
                            this.messageService.showToastrError(err);
                            return of(new fromStages.ErrorStages(err));
                        })
                    );
            })
        );
}
