import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {Store} from '@ngrx/store';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';

import * as fromCourses from '@course/actions/actions';
import {CoursesService} from '@course/services/courses.service';
import {ICourse} from '@models/interfaces/courses';
import {selectCourseData} from '@course/selectors/selectors';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {MessageService} from '@services/message.service';
import {CoursesActions} from '@course/actions/actions';

@Injectable()
export class CourseEffects {

    constructor(private actions$: Actions,
                private store: Store<any>,
                private coursesService: CoursesService,
                private messageService: MessageService) {
    }

    @Effect()
    loadCourses$ = this.actions$
        .pipe(
            ofType(fromCourses.ActionTypes.LoadCourses),
            mergeMap(() => this.coursesService.getCourses()
                .pipe(
                    map((courses: ICourse[]) => new fromCourses.LoadedCourses(courses)),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCourses.CoursesErrors(err));
                    })
                )
            )
        );

    @Effect()
    refreshCourse$ = this.actions$
        .pipe(
            ofType(fromCourses.ActionTypes.RefreshCourses),
            map((action: CoursesActions) => new fromCourses.LoadedCourses(action.payload)),
            catchError((err) => {
                this.messageService.showPushNotification(err);
                this.messageService.showToastrError(err);
                return of(new fromCourses.CoursesErrors(err));
            })
        );

    @Effect()
    createCourse$ = this.actions$
        .pipe(
            ofType(fromCourses.ActionTypes.CreateCourse),
            withLatestFrom(this.store.select(selectCourseData)),
            switchMap(([action, courses]: any[]) => this.coursesService.createCourse(action.payload)
                .pipe(
                    map((course: ICourse) => {
                        const message = {user: ToastrUsersType.COURSE, type: ToastrMessageType.CREATE};
                        if (course) {
                            this.messageService.showToastrSuccess(message);
                            courses.push(course);
                        }

                        return new fromCourses.RefreshCourses(courses);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCourses.CoursesErrors(err));
                    })
                )
            )
        );

    @Effect()
    updateCourse$ = this.actions$
        .pipe(
            ofType(fromCourses.ActionTypes.UpdateCourse),
            withLatestFrom(this.store.select(selectCourseData)),
            switchMap(([action, courses]: any[]) => this.coursesService.updateCourse(action.payload)
                .pipe(
                    map((course: ICourse) => {
                        const message = {user: ToastrUsersType.COURSE, type: ToastrMessageType.UPDATE};
                        if (course) {
                            this.messageService.showToastrSuccess(message);
                            courses = courses.map((item: ICourse) =>
                                item.id === action.payload.id ? action.payload : item);
                        }

                        return new fromCourses.RefreshCourses(courses);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCourses.CoursesErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteCourse$ = this.actions$
        .pipe(
            ofType(fromCourses.ActionTypes.DeleteCourse),
            withLatestFrom(this.store.select(selectCourseData)),
            switchMap(([action, courses]: any[]) => this.coursesService.deleteCourse(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.COURSE, type: ToastrMessageType.REMOVE};
                        this.messageService.showToastrSuccess(message);
                        courses = courses.filter((item: ICourse) => item.id !== action.payload);

                        return new fromCourses.RefreshCourses(courses);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCourses.CoursesErrors(err));
                    })
                )
            )
        );

}
