import {Action} from '@ngrx/store';
import {IStage} from '@models/interfaces/stage';
import {ITournament} from '@models/interfaces/tournament';

export enum ActionTypes {
    LoadStages = '[Stages Page] Load Stages',
    LoadedStages = '[Stages Page] Loaded Stages',
    ErrorStages = '[Stages Page] Error Stages',
    CreateStage = '[Stages Page] Create Stage',
    DeleteStage = '[Stages Page] Delete Stage',
    UpdateStage = '[Stages Page] Update Stage',
    RefreshStages = '[Stages Page] Refresh Stages'
}

interface IPayload {
    stage?: IStage;
    id?: string;
    tournament: ITournament;
}

export class LoadStages implements Action {
    readonly type = ActionTypes.LoadStages;

    constructor(public payload: string) {
    }
}

export class RefreshStages implements Action {
    readonly type = ActionTypes.RefreshStages;

    constructor(public payload: IStage[]) {
    }
}

export class LoadedStages implements Action {
    readonly type = ActionTypes.LoadedStages;

    constructor(public payload: IStage[]) {
    }
}

export class ErrorStages implements Action {
    readonly type = ActionTypes.ErrorStages;

    constructor(public payload: any) {
    }
}

export class CreateStage implements Action {
    readonly type = ActionTypes.CreateStage;

    constructor(public payload: IPayload) {
    }
}

export class DeleteStage implements Action {
    readonly type = ActionTypes.DeleteStage;

    constructor(public payload: IPayload) {
    }
}

export class UpdateStage implements Action {
    readonly type = ActionTypes.UpdateStage;

    constructor(public payload: IPayload) {
    }
}

export type StagesAction = LoadStages &
    LoadedStages & ErrorStages &
    CreateStage & DeleteStage & UpdateStage &
    RefreshStages;
