import {Injectable} from '@angular/core';


@Injectable({
    providedIn: 'root'
})
export class TokenService {
    constructor() {
    }

    public getToken() {
        return localStorage.getItem('auth_token');
    }

    public setToken(token: string) {
        localStorage.setItem('auth_token', token);
    }

    public removeToken() {
        localStorage.removeItem('auth_token');
    }
}
