import {Action} from '@ngrx/store';
import {ICheckin, ICheckinParams} from '@models/interfaces/checkin';
import {IPerson} from '@models/interfaces/human';

export enum ActionTypes {
    LoadCheckin = '[Checkin Page] Load Checkin',
    LoadedCheckin = '[Checkin Page] Loaded Checkin',
    CheckinErrors = '[Checkin Page] Checkin Errors',
    LoadDivisionCheckinByInterval = '[Checkin Page] Load Division Checkin By Interval',
    LoadedDivisionCheckinByInterval = '[Checkin Page] Loaded Division Checkin By Interval',
    LoadPersonsCheckinByDivision = '[Checkin Page] Load Persons Checkin By Division',
    LoadedPersonsCheckinByDivision = '[Checkin Page] Loaded Persons Checkin By Division',
    ClearCheckin = '[Checkin Page] Clear Checkin'
}

export class LoadCheckin implements Action {
    readonly type = ActionTypes.LoadCheckin;

    constructor(public payload: ICheckinParams) {
    }
}

export class LoadedCheckin implements Action {
    readonly type = ActionTypes.LoadedCheckin;

    constructor(public payload: ICheckin[]) {
    }
}

export class ClearCheckin implements Action {
    readonly type = ActionTypes.ClearCheckin;

    constructor(public payload: any) {
    }
}

export class LoadDivisionCheckinByInterval implements Action {
    readonly type = ActionTypes.LoadDivisionCheckinByInterval;

    constructor(public payload: ICheckinParams) {
    }
}

export class LoadedDivisionCheckinByInterval implements Action {
    readonly type = ActionTypes.LoadedDivisionCheckinByInterval;

    constructor(public payload: any) {
    }
}

export class LoadPersonsCheckinByDivision implements Action {
    readonly type = ActionTypes.LoadPersonsCheckinByDivision;

    constructor(public payload: string | number) {
    }
}

export class LoadedPersonsCheckinByDivision implements Action {
    readonly type = ActionTypes.LoadedPersonsCheckinByDivision;

    constructor(public payload: IPerson[]) {
    }
}

export class CheckinErrors implements Action {
    readonly type = ActionTypes.CheckinErrors;

    constructor(public payload: any) {
    }
}

export type CheckinActions = LoadCheckin | LoadedCheckin | CheckinErrors |
    LoadDivisionCheckinByInterval | LoadedDivisionCheckinByInterval |
    LoadPersonsCheckinByDivision | LoadedPersonsCheckinByDivision | ClearCheckin;
