import {PageTypes} from '@models/constants/page-types';
import {Mocks} from '@models/mocks/persons.mock';
import {FormFieldName} from '@models/constants/fields';
import {Icons} from '@models/constants/icons';
import {UnsubscribeOndestroyAdapter} from './unsubscribe-ondestroy-adapter';
import {Input, QueryList, ViewChildren} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {NgbdSortableHeader} from '@table/table.directive';
import * as moment from 'moment';
import 'moment-duration-format';
import {KeyValue} from '@angular/common';


export class TableAbstract extends UnsubscribeOndestroyAdapter {
    public fields = FormFieldName;
    public icons = Icons;
    public direction = '';

    private language = localStorage.getItem('lang');

    @Input() pageType: string;
    @Input() isMobile: boolean;

    @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

    constructor(protected translateService: TranslateService) {
        super();
    }

    get displayedCells() {
        let notDisplay;
        switch (this.pageType) {
            case PageTypes.COMPETITOR:
                notDisplay = [this.fields.ACTIVE, this.fields.RANK];
                return this.filterCells(notDisplay);
            case PageTypes.TOURNAMENT:
                notDisplay = [this.fields.LEVEL, this.fields.RANK];
                return this.filterCells(notDisplay);
            case PageTypes.COMBAT_LIST:
                notDisplay = [this.fields.MEMBER, this.fields.LEVEL, this.fields.ACTIVE];
                return this.filterCells(notDisplay);
            case PageTypes.PERSON:
                notDisplay = [this.fields.MEMBER, this.fields.LEVEL];
                return this.filterCells(notDisplay);
            case PageTypes.SPECIALITIES:
                notDisplay = [this.fields.NAME, this.language === 'ru' ? this.fields.SPECIALITY_KZ : this.fields.SPECIALITY_RUS];
                return this.filterCells(notDisplay);
            case PageTypes.STANDARDS:
                notDisplay = [
                    this.fields.NAME,
                    this.language === 'ru' ? this.fields.NAMED_KZ : this.fields.NAMED_RUS,
                    this.language === 'ru' ? this.fields.DESCRIPTION_KZ : this.fields.DESCRIPTION_RUS,
                ];
                return this.filterCells(notDisplay);
            case PageTypes.STANDARD_DETAILS:
                notDisplay = [this.fields.LEVEL, this.fields.MEMBER, this.fields.ACTIVE];
                return this.filterCells(notDisplay);
            case PageTypes.CATEGORY:
                notDisplay = [this.fields.NAME, this.language === 'ru' ? this.fields.NAME_CATEGORY_KZ : this.fields.NAME_CATEGORY_RUS];
                return this.filterCells(notDisplay);
            case PageTypes.CONDITION:
                notDisplay = [this.fields.NAME, this.language === 'ru' ? this.fields.CONDITIONS_KZ : this.fields.CONDITIONS_RUS];
                return this.filterCells(notDisplay);
            case PageTypes.FAILS:
                notDisplay = [this.fields.NAME, this.language === 'ru' ? this.fields.CONDITIONS_KZ : this.fields.CONDITIONS_RUS];
                return this.filterCells(notDisplay);
            case PageTypes.WEAPON:
                if (this.isMobile) {
                    notDisplay = [this.fields.OWNER];
                    return this.filterCells(notDisplay);
                }
                return Mocks.displayedCells;
            case PageTypes.RATING:
                const ratingCells = [...Mocks.displayedCells];
                ratingCells.push(this.fields.STAGES);
                return ratingCells;
            case PageTypes.SUBJECTS:
            case PageTypes.QUESTIONS:
            case PageTypes.TESTS:
                notDisplay = [this.fields.NAME, this.language === 'ru' ? this.fields.KZ_NAME : this.fields.RU_NAME];
                return this.filterCells(notDisplay);
            default:
                return Mocks.displayedCells;
        }
    }

    filterCells(notDisplay) {
        return Mocks.displayedCells.filter((item) => !(notDisplay.includes(item)));
    }

    detectLanguage() {
        this.subscriptions.sink = this.translateService.onLangChange
            .subscribe((data) => this.language = data.lang);
    }

    formatTime(seconds: string): string {
        if (seconds.includes(':')) {
            return seconds;
        }
        const duration = moment.duration(Number(seconds), 'seconds');
        return duration.format('HH:mm:ss');
    }

    noSorting = (a: KeyValue<number, string>, b: KeyValue<number, string>): number => 0;

    trackByFn(item, i) {
        return item[i];
    }
}
