import {Component, OnDestroy, OnInit} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Subscription} from 'rxjs';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {SubSink} from 'subsink';

import {selectStatuses} from '@shared/selectors/selectors';
import {IDialogActions, IStatus} from '@models/interfaces/human';
import {SharedService} from '@shared/services/shared.service';
import {CombatListService} from '@combat-list/services/combat-list.service';
import {ClearCheckin, LoadCheckin} from '@checkin/actions/actions';
import {ICheckin, ICheckinParams} from '@models/interfaces/checkin';
import {selectCheckinData} from '@checkin/selectors/selectors';
import {PageTypes} from '@models/constants/page-types';
import {LoadStatuses} from '@shared/actions/actions';
import {DialogsService} from '@shared/services/dialogs.service';

@Component({
    selector: 'app-checkin-details',
    templateUrl: './checkin-details.component.html',
    styleUrls: ['./checkin-details.component.scss']
})
export class CheckinDetailsComponent implements OnInit, OnDestroy {
    public personStatuses: IStatus[] = [];
    public statusNames: string[];
    public translatedStatuses: string[];
    public selectedStatus: string;
    public checkin: ICheckin[];
    public officersCheckin: ICheckin[];
    public pages = PageTypes;
    public isOfficers = false;

    private subscriptions: Subscription[] = [];
    private divisionId: string;
    private filteredDate: string;
    private filteredInterval: string;
    private dialogSubs: SubSink = new SubSink();

    constructor(private store: Store<any>,
                private route: ActivatedRoute,
                private sharedService: SharedService,
                private combatListService: CombatListService,
                private location: Location,
                private dialogsService: DialogsService) {
    }

    ngOnInit() {
        this.getParams();
        this.getPersonStatuses();
    }

    getParams() {
        this.subscriptions.push(
            this.route.queryParams.subscribe((params) => {
                this.divisionId = params['divisionId'];
                this.filteredDate = params['date'];
                this.filteredInterval = params['interval'];
            })
        );
    }

    getPersonStatuses() {
        this.subscriptions.push(
            this.store.pipe(select(selectStatuses))
                .subscribe((statuses: IStatus[]) => {
                    this.personStatuses = statuses;
                    this.statusNames = this.getStatusesNames(statuses);
                    this.translatedStatuses = this.getTranslatedStatuses(this.statusNames);
                    if (!this.personStatuses.length) {
                        this.store.dispatch(new LoadStatuses());
                    }
                })
        );
    }

    getStatusesNames(statuses: IStatus[]): string[] {
        return statuses.map((status: IStatus) => status.state);
    }

    getTranslatedStatuses(statusNames: string[]): string[] {
        return this.sharedService.translateArray(statusNames);
    }

    initDialogAction() {
        const options = this.getDialogOptions();
        this.dialogSubs.sink = this.dialogsService.getDialogAction(options)
            .subscribe((result) => {
                if (result !== '' && this.translatedStatuses.includes(result)) {
                    this.selectedStatus = result;
                    const currentStatus = this.getCurrentStatus(this.selectedStatus);
                    const checkinParams = this.getCheckinParams(currentStatus);
                    this.getDivisionCheckinDetails(checkinParams);
                }
            });
    }

    getDialogOptions(): IDialogActions {
        return this.dialogsService.getDialogOptions('', this.translatedStatuses);
    }

    getDivisionCheckinDetails(checkinParams: ICheckinParams) {
        this.fetchCheckin(checkinParams);
        this.subscriptions.push(
            this.store.pipe(select(selectCheckinData))
                .subscribe((checkin: ICheckin[]) => {
                    if (checkin && checkin.length) {
                        this.separatePersonsByRank(checkin);
                    }
                })
        );
    }

    fetchCheckin(checkinParams: ICheckinParams) {
        this.store.dispatch(new LoadCheckin(checkinParams));
    }

    getCurrentStatus(status: string) {
        const index = this.findStatusIndex(status);
        return this.statusNames[index];
    }

    separatePersonsByRank(checkin: ICheckin[]) {
        this.officersCheckin = checkin.filter((item: ICheckin) => item.rank.officer);
        this.checkin = checkin.filter((item: ICheckin) => !item.rank.officer);
    }

    findStatusIndex(status: string): number {
        return this.translatedStatuses.findIndex((item: string) => item === status);
    }

    getCheckinParams(status: string): ICheckinParams {
        return {
            division: this.divisionId,
            date: this.filteredDate,
            interval: this.filteredInterval,
            status: status
        };
    }

    onStatusChanged(value: string) {
        const status = this.getCurrentStatus(value);
        const checkinParams = this.getCheckinParams(status);
        this.store.dispatch(new LoadCheckin(checkinParams));
    }

    goBack() {
        this.location.back();
    }

    onSwitchTables() {
        this.isOfficers = !this.isOfficers;
    }

    ngOnDestroy() {
        this.subscriptions.forEach((s: Subscription) => s.unsubscribe());
        this.store.dispatch(new ClearCheckin([]));
    }
}
