import {ICheckin, ICombatNote} from '@models/interfaces/checkin';
import {ActionTypes, CheckinActions} from '@checkin/actions/actions';
import {IPerson} from '@models/interfaces/human';

export interface State {
    checkin: ICheckin[];
    checkinByInterval: ICombatNote[];
    personCheckinByDivision: IPerson[];
    errors?: any;
}

export const initialState: State = {
    checkin: undefined,
    checkinByInterval: [],
    personCheckinByDivision: [],
};

export function reducer(state: State = initialState, action: CheckinActions) {
    switch (action.type) {
        case ActionTypes.LoadedCheckin:
            return {...state, checkin: action.payload};
        case ActionTypes.LoadedDivisionCheckinByInterval:
            return {...state, checkinByInterval: action.payload};
        case ActionTypes.LoadedPersonsCheckinByDivision:
            return {...state, personCheckinByDivision: action.payload};
        case ActionTypes.CheckinErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
