import {Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs';
import {Store} from '@ngrx/store';
import {Router} from '@angular/router';
import {tap} from 'rxjs/internal/operators/tap';

import {ICondition, IConditionUnits} from '@models/interfaces/standard';
import {selectConditions, selectUnits} from '@quiz/selectors/selectors';
import {Actions} from '@models/constants/actions';
import {StandardsMocks} from '@models/mocks/specialities';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {PageTypes} from '@models/constants/page-types';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {CreateCondition, DeleteCondition, LoadConditions, LoadUnits, UpdateCondition} from '@quiz/actions/actions';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-conditions',
    templateUrl: './conditions.component.html',
    styleUrls: ['./conditions.component.scss']
})
export class ConditionsComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public actions: string[];
    public objectType = PageTypes;

    public conditions$: Observable<ICondition[]>;
    public units$: Observable<IConditionUnits[]>;

    constructor(private store: Store<any>,
                private modalService: ModalService,
                private router: Router) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getConditions();
        this.getUnits();
    }

    getConditions() {
        this.conditions$ = this.store.select(selectConditions)
            .pipe(tap((conditions: ICondition[]) => !conditions && this.fetchConditions()));
    }

    fetchConditions() {
        this.store.dispatch(new LoadConditions());
    }

    getUnits() {
        this.units$ = this.store.select(selectUnits)
            .pipe(tap((units: IConditionUnits[]) => !units && this.fetchUnits()));
    }

    fetchUnits() {
        this.store.dispatch(new LoadUnits());
    }

    handleActions(event: { action: string, item: ICondition }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openConditionsModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
        }
    }

    add() {
        this.openConditionsModal(StandardsMocks.emptyCondition, false);
    }

    openConditionsModal(condition: any, edit: boolean) {
        const objectType = this.objectType.CONDITION;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...condition}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateCondition(res, condition.id) : this.createCondition(res);
                }
            });
    }

    openDialogModal(message: string, item: ICondition) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.conditionsRus, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteCondition(item.id);
                }
            });
    }

    createCondition(condition: ICondition) {
        this.store.dispatch(new CreateCondition(condition));
    }

    updateCondition(updatedCondition: ICondition, conditionId) {
        updatedCondition.id = conditionId;
        this.store.dispatch(new UpdateCondition(updatedCondition));
    }

    deleteCondition(conditionId: string) {
        this.store.dispatch(new DeleteCondition(conditionId));
    }

    goToUnits() {
        this.router.navigate(['subjects/standards/conditions/units']);
    }
}
