import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';
import {of} from 'rxjs';
import {Location} from '@angular/common';
import {select, Store} from '@ngrx/store';

import * as fromPersons from '@persons/actions/actions';
import * as fromTournaments from '@tournaments/actions/actions';
import * as fromCompetitors from '@competitors/actions/actions';
import {ITournament} from '@models/interfaces/tournament';
import {selectTournamentsData} from '@tournaments/selectors/selectors';
import {competitorMapperToStore} from '@mappers/tournament.mapper';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {TournamentsService} from '@tournaments/services/tournaments.service';
import {MessageService} from '@services/message.service';
import {LocalStorageService} from '@services/local-storage.service';
import {DialogsService} from '@shared/services/dialogs.service';

@Injectable()
export class TournamentsEffects {

    constructor(private actions$: Actions,
                private tournamentsService: TournamentsService,
                private messageService: MessageService,
                private localStorageService: LocalStorageService,
                private store: Store<any>,
                private dialogService: DialogsService,
                private location: Location) {
    }

    @Effect()
    loadTournaments$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.LoadTournaments),
            mergeMap(() => this.tournamentsService.getTournaments()
                .pipe(
                    map((tournaments: ITournament[]) => {
                        return new fromTournaments.LoadedTournaments(tournaments);
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromTournaments.ErrorTournaments(err));
                    })
                ))
        );

    @Effect()
    loadTournamentById$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.LoadTournamentById),
            mergeMap((action: any) => this.tournamentsService.getTournamentById(action.payload)
                .pipe(
                    map((tournament: ITournament) => new fromTournaments.LoadedTournamentById(tournament)),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromTournaments.ErrorTournaments(err));
                    })
                )
            )
        );

    @Effect()
    refreshTournaments$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.RefreshTournaments),
            map((action: any) => new fromTournaments.LoadedTournaments(action.payload)),
            catchError((err) => {
                this.messageService.showToastrError(err);
                this.messageService.showPushNotification(err);
                return of(new fromTournaments.ErrorTournaments(err));
            })
        );

    @Effect()
    createTournament$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.CreateTournament),
            withLatestFrom(this.store.select(selectTournamentsData)),
            switchMap(([action, tournaments]: Array<any>) => this.tournamentsService.createTournament(action.payload)
                .pipe(
                    map((tournament: ITournament) => {
                        const message = {user: ToastrUsersType.TOURNAMENT, type: ToastrMessageType.CREATE};
                        if (tournament) {
                            tournaments.push(tournament);
                            this.messageService.showToastrSuccess(message);
                        }
                        return new fromTournaments.RefreshTournaments(tournaments);
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromTournaments.ErrorTournaments(err));
                    })
                ))
        );

    @Effect()
    deleteTournament$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.DeleteTournament),
            withLatestFrom(this.store.select(selectTournamentsData)),
            switchMap(([action, tournaments]: Array<any>) => this.tournamentsService.removeTournament(action.payload)
                .pipe(
                    map(() => {
                        const message = {user: ToastrUsersType.TOURNAMENT, type: ToastrMessageType.REMOVE};
                        tournaments = tournaments.filter((item: ITournament) => item.id !== action.payload);
                        this.messageService.showToastrSuccess(message);
                        return new fromTournaments.RefreshTournaments(tournaments);
                    }),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromTournaments.ErrorTournaments(err));
                    })
                )
            )
        );

    @Effect()
    updateTournament$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.UpdateTournament),
            withLatestFrom(this.store.select(selectTournamentsData)),
            switchMap(([action, tournaments]: Array<any>) => {
                const {tournament, tournamentId} = action.payload;
                return this.tournamentsService.updateTournament(tournamentId, tournament)
                    .pipe(
                        map((updatedTournament: ITournament) => {
                            const message = {user: ToastrUsersType.TOURNAMENT, type: ToastrMessageType.UPDATE};
                            if (updatedTournament) {
                                tournaments = tournaments.map((item: ITournament) =>
                                    item.id === updatedTournament.id ? updatedTournament : item);
                                this.messageService.showToastrSuccess(message);
                            }
                            return new fromTournaments.RefreshTournaments(tournaments);
                        }),
                        catchError((err) => {
                            this.messageService.showToastrError(err);
                            this.messageService.showPushNotification(err);
                            return of(new fromTournaments.ErrorTournaments(err));
                        })
                    );
            })
        );

    @Effect()
    forceUpdateTournamentCompetitors$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.ForceUpdateTournamentCompetitors),
            withLatestFrom(this.store.select(selectTournamentsData)),
            map(([action, tournaments]: Array<any>) => {
                const {tournament, competitor} = action.payload;
                let cmpToUpdate = tournament.competitors.find(cmp => cmp.id === competitor.id);
                for (const key in competitor) {
                    if (competitor.hasOwnProperty(key)) {
                        cmpToUpdate[key] = competitor[key];
                    }
                }
                cmpToUpdate = competitorMapperToStore(cmpToUpdate);
                tournament.competitors = tournament.competitors
                    .map(cmp => cmp.id === cmpToUpdate.id ? cmpToUpdate : cmp);
                tournaments = tournaments.map(trn => trn.id === tournament.id ? tournament : trn);
                return new fromTournaments.RefreshTournaments(tournaments);
            }),
            catchError((err) => {
                this.messageService.showToastrError(err);
                this.messageService.showPushNotification(err);
                return of(new fromTournaments.ErrorTournaments(err));
            })
        );

    @Effect()
    forceUpdateTournament$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.ForceUpdateTournament),
            withLatestFrom(this.store.select(selectTournamentsData)),
            map(([action, tournaments]: Array<any>) => {
                tournaments = tournaments.map(trn => trn.id === action.payload.id ? action.payload : trn);
                return new fromTournaments.RefreshTournaments(tournaments);
            }),
            catchError((err) => {
                this.messageService.showToastrError(err);
                this.messageService.showPushNotification(err);
                return of(new fromTournaments.ErrorTournaments(err));
            })
        );

    @Effect()
    addScoreList$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.AddScoreList),
            withLatestFrom(this.store.pipe(select(selectTournamentsData))),
            switchMap(([action, tournaments]: any) => {
                    const {tournamentId, stageId, scores, fromStorage} = action.payload;
                    return this.tournamentsService.addScoreList(tournamentId, stageId, scores)
                        .pipe(
                            switchMap((response) => {
                                if (fromStorage) {
                                    this.localStorageService.removeScoreList(tournamentId, stageId);
                                }
                                return [new fromCompetitors.LoadCompetitors(tournamentId)];
                            }),
                            catchError((err) => {
                                this.messageService.showToastrError(err);
                                this.messageService.showPushNotification(err);
                                return of(new fromTournaments.ErrorTournaments(err));
                            })
                        );
                }
            )
        );

    @Effect()
    loadAllData$ = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.LoadAllData),
            mergeMap(() => {
                    return [new fromTournaments.LoadTournaments(), new fromPersons.LoadPersons()];
                }
            ),
            catchError((err) => {
                this.messageService.showToastrError(err);
                this.messageService.showPushNotification(err);
                return of(new fromTournaments.ErrorTournaments(err));
            })
        );

    @Effect()
    startTournament = this.actions$
        .pipe(
            ofType(fromTournaments.ActionTypes.StartTournament),
            mergeMap((action: any) => this.tournamentsService.startTournament(action.payload)
                .pipe(
                    map((response) => new fromTournaments.LoadedStartTournament(response.id)),
                    catchError((err) => {
                        this.messageService.showToastrError(err);
                        this.messageService.showPushNotification(err);
                        return of(new fromTournaments.ErrorTournaments(err));
                    })
                )
            )
        );
}
