import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromCheckin from '@checkin/reducers/reducer';

export const selectCheckin = createFeatureSelector<fromCheckin.State>('checkin');

export const selectCheckinData = createSelector(
    selectCheckin,
    (state: fromCheckin.State) => state.checkin
);

export const selectCheckinByInterval = createSelector(
    selectCheckin,
    (state: fromCheckin.State) => state.checkinByInterval
);

export const selectPersonCheckinByDivision = createSelector(
    selectCheckin,
    (state: fromCheckin.State) => state.personCheckinByDivision
);

export const selectCheckinErrors = createSelector(
    selectCheckin,
    (state: fromCheckin.State) => state.errors
);
