import {Action} from '@ngrx/store';

import {IPerson} from '@models/interfaces/human';

export interface IPayload {
    person: IPerson;
    page: IPage;
}

export interface IPage {
    pageSize: number;
    pageNumber: number;
}

export enum ActionTypes {
    LoadPersons = '[Persons Page] Load Persons',
    LoadedPersons = '[Persons Page] Persons Loaded Success',
    LoadPersonsPerPage = '[Persons Page] Load Persons Per Page',
    LoadedPersonsPerPage = '[Persons Page] Persons Per Page Loaded Success',
    ErrorLoadedPersons = '[Persons Page] Persons Loaded Error',
    CreatePerson = '[Persons Page] Create Person',
    DeletePerson = '[Persons Page] Delete Person',
    UpdatePerson = '[Persons Page] Update Person',
    LoadPersonsCount = '[Persons Page] Loaded Persons Count',
    LoadedPersonsCount = '[Persons Page] Persons Count Loaded Success',
    LoadPersonsByDivision = '[Persons Page] Load Persons By Division',
    LoadedPersonsByDivision = '[Persons Page] Loaded Persons By Division',
    LoadPersonsByDivisionPerPage = '[Persons Page] Load Persons By Division Per Page'
}

export class LoadPersons implements Action {
    readonly type = ActionTypes.LoadPersons;
}

export class LoadedPersons implements Action {
    readonly type = ActionTypes.LoadedPersons;

    constructor(public payload: IPerson[]) {
    }
}

export class LoadPersonsPerPage implements Action {
    readonly type = ActionTypes.LoadPersonsPerPage;

    constructor(public payload: IPage) {
    }
}

export class LoadedPersonsPerPage implements Action {
    readonly type = ActionTypes.LoadedPersonsPerPage;

    constructor(public payload: IPerson[]) {
    }
}

export class ErrorLoadedPersons implements Action {
    readonly type = ActionTypes.ErrorLoadedPersons;

    constructor(public payload: any) {
    }
}

export class CreatePerson implements Action {
    readonly type = ActionTypes.CreatePerson;

    constructor(public payload: IPayload) {
    }
}

export class DeletePerson implements Action {
    readonly type = ActionTypes.DeletePerson;

    constructor(public payload: IPayload) {
    }
}

export class UpdatePerson implements Action {
    readonly type = ActionTypes.UpdatePerson;

    constructor(public payload: IPayload) {
    }
}

export class LoadPersonsCount implements Action {
    readonly type = ActionTypes.LoadPersonsCount;

    constructor() {
    }
}

export class LoadedPersonsCount implements Action {
    readonly type = ActionTypes.LoadedPersonsCount;

    constructor(public payload: number) {
    }
}

export class LoadPersonsByDivision implements Action {
    readonly type = ActionTypes.LoadPersonsByDivision;

    constructor(public payload: string | number) {
    }
}

export class LoadedPersonsByDivision implements Action {
    readonly type = ActionTypes.LoadedPersonsByDivision;

    constructor(public payload: IPerson[]) {
    }
}

export class LoadPersonsByDivisionPerPage implements Action {
    readonly type = ActionTypes.LoadPersonsByDivisionPerPage;

    constructor(public payload: {page: IPage, divisionId: string}) {
    }
}

export type PersonActions =
    LoadPersonsPerPage &
    LoadedPersonsPerPage &
    ErrorLoadedPersons &
    CreatePerson &
    DeletePerson &
    UpdatePerson &
    LoadPersonsCount &
    LoadedPersonsCount &
    LoadPersons &
    LoadedPersons &
    LoadPersonsByDivision &
    LoadedPersonsByDivision &
    LoadPersonsByDivisionPerPage;
