import {Component, EventEmitter, Input, OnInit, Output, ViewChild, ViewEncapsulation} from '@angular/core';
import {IActionMapping, TREE_ACTIONS, TreeComponent, TreeNode} from 'angular-tree-component';

import {IDivision} from '@models/interfaces/division';
import {Icons} from '@models/constants/icons';

@Component({
    selector: 'app-table',
    templateUrl: './table.component.html',
    styleUrls: ['./table.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class TableComponent implements OnInit {

    public options: {};
    public icons = Icons;

    @Input() nodes: IDivision[];
    @Input() isHidden = false;

    @ViewChild('tree', { static: true }) tree: TreeComponent;

    @Output() treeValue: EventEmitter<string> = new EventEmitter();
    @Output() handleRemove: EventEmitter<TreeNode> = new EventEmitter<TreeNode>();
    @Output() handleUpdate: EventEmitter<TreeNode> = new EventEmitter<TreeNode>();
    @Output() handleAdd: EventEmitter<TreeNode> = new EventEmitter<TreeNode>();

    ngOnInit() {
        const actionMapping: IActionMapping = {
            mouse: {
                click: TREE_ACTIONS.TOGGLE_ACTIVE,
                dblClick: (tree, node, $event) => {
                    if (node.hasChildren) {
                        TREE_ACTIONS.TOGGLE_EXPANDED(tree, node, $event);
                    }
                }
            },
            keys: {}
        };
        this.options = {actionMapping};
    }

    addTreeItem(value: TreeNode) {
        this.handleAdd.emit(value);
    }

    removeTreeItem(value: TreeNode) {
        this.handleRemove.emit(value);
    }

    updateTreeItem(value: TreeNode) {
        this.handleUpdate.emit(value);
    }

    chooseNode(value) {
        this.treeValue.emit(value);
    }
}
