import {IWeapon, IWeaponTypes} from '@models/interfaces/weapons';
import {ActionTypes, WeaponsActions} from '../actions/actions';

export interface State {
    weaponTypes: IWeaponTypes[];
    weapons: IWeapon[];
    personsWeaponList: IWeapon[];
    errors?: any;
}

export const initialState: State = {
    weaponTypes: undefined,
    weapons: undefined,
    personsWeaponList: undefined
};

export function reducer(state: State = initialState, action: WeaponsActions) {
    switch (action.type) {
        case ActionTypes.LoadedWeaponTypesForList:
            return {...state, weaponTypes: action.payload};
        case ActionTypes.LoadedWeaponList:
            return {...state, weapons: action.payload};
        case ActionTypes.WeaponErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
