import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

import {IConditionUnits} from '@models/interfaces/standard';
import {Actions} from '@models/constants/actions';
import {StandardsMocks} from '@models/mocks/specialities';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {PageTypes} from '@models/constants/page-types';
import {selectUnits} from '@quiz/selectors/selectors';
import {CreateUnit, DeleteUnit, LoadUnits} from '@quiz/actions/actions';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-units',
    templateUrl: './units.component.html',
    styleUrls: ['./units.component.scss']
})
export class UnitsComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public objectType = PageTypes;
    public actions: string[];

    public units$: Observable<IConditionUnits[]>;

    constructor(private modalService: ModalService,
                private store: Store<any>) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.REMOVE];
        this.getUnits();
    }

    getUnits() {
        this.units$ = this.store.select(selectUnits)
            .pipe(tap((units: IConditionUnits[]) => !units && this.fetchUnits()));
    }

    fetchUnits() {
        this.store.dispatch(new LoadUnits());
    }

    handleActions(event: { action: string, item: IConditionUnits }) {
        if (event.action === Actions.REMOVE) {
            this.openDialogModal('dialogQuestion', event.item);
        }
    }

    add() {
        this.openUnitsModal(StandardsMocks.emptyUnit, false);
    }

    openUnitsModal(units: any, edit: boolean) {
        const objectType = this.objectType.UNIT;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...units}, edit, objectType},
            (res) => {
                if (res) {
                    this.createUnit(res);
                }
            });
    }

    openDialogModal(message: string, item: IConditionUnits) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteUnit(item.id);
                }
            });
    }

    createUnit(unit: IConditionUnits) {
        this.store.dispatch(new CreateUnit(unit));
    }

    deleteUnit(unitId: string) {
        this.store.dispatch(new DeleteUnit(unitId));
    }
}
