import {ICheckin, ICombatNote, IStat} from '@models/interfaces/checkin';

export const findCheckinStatus = (statuses: IStat[], statusName: string): IStat => {
    return statuses.find((item: IStat) => item.status === statusName);
};

export const checkinMapperFromServer = (data: any, total: number): ICombatNote => {
    const unknown = findCheckinStatus(data.statList, 'UNDEFINED');
    const outfit = findCheckinStatus(data.statList, 'OUTFIT');
    const mission = findCheckinStatus(data.statList, 'MISSION');
    const sickLeave = findCheckinStatus(data.statList, 'SICK_LEAVE');
    const infirmary = findCheckinStatus(data.statList, 'INFIRMARY');
    const hospital = findCheckinStatus(data.statList, 'HOSPITAL');
    const dayOff = findCheckinStatus(data.statList, 'DAY_OFF');
    const present = findCheckinStatus(data.statList, 'PRESENT');
    const delay = findCheckinStatus(data.statList, 'DELAY');

    return {
        combat: data.combat,
        date: data.date,
        division: data.division,
        id: data.id,
        total: total,
        PRESENT: present ? present.count : 0,
        UNDEFINED: unknown ? unknown.count : 0,
        MISSION: mission ? mission.count : 0,
        SICK_LEAVE: sickLeave ? sickLeave.count : 0,
        INFIRMARY: infirmary ? infirmary.count : 0,
        HOSPITAL: hospital ? hospital.count : 0,
        OUTFIT: outfit ? outfit.count : 0,
        DAY_OFF: dayOff ? dayOff.count : 0,
        DELAY: delay ? delay.count : 0,
    };
};

export const checkinDetailsMapperFromServer = (data): ICheckin => {
    return {
        userName: data.person.userName,
        birthDate: data.person.birthDate,
        rank: data.person.rank,
        status: Object.keys(data.status)[0]
    };
};
