import {Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs';
import {selectSubjects} from '@quiz/selectors/selectors';
import {Store} from '@ngrx/store';
import {Router} from '@angular/router';
import {tap} from 'rxjs/internal/operators/tap';

import {LoadSubjects, RefreshTests} from '@quiz/actions/actions';
import {Actions} from '@models/constants/actions';
import {ISubject} from '@models/interfaces/quiz';
import {PageTypes} from '@models/constants/page-types';
import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';

@Component({
    selector: 'app-subjects',
    templateUrl: './subjects.component.html',
    styleUrls: ['./subjects.component.scss']
})
export class SubjectsComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public quizSubjects$: Observable<ISubject[]>;
    public actions: string[];
    public objectType = PageTypes;

    constructor(private store: Store<any>,
                private router: Router) {
        super();
    }

    ngOnInit() {
        this.getSubjects();
        this.actions = [Actions.TESTS, Actions.STANDARDS];
        this.store.dispatch(new RefreshTests(null));
    }

    getSubjects() {
        this.quizSubjects$ = this.store.select(selectSubjects)
            .pipe(tap((subjects: ISubject[]) => !subjects && this.fetchSubjects()));
    }

    fetchSubjects() {
        this.store.dispatch(new LoadSubjects());
    }

    handleActions(event: { item: any, action: string }) {
        switch (event.action) {
            case Actions.TESTS:
                return this.goToChildPage(event.item, 'subjects/tests');
            case Actions.STANDARDS:
                return this.goToChildPage(event.item, 'subjects/standards');
        }
    }

    goToChildPage(subject: { id: string, ru: string, kk: string }, childPage: string) {
        const language = localStorage.getItem('lang');
        this.router.navigate([childPage], {
            queryParams: {
                subject: subject.id,
                name: language === 'rus' ? subject.kk : subject.ru
            }
        });
    }
}
