import {IDivision} from '@models/interfaces/division';
import {ActionTypes, DivisionActions} from '@divisions/actions/actions';

export interface State {
    divisions: IDivision[];
    rootDivision: IDivision;
    divisionsErrors?: any;
    rootDivisionsErrors?: any;
    divisionNames: string[];
}

export const initialState: State = {
    divisions: undefined,
    rootDivision: null,
    divisionNames: undefined
};

export function reducer (state: State = initialState, action: DivisionActions) {
    switch (action.type) {
        case ActionTypes.LoadedDivisions:
            return {...state, divisions: action.payload};
        case ActionTypes.LoadedRootDivision:
            return {...state, rootDivision: action.payload};
        case ActionTypes.ErrorsDivisions:
            return {...state, divisionsErrors: action.payload};
        case ActionTypes.LoadedDivisionNames:
            return {...state, divisionNames: action.payload};
        case ActionTypes.ErrorsRootDivisions:
            return {...state, rootDivisionsErrors: action.payload};
        default:
            return state;
    }
}
