import {IAnswers, IQuestion, ITest} from '@models/interfaces/quiz';
import {ICondition, IStandard} from '@models/interfaces/standard';

const getActiveQuestions = (questions: IQuestion[]): IQuestion[] => {
    return questions.filter((question: IQuestion) => question.active);
};

export const quizMapperFromServer = (data: any): ITest => {
    return {
        ru: data.name.rus,
        kk: data.name.kz,
        questionList: data.questionList,
        questionsCount: getActiveQuestions(data.questionList).length,
        time: data.time,
        great: data.great,
        good: data.good,
        satisfactorily: data.satisfactorily,
        id: data.id
    };
};

export const quizMapperToServer = (data: ITest) => {
    return {
        name: {
            rus: data.ru,
            kz: data.kk,
        },
        great: data.great,
        good: data.good,
        satisfactorily: data.satisfactorily,
        questionList: data.questionList,
        subject: data.subject,
        time: data.time
    };
};

export const subjectMapperFromServer = (data) => {
    return {
        id: data.id,
        ru: data.rus,
        kk: data.kz
    };
};

export const questionMapperFromServer = (data: any, answers: IAnswers[]) => {
    return {
        random: data.random,
        id: data.id,
        ru: data['question'].rus,
        kk: data['question'].kz,
        active: data.active,
        right: data.right,
        questionImagePath: data['question'].imagePath,
        answers: answers
    };
};

export const questionMapperToServer = (data: any, answers: any[]) => {
    return {
        question: {
            kz: data['question'].kk,
            rus: data['question'].ru,
            imagePath: data['question'].imagePath
        },
        active: data.active,
        random: data.random,
        right: data.right,
        id: data.id,
        answers: answers,
    };
};

export const answerMapperFromServer = (data: any) => {
    return {
        kk: data.kz,
        ru: data.rus,
        imagePath: data.imagePath
    };
};

export const answerMapperToServer = (data: IAnswers) => {
    return {
        kz: data.kk,
        rus: data.ru,
        imagePath: data.imagePath
    };
};

export const standardMapperFromServer = (data: any): IStandard => {
    return {
        namedRus: data.info.namedRus,
        namedKz: data.info.namedKz,
        descriptionKz: data.info.descriptionKz,
        descriptionRus: data.info.descriptionRus,
        active: data.active,
        groups: data.active,
        conditionsList: data.conditionsList,
        categoriesList: data.categoriesList,
        failsList: data.failsList,
        id: data.id
    };
};

export const standardMapperToServer = (data: IStandard): any => {
    return {
        info: {
            namedKz: data.namedKz,
            namedRus: data.namedRus,
            descriptionKz: data.descriptionKz,
            descriptionRus: data.descriptionRus
        },
        active: data.active,
        groups: data.active,
        conditionsList: data.conditionsList,
        categoriesList: data.categoriesList,
        failsList: data.failsList,
        id: data.id,
        subject: data.subject
    };
};

export const conditionsMapper = (data: ICondition) => {
    return {
        conditionsRus: data.conditionsRus,
        conditionsKz: data.conditionsKz,
        minValue: data.minValue,
        maxValue: data.maxValue,
        coefficient: data.coefficient,
        units: data.units.id,
    };
};
