import {Component, Input, OnInit} from '@angular/core';
import {FormGroup} from '@angular/forms';

import {IUser} from '@models/interfaces/human';
import {FormsService} from '@forms/services/forms.service';

@Component({
    selector: 'app-address-form-part',
    templateUrl: './address-form-part.component.html',
    styleUrls: ['./address-form-part.component.scss']
})
export class AddressFormPartComponent implements OnInit {
    public controls: string[] = [];
    @Input() submitted: boolean;
    @Input() form: FormGroup;
    @Input() addressParent: IUser;

    constructor(private formsService: FormsService) {
    }

    ngOnInit() {
        this.controls = this.createArrayOfControls(this.form);
    }

    createArrayOfControls(group: FormGroup): string[] {
        return this.formsService.createArrayOfControls(group);
    }

    get formControls() {
        return this.form.controls;
    }
}
