import {Component, OnInit} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {Store} from '@ngrx/store';

import {LoginService} from '@login/services/login.service';
import {Animations} from '@models/constants/animations';
import {Router} from '@angular/router';
import {SharedService} from '@shared/services/shared.service';
import {selectQueries} from '@shared/selectors/selectors';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss'],
    animations: [Animations.enterLeaveTop]
})
export class HeaderComponent implements OnInit {
    public isShow = false;
    public menu: any[] = [];
    public breadcrumbList: any[] = [];
    public query: any;

    constructor(private translate: TranslateService,
                public loginService: LoginService,
                private router: Router,
                private sharedService: SharedService,
                private store: Store<any>,
                private modalService: ModalService) {
    }

    ngOnInit() {
        this.menu = this.sharedService.getMenu();
        this.listenRouting();
    }

    listenRouting() {
        let routerUrl: string, routerList: Array<any>, target: any;
        this.router.events.subscribe((router: any) => {
            this.getQueries();
            routerUrl = router.urlAfterRedirects;
            if (routerUrl && typeof routerUrl === 'string') {
                target = this.menu;
                this.breadcrumbList.length = 0;
                routerList = routerUrl.slice(1).split('/');
                routerList.forEach((item, index) => {
                    const n = item.indexOf('?');
                    item = item.substring(0, n !== -1 ? n : item.length);
                    target = target.find(page => page.path.slice(1) === item);
                    const currentPath = (index === 0) ? target.path :
                        `${this.breadcrumbList[index - 1].path}/${target.path.slice(1)}`;
                    this.breadcrumbList.push(
                        {
                            name: target.name,
                            path: currentPath
                        }
                    );

                    if (index + 1 !== routerList.length) {
                        target = target.children;
                    }
                });
            }
        });
    }

    getQueries() {
        this.store.select(selectQueries).subscribe((query) => this.query = query);
    }

    goTo(path: string) {
        this.router.navigate([path], {queryParams: this.query});
    }

    openDialogModal(message: string) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {message},
            (res) => {
                if (res && res === 'Yes') {
                    this.logout();
                }
            });
    }


    useLanguage(language: string) {
        this.translate.use(language);
        localStorage.setItem('lang', language);
    }

    show() {
        this.isShow = !this.isShow;
    }

    logout() {
        this.loginService.logOut();
    }

}
