import {ActionTypes, TournamentsAction} from '@tournaments/actions/actions';
import {ITournament} from '@models/interfaces/tournament';
import {IExercise} from '@models/interfaces/exercise';

export interface State {
    tournaments: ITournament[];
    tournament: ITournament;
    scoreList: IExercise[];
    errors?: any;
}

export const initialState: State = {
    tournaments: undefined,
    scoreList: [],
    tournament: null
};

export function reducer (state: State = initialState, action: TournamentsAction): State {
    switch (action.type) {
        case ActionTypes.LoadedTournaments:
            return {...state, tournaments: action.payload};
        case ActionTypes.LoadedTournamentById:
            return {...state, tournament: action.payload};
        case ActionTypes.ErrorTournaments:
            return {...state, errors: action.payload};
        case ActionTypes.AddScoreList:
            return {...state, scoreList: action.payload};
        default:
            return state;
    }
}
