import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '@environments/environment';
import {Observable} from 'rxjs';
import {ICompetitor} from '@models/interfaces/human';
import {map} from 'rxjs/operators';
import {competitorMapperFromServer, competitorMapperToServer, competitorMapperToServerWithBothFields} from '@mappers/tournament.mapper';
import {IExercise} from '@models/interfaces/exercise';

@Injectable({
    providedIn: 'root'
})
export class CompetitorsService {
    private baseUrl: string = environment.BASE_URL + '/api/competitions/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getCompetitors(tournamentId: string): Observable<ICompetitor[]> {
        return this.http.get(this.baseUrl + `/${tournamentId}/competitor/all`)
            .pipe(map((response: ICompetitor[]) =>
                response.map((item: ICompetitor, index: number) => competitorMapperFromServer(item, index))));
    }

    createListOfCompetitors(competitorsIds: string[], tournamentId: string): Observable<ICompetitor[]> {
        return this.http.post(this.baseUrl + `/${tournamentId}/competitors`, competitorsIds)
            .pipe(map((response: ICompetitor[]) => response));
    }

    getCompetitorMarksList(): Observable<string[]> {
        return this.http.get<string[]>(this.baseUrl + '/enum/mark')
            .pipe(map((response: string[]) => response));
    }

    updateCompetitorByJudge(tournamentId: string, competitorId: string, competitorUpdates: any): Observable<ICompetitor> {
        const mappedData = competitorMapperToServer(competitorUpdates);
        return this.http.put(this.baseUrl + `/${tournamentId}/competitor/${competitorId}/judge`, mappedData)
            .pipe(map((response: ICompetitor) => response));
    }

    updateCompetitorWithBothFields(tournamentId: string, competitorId: string, competitorUpdates: any): Observable<ICompetitor> {
        const mappedData = competitorMapperToServerWithBothFields(competitorUpdates);
        return this.http.put(this.baseUrl + `/${tournamentId}/competitor/${competitorId}/judge/both`, mappedData)
            .pipe(map((response: ICompetitor) => response));
    }

    getCompetitorsRatingByStage(tournamentId: string, stageId: string): Observable<IExercise[]> {
        return this.http.get(this.baseUrl + `/competition/${tournamentId}/stage/${stageId}/score/list`)
            .pipe(map((response: IExercise[]) => response));
    }

    getCompetitorsRating(tournamentId: string): Observable<IExercise[]> {
        return this.http.get(this.baseUrl + `/competition/${tournamentId}/rating`)
            .pipe(map((response: IExercise[]) => response));
    }

}
