import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap} from 'rxjs/operators';
import {of} from 'rxjs';

import * as fromWorkspace from '@workspace/actions/actions';
import {WorkspaceService} from '@workspace/services/workspace.service';
import {IWorkSpace} from '@models/interfaces/workspace';
import {MessageService} from '@services/message.service';

@Injectable()
export class WorkspaceEffects {
    constructor(private actions$: Actions,
                private workspaceService: WorkspaceService,
                private messageService: MessageService) {
    }

    @Effect()
    loadWorkspaces$ = this.actions$.pipe(
        ofType(fromWorkspace.ActionTypes.LoadWorkspaces),
        mergeMap(() => this.workspaceService.getWorkSpaceForTest()
            .pipe(
                map((workspaces: IWorkSpace[]) => new fromWorkspace.LoadedWorkspaces(workspaces)),
                catchError((err) => {
                    this.messageService.showToastrError(err);
                    this.messageService.showPushNotification(err);
                    return of();
                })
            )
        )
    );

    @Effect()
    loadCurrentWorkspace$ = this.actions$.pipe(
        ofType(fromWorkspace.ActionTypes.LoadCurrentWorkspace),
        mergeMap(() => this.workspaceService.getCurrentWorkspace()
            .pipe(map((workspace: IWorkSpace) => {
                    workspace.useInTest = true;
                    return new fromWorkspace.SendCurrentWorkspace(workspace);
                }),
                catchError((err) => {
                    this.messageService.showToastrError(err);
                    this.messageService.showPushNotification(err);
                    return of();
                })
            )
        )
    );

    @Effect()
    sendCurrentWorkspace$ = this.actions$.pipe(
        ofType(fromWorkspace.ActionTypes.SendCurrentWorkspace),
        mergeMap((action: any) => this.workspaceService.sendCurrentWorkspace(action.payload)
            .pipe(
                map((workspace: IWorkSpace) => new fromWorkspace.LoadedCurrentWorkspace(workspace)),
                catchError((err) => {
                    this.messageService.showToastrError(err);
                    this.messageService.showPushNotification(err);
                    return of();
                })
            )
        )
    );
}
