import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fromWeapons from '@weapon/reducers/reducer';

export const selectWeapons = createFeatureSelector<fromWeapons.State>('weapons');

export const selectWeaponTypesData = createSelector(
    selectWeapons,
    (state: fromWeapons.State) => state.weaponTypes
);

export const selectWeaponList = createSelector(
    selectWeapons,
    (state: fromWeapons.State) => state.weapons
);

export const selectWeaponErrors = createSelector(
    selectWeapons,
    (state: fromWeapons.State) => state.errors
);
